.product-details {
  padding-bottom: 16px;

  @media screen and (min-width: 768px) {
    padding-bottom: 32px;
  }

  &__top {
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding: 24px 32px;
    border-radius: 16px 16px 0 0;
    background-color: #fff;

    &-title {
      display: flex;
      align-items: center;
      gap: 12px;

      & h2 {
        color: #333;
        font-size: 18px;
        font-weight: 600;
        line-height: normal;

        @media screen and (min-width: 768px) {
          font-size: 22px;
        }
      }
    }

    &-rating {
      display: flex;
      align-items: center;
      gap: 8px;

      & h2 {
        display: none;

        @media screen and (min-width: 768px) {
          display: block;
          color: #000;
          font-size: 16px;
          font-weight: 600;
          line-height: normal;
        }
      }

      & div svg {
        width: 18px;
        height: 18px;

        @media screen and (min-width: 768px) {
          width: 24px;
          height: 24px;
        }
      }
    }
  }

  &__bottom {
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    align-items: center;
    gap: 20px;
    padding: 16px;
    border-radius: 0 0 16px 16px;
    background-color: #fff;
    border-top: 1px solid #ebebeb;

    @media screen and (min-width: 1100px) {
      flex-direction: row;
    }

    &-content {
      display: flex;
      flex-direction: column;
      align-items: center;
      gap: 57px;

      @media screen and (min-width: 768px) {
        align-items: flex-end;
      }

      &-top {
        display: flex;
        flex-direction: column;
        gap: 24px;

        & p {
          color: #000;
          font-size: 14px;
          font-weight: 400;
          line-height: 20px;

          @media screen and (min-width: 768px) {
            font-size: 16px;
            line-height: 24px;
            max-width: 655px;
          }
        }

        &-list {
          display: flex;
          flex-wrap: wrap;
          gap: 24px;

          @media screen and (min-width: 768px) {
            gap: 40px;
          }

          & li {
            display: flex;
            flex-direction: column;
            gap: 6px;

            & h3 {
              color: #656565;
              font-size: 12px;
              font-weight: 500;
              line-height: 20px;
              text-transform: uppercase;
            }

            & p {
              color: #000;
              font-size: 20px;
              font-weight: 500;
              line-height: 20px;
              text-transform: uppercase;

              @media screen and (min-width: 768px) {
                font-size: 24px;
              }
            }
          }
        }
      }

      &-button {
        color: #2b67f6;
        font-size: 18px;
        font-weight: 400;
        line-height: 20px;
        padding: 12px 40px;
        background-color: transparent;
        border: 1px solid #2b67f6;
        border-radius: 27px;
        width: 100%;

        &:hover {
          background-color: #2b67f6;
          color: #ffffff;
        }

        @media screen and (min-width: 768px) {
          width: auto;
        }
      }
    }
  }
}
