.modal-help {
  position: relative;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  gap: 24px;
  background-color: #ffffff;
  border-radius: 24px;
  padding: 40px 24px;
  max-width: 944px;

  @media screen and (min-width: 850px) {
    padding: 40px;
  }

  &-wrapper {
    display: flex;
    justify-content: center;
    align-items: center;
  }

  &__close {
    position: absolute;
    top: 16px;
    right: 16px;
    width: 28px;
    height: 28px;
    background-color: #fff;
    cursor: pointer;
  }

  &-backdrop {
    display: none;
    position: fixed;
    width: 100%;
    height: 100%;
    z-index: 9999;
    top: 0;
    left: 0;
    justify-content: center;
    align-items: center;
    background-color: rgba(0, 0, 0, 0.3);

    &.open {
      display: flex;
    }
  }

  &__header {
    display: flex;
    flex-direction: column;
    gap: 4px;

    &-title {
      color: #333;
      text-align: center;
      font-size: 20px;
      font-weight: 600;
      line-height: normal;

      @media screen and (min-width: 850px) {
        font-size: 32px;
      }
    }

    &-text {
      color: #333;
      text-align: center;
      font-size: 14px;
      font-weight: 400;
      line-height: 18px;

      @media screen and (min-width: 850px) {
        font-size: 18px;
        line-height: 28px;
        max-width: 630px;
      }

      & span {
        font-weight: 600;
      }
    }
  }

  &__links {
    display: flex;
    flex-direction: column;
    gap: 8px;

    @media screen and (min-width: 850px) {
      flex-direction: row;
      gap: 16px;
    }

    &-link {
      display: flex;
      gap: 20px;
      padding: 10px 12px;
      background-color: transparent;
      border: 1px solid #ebebeb;
      border-radius: 8px;
      max-width: 235px;
      width: 100%;

      @media screen and (min-width: 850px) {
        flex-direction: column;
        align-items: center;
        padding: 24px 16px;
        gap: 16px;
      }

      &:hover {
        background-color: #2b67f6;

        & svg {
          color: #ffffff;
        }

        & p {
          color: #ffffff;
        }
      }

      & svg {
        color: #2b67f6;
        width: 20px;
        height: 20px;

        @media screen and (min-width: 850px) {
          width: 28px;
          height: 28px;
        }
      }

      & p {
        color: #333;
        text-align: center;
        font-size: 14px;
        font-weight: 400;
        line-height: 20px;

        @media screen and (min-width: 850px) {
          font-size: 16px;
        }
      }
    }
  }

  &__form {
    display: flex;
    flex-direction: column;
    gap: 16px;

    @media screen and (min-width: 850px) {
      max-width: 570px;
      width: 100%;
    }

    & input {
      padding: 8px 12px;
      border-radius: 8px;
      background-color: #f8f8f8;
      height: 48px;
    }

    & textarea {
      padding: 8px 12px;
      border-radius: 8px;
      background-color: #f8f8f8;
      resize: none;
      max-height: 140px;
      height: 100%;
    }
  }

  &__checkbox {
    display: flex;
    justify-content: center;
    align-items: center;
    gap: 12px;

    &-label {
      color: #000;
      font-size: 14px;
      font-weight: 400;

      @media screen and (min-width: 850px) {
        font-size: 16px;
      }

      & span {
        color: #2b67f6;
      }
    }
  }

  &__button {
    color: #fff;
    font-size: 16px;
    font-weight: 400;
    line-height: 20px;
    border-radius: 27px;
    background: #2b67f6;
    padding: 12px 40px;
    width: 100%;

    @media screen and (min-width: 850px) {
      max-width: 140px;
      font-size: 20px;
    }
  }

  &__button-wrapper {
    display: flex;
    flex-direction: column;
    gap: 16px;

    @media screen and (min-width: 850px) {
      flex-direction: row;
      align-items: flex-start;
      justify-content: space-between;
      max-width: 570px;
      width: 100%;
    }
  }
}
