.container {
  max-width: 1230px;
  padding: 0px 15px;
  margin: 0 auto;
  width: 100%;
}

.header {
  position: absolute;
  top: 0;
  width: 100%;
  left: 0;
  z-index: 99;

  &__top {
    display: none;

    @media screen and (min-width: 768px) {
      display: block;
      background: #00004c;
      padding: 8px 0px;
    }

    &-inner {
      display: flex;
      align-items: center;
      gap: 24px;
    }

    select {
      color: #fff;
      font-size: 11px;
      font-style: normal;
      font-weight: 400;
      line-height: 12px;
      letter-spacing: 0.417px;
      text-transform: uppercase;
      background-position: right;
      background-repeat: no-repeat;

      option {
        color: black;
      }
    }

    ul {
      margin-left: auto;
      display: flex;
      align-items: center;
      gap: 24px;

      a {
        display: flex;
        align-items: center;
        gap: 8px;
        color: #fff;
        font-size: 11px;
        font-style: normal;
        font-weight: 400;
        line-height: 12px;
        letter-spacing: 0.417px;
        text-transform: uppercase;

        &:hover {
          text-decoration: underline;
        }
      }
    }
  }

  &__main {
    background: linear-gradient(
      0deg,
      rgba(0, 0, 0, 0) 0%,
      rgba(0, 0, 0, 0.4) 100%
    );
  }

  &__inner {
    display: none;

    @media screen and (min-width: 768px) {
      display: flex;
      align-items: center;
      padding: 10px 0px;
      justify-content: space-between;
      position: relative;
    }

    img {
      max-width: 190px;
    }

    ul {
      display: none;

      @media screen and (min-width: 768px) {
        display: flex;
        align-items: center;
        gap: 16px;
      }

      li {
        &.parent {
          &:hover {
            .submenu {
              opacity: 1 !important;
              visibility: visible !important;
            }

            a {
              background: rgba(255, 255, 255, 0.1);
            }
          }
        }

        .submenu {
          position: absolute;
          width: 100%;
          border-radius: 0px 0px 24px 24px;
          border-top: 4px solid #2b67f6;
          background: #fff;
          left: 0;
          top: 100%;
          padding: 32px 0;
          display: grid;
          grid-template-columns: repeat(auto-fit, minmax(200px, 1fr));
          opacity: 0;
          visibility: hidden;
          transition: 0.2s linear;

          &::before {
            content: "";
            position: absolute;
            display: block;
            bottom: 100%;
            height: 30px;
            left: 0;
          }

          &__col {
            padding-left: 40px;
            padding-right: 40px;
            border-right: 1px solid #00000023;

            &-title {
              color: #2b67f6;
              font-size: 16px;
              font-style: normal;
              font-weight: 700;
              line-height: 24px;
              letter-spacing: 0.417px;
              text-transform: uppercase;
              margin-bottom: 8px;
            }

            ul {
              flex-direction: column;
              gap: 0;
              align-items: flex-start;

              li {
                padding: 0;

                a {
                  padding: 0;
                  color: #000;
                  font-size: 16px;
                  font-style: normal;
                  font-weight: 400;
                  line-height: 32px;
                  letter-spacing: 0.417px;
                  text-align: left;

                  &:hover {
                    text-decoration: underline;
                  }
                }
              }
            }
          }
        }
      }

      a {
        color: #fff;
        font-size: 16px;
        font-style: normal;
        font-weight: 400;
        line-height: 24px;
        letter-spacing: 0.48px;
        padding: 8px;
        border-radius: 3px;

        &:hover {
          background: rgba(255, 255, 255, 0.1);
        }
      }
    }
  }

  &__mobile {
    display: flex;
    justify-content: space-between;

    &-wrapper {
      display: block;
      background: linear-gradient(
        180deg,
        rgba(0, 0, 0, 0.5) 0%,
        rgba(0, 0, 0, 0) 100%
      );
      padding: 53px 16px 42px;

      @media screen and (min-width: 768px) {
        display: none;
      }
    }

    &-wrapper-result {
      display: block;
      background-color: #ffffff;
      padding: 53px 16px 42px;

      @media screen and (min-width: 768px) {
        display: none;
      }
    }
  }
}

.header.static {
  position: static;

  .header__main {
    a {
      color: black;
    }

    background: #fff;
  }
}

.main-screen {
  width: 100%;

  &__slider {
    display: none;

    @media screen and (min-width: 768px) {
      display: block;
      overflow: hidden;
      position: relative;
    }

    .swiper-pagination {
      padding-bottom: 130px;

      .swiper-pagination-bullet {
        opacity: 1;
        width: 12px;
        height: 12px;
        background: #fff;

        &.swiper-pagination-bullet-active {
          background-image: url("data:image/svg+xml,%3Csvg width='12' height='12' viewBox='0 0 12 12' fill='none' xmlns='http://www.w3.org/2000/svg'%3E%3Ccircle cx='6' cy='6' r='6' fill='white'/%3E%3Ccircle cx='4' cy='4' r='4' transform='matrix(-1 0 0 1 10 2)' fill='%2300004C'/%3E%3C/svg%3E%0A");
          background-size: cover;
          background-position: center;
          background-repeat: no-repeat;
        }
      }
    }

    &-wrapper {
      height: 100%;
    }

    &-slide {
      background-size: cover;
      background-position: center;
      background-repeat: no-repeat;
      height: 100%;
      min-height: 740px;
      padding: 220px 0px;

      .subtitle {
        color: #fff;
        font-size: 14px;
        font-style: normal;
        font-weight: 400;
        line-height: normal;
        margin-bottom: 8px;
      }

      .title {
        color: #fff;
        font-size: 44px;
        font-style: normal;
        font-weight: 600;
        line-height: normal;
        margin-bottom: 24px;
        text-align: left;
        max-width: 100%;

        @media screen and (min-width: 460px) {
          font-size: 64px;
        }
      }

      .location {
        display: flex;
        align-items: center;
        gap: 8px;
        color: #fff;
        font-size: 24px;
        font-style: normal;
        font-weight: 600;
        line-height: normal;

        &::before {
          content: "";
          width: 20px;
          min-width: 20px;
          height: 20px;
          background-size: cover;
          background-position: center;
          background-repeat: no-repeat;
          display: block;
          background-image: url("data:image/svg+xml,%3Csvg width='20' height='20' viewBox='0 0 20 20' fill='none' xmlns='http://www.w3.org/2000/svg'%3E%3Cpath d='M17.5 8.33325C17.5 14.1666 10 19.1666 10 19.1666C10 19.1666 2.5 14.1666 2.5 8.33325C2.5 6.34413 3.29018 4.43647 4.6967 3.02995C6.10322 1.62343 8.01088 0.833252 10 0.833252C11.9891 0.833252 13.8968 1.62343 15.3033 3.02995C16.7098 4.43647 17.5 6.34413 17.5 8.33325Z' stroke='white' stroke-width='1.5' stroke-linecap='round' stroke-linejoin='round'/%3E%3Cpath d='M10 10.8333C11.3807 10.8333 12.5 9.71396 12.5 8.33325C12.5 6.95254 11.3807 5.83325 10 5.83325C8.61929 5.83325 7.5 6.95254 7.5 8.33325C7.5 9.71396 8.61929 10.8333 10 10.8333Z' stroke='white' stroke-width='1.5' stroke-linecap='round' stroke-linejoin='round'/%3E%3C/svg%3E%0A");
        }

        margin-bottom: 16px;
      }

      ul {
        color: #fff;
        font-size: 16px;
        font-style: normal;
        font-weight: 500;
        line-height: 32px;
        list-style: disc;

        li {
          list-style: disc;
        }

        padding-left: 16px;
      }

      .button {
        margin-top: 32px;
      }
    }
  }

  &-mobile {
    background-repeat: no-repeat;
    background-size: cover;
    padding: 297px 24px 58px;
    display: flex;
    flex-direction: column;
    gap: 24px;

    @media screen and (min-width: 768px) {
      display: none;
    }

    &__title {
      color: #fff;
      font-family: Archivo;
      font-size: 32px;
      font-style: normal;
      font-weight: 600;
      line-height: normal;
      max-width: 250px;
    }

    &__button {
      display: flex;
      justify-content: space-between;
      align-items: center;
      height: 50px;
      padding: 8px 12px 8px 24px;
      border-radius: 189px;
      background: #fff;
      max-width: 327px;
      width: 100%;
      text-align: left;
      color: #8e8e8e;
      font-family: Archivo;
      font-size: 18px;
      font-weight: 400;

      & img {
        width: 24px;
        height: 24px;
      }
    }
  }
}

.button--primary {
  color: #fff;
  font-size: 18px;
  font-style: normal;
  font-weight: 400;
  line-height: 20px;
  padding: 12px 40px;
  display: flex;
  align-items: center;
  justify-content: center;
  border-radius: 27px;
  border: 1px solid #2b67f6;
  background: #2b67f6;
  width: max-content;

  &:hover {
    background-color: #1753e2;
  }
}

.results {
  padding: 32px 0px;
  background: #f2f2f2;

  &__inner {
  }

  &__filter {
    display: none;

    @media screen and (min-width: 880px) {
      display: flex;
      align-items: center;
      justify-content: space-between;
      margin-bottom: 32px;
    }
  }

  &__cards {
    display: none;
    @media screen and (min-width: 880px) {
      display: block;
    }

    &.mobile {
      display: block;

      @media screen and (min-width: 880px) {
        display: none;
      }
    }

    &-item {
      display: grid;
      grid-template-columns: 1fr;
      gap: 16px;
      margin-bottom: 32px;

      @media screen and (min-width: 1200px) {
        grid-template-columns: 290px 1fr;
      }

      &-mobile {
        display: flex;
        flex-direction: column;
        gap: 8px;
        margin-bottom: 20px;

        &.crucero {
          border-radius: 16px;
          padding: 16px;
          background-color: #00004c;
        }

        &-crucero {
          display: flex;
          justify-content: space-between;
          align-items: center;
          gap: 4px;

          &-date {
            color: #fff;
            font-size: 12px;
            font-weight: 400;
            line-height: normal;
          }

          &-title {
            color: #fff;
            font-size: 18px;
            font-weight: 600;
            line-height: normal;
          }

          &-text {
            display: none;
            color: #fff;
            font-size: 14px;
            font-weight: 400;
            line-height: 18px;

            &.open {
              display: block;
            }
          }

          &-button {
            &.open {
              transform: rotate(180deg);
            }
          }
        }

        img {
          width: 100%;
          height: auto;
          max-height: 180px;
          height: 100%;
          object-fit: cover;
          border-radius: 16px;
        }
      }

      &.crucero {
        border-radius: 16px;
        padding: 16px;
        background-color: #00004c;
        width: calc(100% + 30px);
        margin-left: -15px;
      }

      &-crucero {
        display: flex;
        flex-direction: column;
        gap: 4px;

        &-date {
          color: #fff;
          font-size: 14px;
          font-weight: 400;
          line-height: normal;
        }

        &-title {
          color: #fff;
          font-size: 24px;
          font-weight: 600;
          line-height: normal;
        }

        &-text {
          color: #fff;
          font-size: 14px;
          font-weight: 400;
          line-height: 20px;
        }
      }

      &.tour {
        & .results__cards-footer {
          margin-top: -14px;
        }
      }

      img {
        width: 100%;
        height: auto;
        max-height: 240px;
        height: 100%;
        object-fit: cover;
        border-radius: 16px;
      }
    }

    &-image {
      position: relative;

      & .heart {
        position: absolute;
        right: 16px;
        top: 16px;
        width: 36px;
        height: 36px;
        background-image: url("../img/heart.png");
        background-repeat: no-repeat;
        background-position: center;
        cursor: pointer;
        content: "";

        &.open {
          background-image: url("../img/heart-empty.png");
        }
      }

      &-two {
        position: absolute;
        top: 0;
        left: 0;
        opacity: 0;
        transition: opacity 0.5s ease;

        &:hover {
          opacity: 1;
          transition: opacity 0.5s ease;
        }
      }
    }

    &-top-mobile {
      display: flex;
      justify-content: space-between;
      align-items: center;
      margin-bottom: 6px;

      & p {
        color: #2b67f6;
        font-size: 16px;
        font-weight: 500;
        line-height: 20px;
      }

      &-logo {
        padding: 5px;
        border-radius: 2.531px;
        border: 0.633px solid #e7e7e7;
      }
    }

    &-footer {
      display: grid;
      grid-template-columns: 1fr 185px;
      gap: 64px;

      &-info-wrapper {
        display: flex;
        flex-direction: column;
        justify-content: flex-end;
      }

      &-button {
        display: flex;
        flex-direction: column;
        justify-content: center;
        align-items: center;
      }

      &-mobile {
        display: flex;
        justify-content: space-between;
        align-items: center;
        gap: 12px;
        margin-bottom: 18px;

        &-button {
          padding: 8px 22px;
          color: #2b67f6;
          font-size: 14px;
          font-weight: 400;
          line-height: 20px;
          background-color: transparent;
          border: 1px solid #2b67f6;
          border-radius: 27px;
          width: 100%;
          text-align: center;

          &:hover {
            background-color: #2b67f6;
            color: #ffffff;
          }

          &.blue {
            background-color: #2b67f6;
            color: #ffffff;

            &:hover {
              background-color: #1753e2;
            }

            &.info-btn-open.open {
              background-color: transparent;
              border-color: #2b67f6;
              color: #2b67f6;
            }
          }
        }
      }
    }

    &-help {
      display: flex;
      justify-content: space-between;

      & .info-mobile.tooltip {
        &.tooltip {
          position: relative;

          &:hover .tooltip-text {
            visibility: visible;
            opacity: 1;
          }

          & .tooltip-text {
            position: absolute;
            z-index: 1;
            bottom: 125%;
            right: 13%;
            background-color: #e8f0ff;
            color: #000;
            text-align: center;
            font-size: 14px;
            font-weight: 400;
            line-height: 18px;
            visibility: hidden;
            width: 250px;
            border-radius: 8px;
            padding: 16px;
            margin-left: -60px;
            opacity: 0;
            transition: opacity 0.3s;
            text-transform: capitalize;

            & h3 {
              color: #000;
              font-size: 18px;
              font-weight: 600;
              line-height: 20px;
              margin-bottom: 16px;
            }

            & a {
              display: flex;
              gap: 8px;
              color: #000;
              font-size: 16px;
              font-weight: 400;
              line-height: 20px;
              text-transform: capitalize;

              &:not(:last-child) {
                margin-bottom: 12px;
              }
            }

            &::after {
              content: "";
              position: absolute;
              top: 100%;
              right: 10%;
              margin-left: -5px;
              border-width: 8px;
              border-style: solid;
              border-color: #e8f0ff transparent transparent transparent;
              content: " ";
            }
          }
        }
      }

      & img {
        width: 16px;
        height: 16px;
      }
    }

    &-badge {
      display: flex;
      align-items: center;
      gap: 6px;
      padding: 11px 8px;
      color: var(--orange-400, #ff8a4c);
      font-size: 12px;
      font-style: normal;
      font-weight: 500;
      text-transform: uppercase;
      border-radius: 10px;
      background: var(--orange-50, #fff8f1);
      width: max-content;
      margin-bottom: 8px;

      & button {
        color: #ff8a4c;
        font-size: 14px;
        font-style: normal;
        font-weight: 700;
        line-height: 15.398px;
        text-transform: uppercase;
        border-radius: 6px;
        background-color: #fbe9d9;
        padding: 4px;
      }
    }

    &-block-mobile {
      display: flex;
      align-items: flex-end;
      justify-content: space-between;
      margin-bottom: 12px;
    }

    .button {
      width: 100%;
      margin-bottom: 12px;
    }

    .info-btn-open.open {
      background-color: transparent;
      border-color: #2b67f6;
      color: #2b67f6;
    }

    .view {
      color: #2b67f6;
      font-size: 16px;
      font-style: normal;
      font-weight: 400;
      line-height: normal;
    }

    .info {
      display: flex !important;
      align-items: center !important;
      gap: 24px !important;
      color: #2b67f6;
      font-size: 14px;
      font-weight: 500;
      line-height: normal;
      gap: 4px;
      width: max-content;

      &-mobile {
        display: flex;
        align-items: center;
        gap: 4px;
        color: #2b67f6;
        font-size: 14px;
        font-weight: 500;
        line-height: normal;
      }

      & div {
        &.help {
          position: relative;
          display: flex;
          align-items: center;
          gap: 8px;

          &:hover .tooltip-text {
            visibility: visible;
            opacity: 1;
          }

          & .tooltip-text {
            position: absolute;
            z-index: 1;
            bottom: 175%;
            left: 54%;
            background-color: #e8f0ff;
            color: #000;
            text-align: center;
            font-size: 13px;
            font-weight: 400;
            line-height: 18px;
            visibility: hidden;
            width: 220px;
            border-radius: 8px;
            padding: 8px 12px;
            margin-left: -60px;
            opacity: 0;
            transition: opacity 0.3s;
            text-transform: capitalize;

            & h3 {
              color: #000;
              font-size: 18px;
              font-weight: 600;
              line-height: 20px;
              margin-bottom: 16px;
            }

            & p {
              display: flex;
              gap: 8px;
              color: #000;
              font-size: 16px;
              font-weight: 400;
              line-height: 20px;
              text-transform: capitalize;

              &:not(:last-child) {
                margin-bottom: 12px;
              }
            }

            &::after {
              content: "";
              position: absolute;
              top: 100%;
              left: 49%;
              margin-left: -5px;
              border-width: 8px;
              border-style: solid;
              border-color: #e8f0ff transparent transparent transparent;
              content: " ";
            }
          }
        }
      }

      & .tooltip {
        position: relative;
        display: flex;
        align-items: center;
        gap: 8px;

        &:hover .tooltip-text {
          visibility: visible;
          opacity: 1;
        }

        & .tooltip-text {
          position: absolute;
          z-index: 1;
          bottom: 125%;
          left: -41%;
          background-color: #e8f0ff;
          color: #000;
          text-align: center;
          font-size: 14px;
          font-weight: 400;
          line-height: 18px;
          visibility: hidden;
          width: 250px;
          border-radius: 8px;
          padding: 16px;
          margin-left: -60px;
          opacity: 0;
          transition: opacity 0.3s;
          text-transform: capitalize;

          & h3 {
            color: #000;
            font-size: 18px;
            font-weight: 600;
            line-height: 20px;
            margin-bottom: 16px;
          }

          & a {
            display: flex;
            gap: 8px;
            color: #000;
            font-size: 16px;
            font-weight: 400;
            line-height: 20px;
            text-transform: capitalize;

            &:not(:last-child) {
              margin-bottom: 12px;
            }
          }

          &::after {
            content: "";
            position: absolute;
            top: 100%;
            left: 49%;
            margin-left: -5px;
            border-width: 8px;
            border-style: solid;
            border-color: #e8f0ff transparent transparent transparent;
            content: " ";
          }
        }
      }
    }

    &-header {
      display: flex;
      justify-content: space-between;
      gap: 32px;

      &-mobile {
        display: flex;
        justify-content: space-between;
        gap: 32px;
        margin-bottom: 12px;

        & > div {
          display: flex;
          flex-direction: column;
          gap: 3px;
        }
      }
    }

    &-list {
      .attributes {
        display: grid;
        grid-template-columns: 1fr 2fr;

        &__item {
          display: flex;
          align-items: center;
          color: var(--Black-color, #333);
          font-size: 18px;
          font-style: normal;
          font-weight: 400;
          line-height: 20px;
          gap: 6px;
          margin-bottom: 18px;

          & .date {
            display: flex;
            color: #2b67f6;

            & svg.open {
              transform: rotate(180deg);
            }
          }

          &-tab {
            position: relative;
            display: flex;
            align-items: center;
            gap: 4px;
            border-radius: 4px;
            background: #00004c;
            padding: 2px 4px;
            width: min-content;
            padding-right: 24px;
            width: 108px;

            &:hover {
              background-color: #2b67f6;
              background-image: url("../img/chevron-right-white.svg");
              background-repeat: no-repeat;
              background-position: bottom right;
            }

            & img {
              width: 16px;
              height: 16px;
            }

            & p {
              color: #fff;
              font-size: 10px;
              font-weight: 600;
              line-height: 10px;
              text-transform: uppercase;
            }
          }
        }

        &__col {
          &-wrapper {
            display: flex;
            flex-direction: column;
            gap: 10px;

            & .attributes__item {
              margin-bottom: 2px;
            }
          }

          .col-text {
            padding-left: 32px;
            color: #656565;
            font-size: 15px;
            font-style: normal;
            font-weight: 400;
            line-height: 20px;

            & span {
              font-weight: 600;

              &.modal {
                color: #2b67f6;
                font-size: 14px;
                font-weight: 500;
                text-decoration-line: underline;
              }
            }
          }
        }
      }
    }

    &-select {
      display: flex;
      align-items: center;
      gap: 6px;
      padding: 4px 6px;
      border-radius: 4px;
      background: rgba(43, 103, 246, 0.06);
      margin-bottom: 12px;

      & img {
        width: 16px;
        height: 16px;
      }

      & select {
        appearance: none;
        width: 100%;
        background-image: url("../img/chevron-right.svg");
        background-position: right center;
        background-repeat: no-repeat;
      }
    }

    &-price {
      display: flex;
      align-items: center;
      gap: 6px;

      & img {
        margin-bottom: 16px;

        &.open {
          transform: rotate(180deg);
        }
      }
    }

    &-content {
      padding: 24px;
      border-radius: 16px;
      background: #fff;

      &-mobile {
        padding: 8px;
        border-radius: 16px;
        background: #fff;

        .title {
          color: #333;
          font-size: 20px;
          font-style: normal;
          font-weight: 600;
          line-height: normal;
          text-align: left !important;
          margin: 0;
          padding: 0;
        }

        .subtitle {
          color: #2b67f6;
          font-size: 16px;
          font-style: normal;
          font-weight: 400;
          line-height: 20px;
        }

        .uppertext {
          color: #000;
          font-size: 14px;
          font-weight: 400;
          line-height: 20px;
          margin-bottom: 10px;

          span {
            font-weight: 700;
          }
        }

        .text {
          color: #333;
          font-family: Archivo;
          font-size: 16px;
          font-style: normal;
          font-weight: 400;
          line-height: 16px;

          & span {
            font-weight: 600;
          }
        }

        .price {
          color: #333;
          text-align: right;
          font-size: 30px;
          font-style: normal;
          font-weight: 700;
          line-height: 30px;
          margin: 2px 0px;

          &-subtext {
            color: #333;
            text-align: right;
            font-size: 12px;
            font-style: normal;
            font-weight: 500;
            line-height: 10px;
            text-transform: uppercase;
            margin: 4px 0px;
          }

          &-text {
            color: #333;
            text-align: right;
            font-size: 14px;
            font-style: normal;
            font-weight: 400;
            line-height: 10px;
            text-transform: uppercase;
          }

          &-uptitle {
            color: #333;
            text-align: right;
            font-size: 12px;
            font-style: normal;
            font-weight: 500;
            line-height: 10px;
            text-transform: uppercase;
          }

          &--old {
            color: #9baab6;
            text-align: right;
            font-family: Archivo;
            font-size: 18px;
            font-style: normal;
            font-weight: 500;
            line-height: normal;
            text-decoration: line-through;
          }
        }
      }

      .title {
        color: #333;
        font-size: 28px;
        font-style: normal;
        font-weight: 600;
        line-height: normal;
        text-align: left !important;
        margin: 0;
        margin-bottom: 8px;
      }

      .subtitle {
        color: #2b67f6;
        font-size: 18px;
        font-style: normal;
        font-weight: 400;
        line-height: 20px;
      }

      .text {
        color: #000;
        font-size: 16px;
        font-weight: 400;
        line-height: 20px;
        margin-bottom: 16px;

        span {
          font-weight: 700;
        }
      }

      .price {
        color: #333;
        text-align: right;
        font-size: 40px;
        font-style: normal;
        font-weight: 700;
        line-height: 37px;
        margin: 2px 0px;

        &-subtext {
          color: #333;
          text-align: right;
          font-size: 10px;
          font-style: normal;
          font-weight: 500;
          line-height: 10px;
          text-transform: uppercase;
          margin: 4px 0px;
        }

        &-text {
          color: #333;
          text-align: right;
          font-size: 12px;
          font-style: normal;
          font-weight: 400;
          line-height: 10px;
          text-transform: uppercase;
        }

        &-uptitle {
          color: #333;
          text-align: right;
          font-size: 10px;
          font-style: normal;
          font-weight: 500;
          line-height: 10px;
          text-transform: uppercase;
        }

        &--old {
          color: #9baab6;
          text-align: right;
          font-family: Archivo;
          font-size: 24px;
          font-style: normal;
          font-weight: 500;
          line-height: normal;
          text-decoration: line-through;
        }
      }
    }

    &-logo {
      display: flex;
      align-items: center;
      justify-content: center;
      padding: 10px;
      border-radius: 12px;
      background: #fff;
      margin-top: 12px;
      max-height: 67px;
      height: 100%;

      img {
        max-height: 40px;
        width: auto;
        object-fit: contain;
      }
    }
  }

  &__selects {
    display: flex;
    align-items: center;
    gap: 16px;

    &-item {
      span {
        color: #333;
        font-size: 16px;
        font-style: normal;
        font-weight: 600;
        line-height: normal;
      }

      select {
        color: #2b67f6;
        font-size: 16px;
        font-style: normal;
        font-weight: 600;
        line-height: normal;
      }
    }
  }

  &__title {
    color: #333;
    font-size: 24px;
    font-style: normal;
    font-weight: 600;
    line-height: normal;

    span {
      color: #2b67f6;
    }
  }
}
