.cruise-result {
  display: none;

  @media screen and (min-width: 768px) {
    display: block;
    padding: 40px 0;
    border-top: 1px solid rgba(0, 0, 0, 0.1);
    background: #fff;
    margin-bottom: 8px;
  }

  &-wrapper {
    display: flex;
    flex-direction: column;
    gap: 27px;
  }

  &__top {
    display: flex;
    justify-content: space-between;
    align-items: center;

    &-content {
      display: flex;
      gap: 18px;
      align-items: center;

      &-title {
        color: #333;
        font-family: Archivo;
        font-size: 24px;
        font-style: normal;
        font-weight: 600;
        line-height: normal;
      }

      &-label {
        color: #000;
        text-align: center;
        font-family: Archivo;
        font-size: 16px;
        font-style: normal;
        font-weight: 400;
        line-height: normal;
        display: inline-block;
        position: relative;
        padding-left: 30px;
        margin-right: 16px;
        cursor: pointer;

        & input[type="radio"] {
          display: none;
        }

        &:hover .radio-checkmark {
          background-color: #ccc;
        }

        & input[type="radio"]:checked + .radio-checkmark {
          border: 4px solid #2b67f6;
        }
      }
    }

    &-links {
      display: flex;
      gap: 24px;
    }

    &-link {
      display: flex;
      gap: 4px;
      align-items: center;
      color: #2b67f6;
      font-family: Archivo;
      font-size: 12px;
      font-style: normal;
      font-weight: 400;
      line-height: 20px;
      text-transform: uppercase;

      &.tooltip {
        position: relative;

        &:hover .tooltip-text {
          visibility: visible;
          opacity: 1;
        }

        & .tooltip-text {
          position: absolute;
          z-index: 1;
          top: 125%;
          left: 13%;
          background-color: #e8f0ff;
          color: #000;
          text-align: center;
          font-size: 14px;
          font-weight: 400;
          line-height: 18px;
          visibility: hidden;
          width: 250px;
          border-radius: 8px;
          padding: 16px;
          margin-left: -60px;
          opacity: 0;
          transition: opacity 0.3s;
          text-transform: capitalize;

          & h3 {
            color: #000;
            font-size: 18px;
            font-weight: 600;
            line-height: 20px;
            margin-bottom: 16px;
          }

          & p {
            display: flex;
            gap: 8px;
            color: #000;
            font-size: 16px;
            font-weight: 400;
            line-height: 20px;
            text-transform: capitalize;

            &:not(:last-child) {
              margin-bottom: 12px;
            }
          }

          &::after {
            content: "";
            position: absolute;
            bottom: 100%;
            left: 49%;
            margin-left: -5px;
            border-width: 8px;
            border-style: solid;
            border-color: transparent transparent #e8f0ff transparent;
            content: " ";
          }
        }
      }
    }
  }

  &__bottom {
    display: grid;
    grid-template-columns: repeat(3, 1fr);
    row-gap: 16px;
    column-gap: 32px;

    &-item {
      display: flex;
      gap: 8px;
      align-items: center;
      height: 48px;
      border-radius: 8px;
      background: #f8f8f8;
      padding: 8px 12px;
      width: 100%;
    }

    &-select {
      height: 48px;
      border-radius: 8px;
      background: #f8f8f8;
      padding: 8px 12px;
      width: 100%;
      -webkit-appearance: none;
      -moz-appearance: none;
      appearance: none;
      background-image: url("../img/chevron-down.svg");
      background-position: right center;
      background-repeat: no-repeat;
      padding-right: 12px;

      &::-ms-expand {
        display: none;
      }
    }

    &-filters {
      display: flex;
      justify-content: center;
      align-items: center;
      gap: 8px;
      position: relative;
      color: #000;
      text-align: center;
      font-family: Archivo;
      font-size: 14px;
      font-style: normal;
      font-weight: 500;
      line-height: normal;
      text-transform: uppercase;
      border-radius: 100px;
      border: 1px solid #ebebeb;
      padding: 9px 24px;

      &:hover {
        background-color: #f8f8f8;
      }

      & span {
        border-radius: 100px;
        display: inline-flex;
        background: #2b67f6;
        color: #fff;
        font-family: Archivo;
        font-size: 14px;
        width: 22px;
        height: 22px;
        justify-content: center;
        align-items: center;
      }
    }

    &-button {
      color: #fff;
      font-family: Archivo;
      font-size: 20px;
      font-style: normal;
      font-weight: 400;
      line-height: 20px;
      border-radius: 27px;
      background: #2b67f6;
      padding: 12px 40px;
      text-align: center;

      &:hover {
        background-color: #1753e2;
      }
    }
  }

  &-full-wrapper {
    display: none;
    padding-top: 32px;
    margin-top: 32px;
    border-top: 1px solid rgba(0, 0, 0, 0.1);
    background: #fff;
  }

  &-full__top {
    display: grid;
    grid-template-columns: repeat(3, 1fr);
    row-gap: 16px;
    column-gap: 32px;
    padding-bottom: 28px;

    &-range-title {
      color: #000;
      font-family: Archivo;
      font-size: 16px;
      font-weight: 400;
      margin-bottom: 56px;
    }

    &-range-cost {
      display: flex;
      justify-content: space-between;
      padding-top: 14px;
    }

    &-range-cost p {
      color: #000;
      font-family: Archivo;
      font-size: 16px;
      font-weight: 400;
    }

    &-select {
      height: 48px;
      border-radius: 8px;
      background: #f8f8f8;
      padding: 8px 12px;
      width: 100%;
      -webkit-appearance: none;
      -moz-appearance: none;
      appearance: none;
      background-image: url("../img/chevron-down.svg");
      background-position: right center;
      background-repeat: no-repeat;
      padding-right: 12px;
    }

    &-range-wrapper {
      grid-row: span 2;
      padding-right: 30px;
    }
  }

  &-full__search-wrapper {
    position: relative;
    height: 48px;
    border-radius: 8px;
    background: #f8f8f8;
    padding: 8px 12px;
    max-width: 378px;
    width: 100%;
    margin-bottom: 16px;
  }

  &-full__search {
    position: relative;
    border-radius: 8px;
    background: #f8f8f8;
    padding: 8px 20px 8px 12px;
    width: 100%;
    background-image: url("../img/search.svg");
    background-position: right center;
    background-repeat: no-repeat;

    &::placeholder {
      color: #898989;
      font-family: Archivo;
      font-size: 16px;
      font-weight: 400;
    }
  }

  &-full__bottom {
    display: flex;
    justify-content: space-between;
    align-items: flex-end;

    &-wrapper {
      display: flex;
      flex-direction: column;
      gap: 28px;

      &-select {
        display: flex;
        gap: 40px;
      }

      &-checkbox {
        display: flex;
        flex-direction: column;
        gap: 19px;
      }
    }

    &-select-item {
      display: flex;
      flex-direction: column;
      gap: 10px;

      & .select {
        display: flex;
        gap: 4px;
        align-items: center;
        color: #2b67f6;

        & img.open {
          transform: rotate(180deg);
        }
      }

      & .checkbox-wrapper {
        display: none;

        &.open {
          display: flex;
          flex-direction: column;
          gap: 12px;
          padding: 16px;
          border-radius: 8px;
          border: 1px solid var(--gray-200, #e5e7eb);
          box-shadow: 0px 1px 2px 0px rgba(0, 0, 0, 0.08);
        }

        & .checkbox {
          display: flex;
          justify-content: space-between;
          align-items: center;

          & label {
            color: #000;
            font-size: 16px;
            font-weight: 400;
            line-height: normal;
          }
        }
      }

      & span {
        border-radius: 100px;
        display: none;
        background: #2b67f6;
        color: #fff;
        font-family: Archivo;
        font-size: 14px;
        width: 22px;
        height: 22px;
        justify-content: center;
        align-items: center;
      }
    }

    &-select {
      color: #2b67f6;
    }

    &-label {
      margin-left: 12px;
      color: #000;
      font-family: Archivo;
      font-size: 16px;
      font-weight: 400;

      & span {
        color: #2b67f6;
        font-family: Archivo;
        font-size: 18px;
        font-weight: 600;
      }
    }
  }
}

.radio-checkmark {
  position: absolute;
  top: 0;
  left: 0;
  height: 16px;
  width: 16px;
  border: 1px solid #ebebeb;
  background: #f8f8f8;
  border-radius: 50%;
}

.is-open {
  display: block;
}

.noUi-handle-lower::after,
.noUi-handle-upper::after {
  content: attr(data-value) !important;
  position: absolute !important;
  top: -30px !important;
  left: 50% !important;
  transform: translateX(-50%);
  color: #000;
  background: #e3ebff !important;
  padding: 4px;
  border-radius: 8px;
  font-family: Archivo;
  font-size: 14px;
  font-weight: 400;
  line-height: 18px;
  z-index: 1;
  width: max-content !important;
  height: max-content !important;
}

.noUi-horizontal {
  height: 6px !important;
}

.noUi-connect {
  background: #2b67f6 !important;
}

.noUi-connects {
  border-radius: 4px !important;
}

.noUi-horizontal .noUi-handle {
  width: 22px !important;
  height: 22px !important;
  border-radius: 8px;
  right: -17px;
  top: -9px !important;
}

.noUi-target {
  background: #f3f4f6 !important;
  border-radius: 4px;
  border: none !important;
}
