.menus {
  display: block;
  padding: 16px 12px;
  border-top: 1px solid #ebebeb;
  background: #fff;

  @media screen and (min-width: 768px) {
    display: none;
  }

  &-wrapper {
    display: flex;
    justify-content: center;
    align-items: center;
    gap: 10px;
  }

  &__button {
    display: flex;
    justify-content: center;
    align-items: center;
    gap: 8px;
    border-radius: 100px;
    border: 1px solid #ebebeb;
    background: #fff;
    height: 42px;
    padding: 10px 16px;
    color: #000;
    font-family: Archivo;
    font-size: 14px;
    font-weight: 500;
    text-transform: uppercase;
    min-width: 153px;
    width: 100%;

    &:hover {
      background-color: #f8f8f8;
    }

    & span {
      border-radius: 100px;
      display: inline-flex;
      background: #2b67f6;
      color: #fff;
      font-family: Archivo;
      font-size: 14px;
      width: 22px;
      height: 22px;
      justify-content: center;
      align-items: center;
    }
  }
}
