.newsletter {
  padding: 36px 0 72px;

  &-wrapper {
    display: flex;
    align-items: center;
    justify-content: space-between;
    padding: 40px;
    border-radius: 24px;
    background-color: #2b67f6;

    @media screen and (max-width: 1150px) {
      flex-direction: column;
      gap: 24px;
    }
  }

  &__content {
    display: flex;
    align-items: center;
    gap: 40px;

    @media screen and (max-width: 1150px) {
      flex-direction: column;
      align-items: flex-start;
      gap: 24px;
    }

    &-img {
      padding: 25px;
      border-radius: 24px;
      background-color: rgba(0, 0, 0, 0.08);

      @media screen and (max-width: 1150px) {
        padding: 17px;
      }

      & svg {
        width: 100px;
        height: 100px;

        @media screen and (max-width: 1150px) {
          width: 66px;
          height: 66px;
        }
      }
    }

    &-wrapper {
      display: flex;
      flex-direction: column;
      gap: 16px;
      max-width: 586px;

      @media screen and (max-width: 1150px) {
        gap: 4px;
      }

      &-title {
        color: #fff;
        font-size: 32px;
        font-weight: 600;
        line-height: 38px;

        @media screen and (max-width: 1150px) {
          font-size: 24px;
          line-height: 32px;
        }
      }

      &-text {
        color: #fff;
        font-size: 18px;
        font-weight: 400;
        line-height: 24px;

        @media screen and (max-width: 1150px) {
          font-size: 16px;
          line-height: 22px;
        }
      }
    }
  }

  &__button {
    color: #fff;
    font-size: 20px;
    font-weight: 400;
    line-height: 20px;
    padding: 16px 56px;
    border-radius: 27px;
    border: 1px solid #fff;
    background-color: transparent;
    height: 52px;

    @media screen and (max-width: 1150px) {
      font-size: 18px;
    }

    &:hover {
      background-color: #fff;
      color: #2b67f6;
    }
  }
}
