.product-adventages {
  background-image: url("../img/bg-product.png");
  background-repeat: no-repeat;
  background-size: contain;
  background-position: bottom;
  padding-bottom: 300px;

  @media screen and (min-width: 800px) {
    background-image: url("../img/bg-product.png");
    background-repeat: no-repeat;
    background-size: cover;
    background-position: center;
  }

  &-wrapper {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    gap: 32px;

    @media screen and (min-width: 1100px) {
      flex-direction: row;
    }
  }

  &__left {
    max-width: 580px;
    width: 100%;

    &-top {
      display: flex;
      align-items: center;
      gap: 8px;
      padding: 16px;
      border-radius: 16px 16px 0px 0px;
      background: #fff;

      @media screen and (min-width: 1100px) {
        gap: 12px;
        padding: 24px 32px;
      }

      & h2 {
        color: #333;
        font-size: 18px;
        font-weight: 600;
        line-height: normal;

        @media screen and (min-width: 1100px) {
          font-size: 22px;
        }
      }
    }

    &-bottom {
      display: flex;
      justify-content: center;
      align-items: center;
      padding: 32px 16px;
      border-radius: 0px 0px 16px 16px;
      background: #fff;
      border-top: 1px solid #ebebeb;

      @media screen and (min-width: 1100px) {
        padding: 32px;
      }

      &-list {
        display: grid;
        grid-template-columns: repeat(2, 1fr);
        row-gap: 24px;

        @media screen and (min-width: 500px) {
          grid-template-columns: repeat(3, 1fr);
        }

        & li {
          display: flex;
          flex-direction: column;
          align-items: center;
          gap: 10px;

          & img {
            width: 56px;
            height: 56px;

            @media screen and (min-width: 1100px) {
              width: 64px;
              height: 64px;
            }
          }

          & p {
            color: #000;
            text-align: center;
            font-size: 14px;
            font-weight: 400;
            line-height: 19px;

            @media screen and (min-width: 1100px) {
              font-size: 15px;
            }
          }
        }
      }
    }
  }

  &__right {
    max-width: 580px;
    width: 100%;

    &-top {
      display: flex;
      align-items: center;
      gap: 12px;
      padding: 16px;
      border-radius: 16px 16px 0px 0px;
      background: #fff;

      @media screen and (min-width: 1100px) {
        padding: 24px 32px;
      }

      & svg {
        width: 24px;
        height: 24px;

        @media screen and (min-width: 1100px) {
          width: 26px;
          height: 26px;
        }
      }

      & h2 {
        color: #333;
        font-size: 22px;
        font-weight: 600;
        line-height: normal;
      }
    }

    &-bottom {
      display: flex;
      flex-direction: column;
      justify-content: center;
      align-items: center;
      gap: 16px;
      padding: 16px;
      border-radius: 0px 0px 16px 16px;
      background: #fff;
      border-top: 1px solid #ebebeb;

      @media screen and (min-width: 1100px) {
        gap: 43px;
        padding: 32px;
      }

      &-item {
        display: flex;
        flex-direction: column;
        gap: 6px;

        & h3 {
          color: #000;
          font-size: 14px;
          font-weight: 500;
          line-height: 20px;

          @media screen and (min-width: 1100px) {
            font-size: 16px;
          }
        }

        & p {
          color: #656565;
          font-size: 12px;
          font-weight: 400;
          line-height: 16px;

          @media screen and (min-width: 1100px) {
            font-size: 13px;
            line-height: 18px;
          }
        }
      }
    }
  }
}
