.info-cabin {
  padding-bottom: 16px;
  display: none;

  @media screen and (min-width: 880px) {
    &.open {
      display: block;
    }
  }

  &.mobile {
    &.open {
      display: block;
    }

    & .info-cabin__top {
      gap: 8px;

      & h2 {
        font-size: 18px;
      }
    }

    & .info-cabin__bottom {
      padding: 16px;

      &-item {
        flex-direction: column;
        gap: 12px;
        padding: 16px 0;

        &:first-child {
          padding-top: 0;
        }

        &:last-child {
          padding-bottom: 0;
        }

        &-left {
          width: 100%;

          & img {
            width: 100%;
          }
        }

        &-right {
          flex-direction: column;
          align-items: normal;
          width: 100%;
          gap: 12px;

          & .price {
            &-uptitle {
              font-size: 10px;
            }

            &-main {
              font-size: 30px;
            }

            &-text {
              font-size: 10px;
            }
          }

          & a {
            text-align: center;
            font-size: 16px;
          }
        }
      }
    }

    @media screen and (min-width: 880px) {
      display: none;
    }
  }

  &__top {
    display: flex;
    align-items: center;
    gap: 12px;
    padding: 24px 32px;
    border-radius: 16px 16px 0px 0px;
    background: #fff;

    & h2 {
      color: #333;
      font-size: 22px;
      font-weight: 600;
      line-height: normal;
    }
  }

  &__bottom {
    padding: 32px;
    border-radius: 0px 0px 16px 16px;
    background-color: #fff;
    border-top: 1px solid #ebebeb;

    &-item {
      display: flex;
      justify-content: space-between;
      align-items: center;
      padding: 24px;
      border-top: 1px solid #ebebeb;

      &:first-child {
        padding-top: 0;
        border: none;
      }

      &:last-child {
        padding-bottom: 0;
      }

      &-left {
        position: relative;

        & h3 {
          position: absolute;
          top: 16px;
          left: 16px;
          color: #fff;
          font-size: 28px;
          font-weight: 700;
          line-height: 37px;
        }
      }

      &-right {
        display: flex;
        align-items: center;
        gap: 40px;

        & .price {
          &-uptitle {
            color: #333;
            text-align: right;
            font-size: 14px;
            font-weight: 400;
            line-height: 10px;
            text-transform: uppercase;
          }

          &-main {
            color: #333;
            text-align: right;
            font-size: 40px;
            font-weight: 700;
            line-height: 37px;
          }

          &-text {
            color: #333;
            text-align: right;
            font-size: 14px;
            font-weight: 400;
            line-height: 10px;
            text-transform: uppercase;
          }
        }

        & a {
          color: #fff;
          font-size: 18px;
          font-weight: 400;
          line-height: 20px;
          padding: 12px 40px;
          border-radius: 27px;
          background-color: #2b67f6;
          cursor: pointer;

          &:hover {
            background-color: #1753e2;
          }
        }
      }
    }
  }

  &.checkout-page {
    display: none;

    &.open {
      display: block;
      width: 100%;
    }
  }
}
