.expedition {
  padding: 40px 0 36px;

  &-wrapper {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
  }

  &__title {
    color: #333;
    font-size: 32px;
    font-weight: 600;
    line-height: normal;
    margin-bottom: 12px;

    @media screen and (max-width: 1150px) {
      font-size: 24px;
      margin-bottom: 8px;
    }
  }

  &__text {
    color: #565656;
    font-size: 18px;
    font-weight: 400;
    line-height: normal;
    margin-bottom: 40px;
  }

  &__content {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    gap: 32px;
    padding: 115px 0;
    width: 100%;
    background-image: url("../img/expedition-content.png");
    background-size: cover;
    background-repeat: no-repeat;
    border-radius: 24px;

    @media screen and (max-width: 1150px) {
      padding: 150px 0;
    }

    &-title {
      color: #fff;
      text-align: center;
      font-size: 40px;
      font-weight: 600;
      line-height: 48px;
      max-width: 430px;

      @media screen and (max-width: 1150px) {
        font-size: 28px;
        line-height: normal;
      }
    }

    &-button {
      color: #fff;
      font-size: 18px;
      font-weight: 400;
      line-height: 20px;
      padding: 12px 40px;
      border-radius: 27px;
      border: 1px solid #fff;

      &:hover {
        background-color: #2b67f6;
        border: 1px solid #2b67f6;
      }
    }
  }
}
