.reserv-datos-cruise {
  padding-bottom: 32px;

  &__top {
    padding: 24px;
    border-radius: 16px 16px 0px 0px;
    background-color: #fff;

    & h2 {
      display: flex;
      align-items: center;
      gap: 12px;
      color: #333;
      font-size: 22px;
      font-weight: 600;
      line-height: normal;
    }
  }

  &__bottom {
    display: grid;
    grid-template-columns: 1fr 1fr;
    justify-items: end;
    padding: 32px;
    border-radius: 0px 0px 16px 16px;
    background-color: #fff;
    border-top: 1px solid #ebebeb;

    &-left {
      display: flex;
      flex-direction: column;
      gap: 40px;

      & h3 {
        color: #000;
        font-size: 28px;
        font-weight: 600;
        line-height: 28px;
      }

      &-content {
        display: flex;
        gap: 60px;
        height: 100%;

        &-item {
          display: flex;
          flex-direction: column;
          justify-content: space-between;

          & span {
            color: #656565;
            font-size: 14px;
            font-weight: 500;
            line-height: 20px;
            text-transform: uppercase;
            margin-bottom: 4px;
          }

          & h4 {
            color: #2b67f6;
            font-size: 24px;
            font-weight: 500;
            line-height: 32px;
          }

          & p {
            color: #000;
            font-size: 20px;
            font-weight: 400;
            line-height: 32px;
          }
        }
      }
    }

    &-right {
      display: flex;
      flex-direction: column;
      gap: 24px;
      max-width: 430px;
      width: 100%;
    }
  }

  &__slider {
    position: relative;
    overflow: hidden;

    .swiper-pagination-bullet {
      width: 10px;
      height: 10px;
      background-color: #fff;
      opacity: 1;
    }

    .swiper-pagination-bullet-active {
      background-color: #000;
      border: 2px solid #fff;
    }

    .reserv-datos-cruise__slide {
      display: flex;
      justify-content: center;
    }
  }
}
