.info-camarote {
  width: 100%;
  display: none;

  &.open {
    display: block;
  }

  &__top {
    padding: 24px;
    border-radius: 16px 16px 0 0;
    background-color: #fff;

    @media screen and (max-width: 880px) {
      padding: 16px;
    }

    & h2 {
      display: flex;
      align-items: center;
      gap: 12px;
      color: #333;
      font-size: 22px;
      font-weight: 600;
      line-height: normal;

      @media screen and (max-width: 880px) {
        font-size: 18px;
      }
    }
  }

  &__img {
    position: relative;

    & img {
      width: 100%;
    }

    & > div {
      position: absolute;
      top: 32px;
      left: 24px;
      display: flex;
      align-items: center;
      gap: 8px;
      border-radius: 8px;
      background-color: rgba(0, 0, 0, 0.24);
      padding: 8px 16px;
      color: #fff;
      font-size: 14px;
      font-weight: 600;
      line-height: 20px;

      @media screen and (max-width: 880px) {
        font-size: 12px;
      }
    }
  }

  &__bottom {
    display: flex;
    flex-direction: column;
    gap: 32px;
    padding: 0px 24px 24px;
    border-radius: 0 0 16px 16px;
    background-color: #fff;

    @media screen and (max-width: 880px) {
      padding: 0 16px 16px;
      gap: 20px;
    }

    &-list {
      position: relative;
      display: flex;
      justify-content: space-between;
      padding: 16px 32px;
      border-radius: 12px;
      background-color: #2b67f6;
      z-index: 1;
      margin-top: -60px;

      @media screen and (max-width: 880px) {
        display: grid;
        grid-template-columns: repeat(3, 1fr);
        column-gap: 32px;
        row-gap: 12px;
        padding: 12px 16px;
        margin-bottom: 4px;
      }

      &-item {
        & span {
          color: #fff;
          font-size: 12px;
          font-weight: 500;
          line-height: 20px;
          text-transform: uppercase;
          opacity: 0.72;

          @media screen and (max-width: 880px) {
            font-size: 10px;
          }
        }

        & p {
          color: #fff;
          font-size: 18px;
          font-weight: 500;
          line-height: 20px;

          @media screen and (max-width: 880px) {
            font-size: 14px;
          }
        }
      }
    }

    &-content {
      display: flex;
      flex-direction: column;
      align-items: flex-start;
      gap: 16px;

      &-label input[type="radio"] {
        display: none;
      }

      &-label {
        color: #000;
        text-align: center;
        font-family: Archivo;
        font-size: 16px;
        font-style: normal;
        font-weight: 400;
        line-height: normal;
        display: inline-block;
        position: relative;
        padding-left: 30px;
        margin-right: 16px;
        cursor: pointer;

        @media screen and (max-width: 880px) {
          font-size: 14px;
        }

        &:hover .radio-checkmark {
          background-color: #ccc;
        }

        & input[type="radio"]:checked + .radio-checkmark {
          border: 4px solid #2b67f6;
        }
      }

      & h2 {
        color: #333;
        font-size: 18px;
        font-weight: 600;
        line-height: normal;

        @media screen and (max-width: 880px) {
          font-size: 16px;
        }
      }

      & p {
        color: #656565;
        font-size: 16px;
        font-weight: 400;
        line-height: 22px;
        margin-left: 30px;

        @media screen and (max-width: 880px) {
          font-size: 12px;
        }
      }
    }
  }

  &-buttons {
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding-top: 24px;
    margin-bottom: 30px;

    & .prev {
      color: #2b67f6;
      font-size: 18px;
      font-weight: 400;
      line-height: 20px;
      padding: 16px 40px;
      border-radius: 27px;
      border: 1.5px solid #2b67f6;

      @media screen and (max-width: 880px) {
        font-size: 16px;
        padding: 10px 20px;
      }

      &:hover {
        background-color: #2b67f6;
        color: #fff;
      }
    }

    & .next {
      color: #fff;
      font-size: 20px;
      font-weight: 400;
      line-height: 20px;
      padding: 16px 56px;
      border-radius: 27px;
      background-color: #2b67f6;
      border: 1.5px solid #2b67f6;

      @media screen and (max-width: 880px) {
        font-size: 16px;
        padding: 10px 40px;
      }

      &:hover {
        color: #2b67f6;
        background-color: transparent;
      }
    }
  }
}
