.reserv-pay {
  &__top {
    padding: 24px;
    border-radius: 16px 16px 0px 0px;
    background-color: #fff;

    &-title {
      display: flex;
      align-items: center;
      gap: 12px;
    }
  }

  &__bottom {
    border-top: 1px solid #ebebeb;
    padding: 32px;
    border-radius: 0px 0px 16px 16px;
    background-color: #fff;

    &-item {
      display: grid;
      grid-template-columns: 1fr 1fr 1fr 1fr;
    }
  }
}
