.reserv-datos-info {
  margin-top: -370px;
  position: relative;
  padding-bottom: 32px;

  &__top {
    display: flex;
    justify-content: space-between;
    padding: 12px 24px;
    border-radius: 16px 16px 0px 0px;
    background: #fff;

    &-title {
      display: flex;
      align-items: center;
      gap: 12px;
      color: #333;
      font-size: 22px;
      font-weight: 600;
      line-height: normal;
    }

    &-right {
      display: flex;
      gap: 32px;

      &-important {
        display: flex;
        align-items: center;
        gap: 8px;
        padding: 8px 12px;
        border-radius: 6px;
        background-color: var(--red-50, #fdf2f2);

        & h4 {
          color: #e02424;
          font-size: 14px;
          font-weight: 600;
          line-height: 16px;
          margin-bottom: 4px;
        }

        & p {
          color: #e02424;
          font-size: 14px;
          font-weight: 400;
          line-height: 14px;
        }
      }

      &-button {
        display: flex;
        gap: 24px;

        & div {
          display: flex;
          flex-direction: column;
          align-items: center;
          justify-content: center;
          gap: 4px;
        }
      }
    }
  }

  &__bottom {
    display: grid;
    grid-template-columns: repeat(3, 1fr);
    border-top: 1px solid #ebebeb;
    padding: 32px;
    border-radius: 0px 0px 16px 16px;
    background-color: #fff;

    &-number {
      display: flex;
      flex-direction: column;
      gap: 24px;

      & span {
        color: #656565;
        font-size: 14px;
        font-weight: 500;
        line-height: 20px;
        text-transform: uppercase;
        margin-bottom: 4px;
      }

      & h3 {
        color: #2b67f6;
        font-size: 24px;
        font-weight: 500;
        line-height: 32px;
      }

      & p {
        color: #000;
        font-size: 20px;
        font-weight: 400;
        line-height: 32px;
      }
    }

    &-agency {
      display: flex;
      flex-direction: column;

      & span {
        color: #656565;
        font-size: 14px;
        font-weight: 500;
        line-height: 20px;
        text-transform: uppercase;
        margin-bottom: 4px;
      }

      & h3 {
        color: #000;
        font-size: 20px;
        font-weight: 500;
        line-height: 32px;
      }

      & p {
        color: #000;
        font-size: 20px;
        font-weight: 400;
        line-height: 32px;
      }
    }

    &-contact {
      display: flex;
      flex-direction: column;
      justify-content: space-between;

      &-top {
        & span {
          color: #656565;
          font-size: 14px;
          font-weight: 500;
          line-height: 20px;
          text-transform: uppercase;
          margin-bottom: 4px;
        }

        & p {
          position: relative;
          color: #000;
          font-size: 20px;
          font-weight: 400;
          line-height: 32px;
          padding-left: 30px;

          &::after {
            position: absolute;
            top: 14px;
            left: 12px;
            width: 5px;
            height: 5px;
            border-radius: 50%;
            background-color: #000;
            content: "";
          }
        }
      }

      &-bottom {
        & span {
          color: #656565;
          font-size: 14px;
          font-weight: 500;
          line-height: 20px;
          text-transform: uppercase;
          margin-bottom: 10px;
        }

        &-control {
          display: flex;
          gap: 16px;

          & input {
            height: 48px;
            padding: 8px 12px;
            border-radius: 8px;
            background-color: #f8f8f8;
            width: 100%;

            &::placeholder {
              color: #8d8d8d;
              font-size: 16px;
              font-weight: 400;
              line-height: normal;
            }
          }

          & button {
            padding: 12px 30px;
            border-radius: 27px;
            background-color: #2b67f6;

            & svg {
              width: 36px;
              height: 24px;
            }
          }
        }
      }
    }
  }
}
