.result-table-price {
  display: none;

  &.open {
    display: block;
    margin-bottom: 32px;
  }

  &-wrapper {
    border-radius: 16px;
    background: #fff;
    padding: 24px 0;
    overflow: hidden;
  }

  &-slider {
    width: calc(100% - 175px);
  }

  &__top {
    position: relative;
    display: flex;
    justify-content: end;
    align-items: center;
    gap: 52px;
    margin-bottom: 16px;
    margin-left: 50px;
    padding: 0 24px;

    &-button {
      display: flex;
      padding: 0 12px;
      border: 1px solid #ebebeb;
      border-radius: 100px;

      &:hover {
        background-color: #2b67f6;
      }

      & svg {
        max-width: fit-content;
        color: #000;
      }

      &:hover svg {
        color: #fff;
      }

      &.next {
        transform: rotate(180deg);
      }
    }

    &-date {
      display: flex;

      & p {
        color: #000;
        font-family: Archivo;
        font-size: 16px;
        font-weight: 500;
        line-height: 20px;
        text-transform: uppercase;

        &.mountain {
          position: relative;
          display: flex;
          align-items: center;
          gap: 8px;

          & img {
            width: 16px;
            height: 16px;
            border-radius: 4px;
            background-color: #00004c;
            padding: 2px;
            position: relative;

            &:hover + .tooltip-text {
              visibility: visible;
              opacity: 1 !important;
            }

            & + .tooltip-text {
              position: absolute;
              z-index: 3;
              bottom: 148%;
              left: 27%;
              background-color: #e8f0ff;
              color: #000;
              text-align: center;
              font-size: 14px;
              font-weight: 400;
              line-height: 18px;
              visibility: hidden;
              width: 230px;
              border-radius: 8px;
              padding: 8px 12px;
              margin-left: -60px;
              opacity: 0;
              transition: opacity 0.3s;
              text-transform: capitalize;

              &::after {
                content: "";
                position: absolute;
                top: 100%;
                left: 50%;
                margin-left: -5px;
                border-width: 8px;
                border-style: solid;
                border-color: #e8f0ff transparent transparent transparent;
              }
            }
          }
        }
      }
    }
  }

  &__cost {
    display: flex;
    border-top: 1px solid #ebebeb;
    padding-right: 124px;

    & > .result-table-price-slider {
      position: relative;

      &::after {
        position: absolute;
        top: 2px;
        right: -145px;
        width: 100px;
        height: 46px;
        background-color: #fff;
        content: "";
        z-index: 3;
      }
    }

    &.active {
      border-radius: 6px;
      background-color: #2b67f6;

      & .result-table-price__cost-checkbox {
        background-color: #2b67f6;

        &-label {
          color: #fff;
        }
      }

      & > .result-table-price-slider {
        position: relative;

        &::after {
          position: absolute;
          top: 2px;
          right: -145px;
          width: 100px;
          height: 46px;
          background-color: #2b67f6;
          content: "";
          z-index: 3;
        }
      }

      & p {
        color: #fff;

        &.absent {
          color: rgba(255, 255, 255, 0.5);
        }
      }

      & .result-table-price__cost-checkbox-button {
        background-color: #fff;
        color: #2b67f6;
      }
    }

    &-content {
      display: flex;
      padding: 16px 0;

      .tooltip {
        position: relative;
        display: inline-block;

        &:hover .tooltip-text {
          visibility: visible;
          opacity: 1 !important;
        }

        & .tooltip-text {
          position: absolute;
          z-index: 3;
          bottom: 125%;
          left: 20%;
          background-color: #e8f0ff;
          color: #000;
          text-align: center;
          font-size: 14px;
          font-weight: 400;
          line-height: 18px;
          visibility: hidden;
          width: 120px;
          border-radius: 8px;
          padding: 8px 12px;
          margin-left: -60px;
          opacity: 0;
          transition: opacity 0.3s;
          text-transform: capitalize;

          &::after {
            content: "";
            position: absolute;
            top: 100%;
            left: 50%;
            margin-left: -5px;
            border-width: 8px;
            border-style: solid;
            border-color: #e8f0ff transparent transparent transparent;
          }

          &.orange {
            display: flex;
            gap: 8px;
            left: -16px;
            align-items: center;
            color: #ff8a4c;
            font-size: 14px;
            font-weight: 700;
            line-height: 18px;
            width: 212px;

            & span {
              color: #2b67f6;
              font-size: 14px;
              font-weight: 500;
              line-height: 18px;
              text-decoration: line-through;
            }
          }
        }
      }
    }

    &-sub {
      display: flex;
      padding: 16px 0;
      background-color: #e8f0ff;
      transition: transform 0.3s ease;
      padding-right: 124px;

      & > .result-table-price-slider {
        position: relative;

        &::after {
          position: absolute;
          top: 2px;
          right: -145px;
          width: 100px;
          height: 30px;
          background-color: #e8f0ff;
          content: "";
          z-index: 3;
        }
      }

      &-wrapper {
        display: none;

        &.open {
          display: block;
        }
      }

      &-search {
        display: flex;
        gap: 4px;
        align-items: center;
        max-width: 176px;
        width: 100%;
        padding-left: 24px;
        background-color: #e8f0ff;
        position: relative;
        z-index: 3;

        & img {
          width: 14px;
          height: 14px;
        }

        & span {
          color: #000;
          font-size: 14px;
          font-weight: 400;
          line-height: 20px;
        }
      }

      & p {
        color: #2b67f6;
        font-family: Archivo;
        font-size: 20px;
        font-weight: 500;
        line-height: 20px;
        text-transform: uppercase;

        &.absent {
          text-decoration-line: line-through;
          color: rgba(43, 103, 246, 0.5);
        }
      }
    }

    &-checkbox {
      display: flex;
      align-items: center;
      gap: 8px;
      min-width: 176px;
      padding-left: 24px;
      background-color: #fff;
      position: relative;
      z-index: 2;

      &-button {
        width: 14px;
        height: 14px;
        color: #fff;
        background-color: #2b67f6;
        border-radius: 3px;

        &.open {
          transform: rotate(180deg);
        }
      }

      &-label {
        color: #000;
        font-family: Archivo;
        font-size: 16px;
        font-style: normal;
        font-weight: 500;
        line-height: 20px;
        text-transform: capitalize;
      }
    }

    & p {
      color: #2b67f6;
      font-family: Archivo;
      font-size: 20px;
      font-weight: 500;
      line-height: 20px;
      text-transform: uppercase;

      &.active {
        color: #fff;
      }

      &.absent {
        text-decoration-line: line-through;
        color: rgba(43, 103, 246, 0.5);
      }

      &.thumbs .tooltip {
        position: relative;
        display: inline-block;

        &:hover + .tooltip-text {
          visibility: visible;
          opacity: 1 !important;
        }

        & + .tooltip-text {
          position: absolute;
          z-index: 3;
          bottom: 125%;
          right: -28%;
          background-color: #e8f0ff;
          color: #000;
          text-align: center;
          font-size: 13px;
          font-weight: 400;
          line-height: 18px;
          visibility: hidden;
          width: 137px;
          border-radius: 8px;
          padding: 8px 12px;
          margin-left: -60px;
          opacity: 0;
          transition: opacity 0.3s;
          text-transform: capitalize;

          &::after {
            content: "";
            position: absolute;
            top: 100%;
            left: 50%;
            margin-left: -5px;
            border-width: 8px;
            border-style: solid;
            border-color: #e8f0ff transparent transparent transparent;
          }
        }
      }
    }
  }

  &-mobile {
    display: none;

    &.open {
      display: block;
    }

    &-wrapper {
      padding: 16px 0;
      border-radius: 16px;
      background: #fff;
    }

    &__cost {
      display: flex;
      justify-content: space-between;
      align-items: center;
      padding: 16px 32px;
      border-top: 1px solid #ebebeb;

      &.open {
        background-color: rgba(43, 103, 246, 0.05);
      }

      &:last-child {
        border: none;
      }

      &.active {
        background-image: url("../img/chevron-right.svg");
        background-position: right center;
        background-repeat: no-repeat;
      }

      &-wrapper {
        display: flex;
        align-items: center;
        gap: 6px;

        & p {
          color: #000;
          font-size: 16px;
          font-weight: 500;
          line-height: 20px;
          text-transform: capitalize;
        }
      }

      &-button {
        width: 14px;
        height: 14px;
        color: #fff;
        background-color: #2b67f6;
        border-radius: 3px;

        &.open {
          transform: rotate(180deg);
        }
      }

      &-content p {
        color: #2b67f6;
        text-align: right;
        font-size: 18px;
        font-weight: 500;
        line-height: 20px;
        text-transform: uppercase;

        &.absent {
          text-decoration-line: line-through;
          color: rgba(43, 103, 246, 0.5);
        }
      }

      &-sub {
        display: flex;
        align-items: center;
        justify-content: space-between;
        padding: 0 32px;

        &.active {
          background-image: url("../img/chevron-right.svg");
          background-position: right center;
          background-repeat: no-repeat;
        }

        &-wrapper {
          display: none;
          padding: 0 0 16px;
          flex-direction: column;
          gap: 24px;
          background-color: rgba(43, 103, 246, 0.05);

          &.open {
            display: flex;
          }
        }

        &-search {
          & span {
            color: #000;
            font-size: 14px;
            font-weight: 400;
            line-height: 20px;
            text-transform: capitalize;
          }
        }

        &-content p {
          color: #2b67f6;
          text-align: right;
          font-size: 18px;
          font-weight: 500;
          line-height: 20px;
          text-transform: uppercase;

          &.absent {
            text-decoration-line: line-through;
            color: rgba(43, 103, 246, 0.5);
          }
        }
      }
    }

    &-slider {
      margin-bottom: 8px;
      padding: 0 16px !important;
      overflow: hidden;
    }

    &__top-date {
      display: flex;
      gap: 8px;
    }

    &-slide {
      color: #2b67f6;
      font-size: 12px;
      font-weight: 500;
      line-height: normal;
      text-transform: uppercase;
      border-radius: 63px;
      background: transparent;
      border: 1px solid #2b67f6;
      padding: 6px 12px;
      max-width: fit-content;

      &:hover {
        background-color: #2b67f6;
        color: #fff;
      }
    }

    &__select {
      display: flex;
      align-items: center;
      gap: 6px;
      border-radius: 4px;
      background-color: rgba(43, 103, 246, 0.06);
      padding: 4px;
      margin: 8px 32px 0;

      & select {
        color: #2b67f6;
        font-size: 12px;
        font-weight: 600;
        line-height: 20px;
        text-transform: uppercase;
        -webkit-appearance: none;
        -moz-appearance: none;
        appearance: none;
        background-image: url("../img/chevron-right.svg");
        background-position: right center;
        background-repeat: no-repeat;
        width: 100%;
      }
    }
  }
}
