.product-main {
  padding: 150px 0;
  background-image: url("../img/bg-product-main.png");
  background-repeat: no-repeat;
  background-size: cover;
  position: relative;

  &::before {
    position: absolute;
    bottom: -1px;
    width: 100%;
    height: 220px;
    background: linear-gradient(
      180deg,
      rgba(239, 239, 239, 0) 0%,
      #f2f2f2 100%
    );
    content: "";
  }

  &-wrapper {
    display: flex;
    flex-direction: column;
    gap: 56px;

    @media screen and (min-width: 768px) {
      gap: 150px;
    }
  }

  &__top {
    display: none;

    @media screen and (min-width: 768px) {
      display: flex;
      justify-content: space-between;
      align-items: left;
    }

    &-button {
      display: flex;
      justify-content: center;
      align-items: center;
      gap: 8px;
      color: #fff;
      font-size: 16px;
      font-weight: 400;
      line-height: normal;
    }

    &-mobile {
      display: block;

      @media screen and (min-width: 768px) {
        display: none;
      }

      & .subtitle {
        color: #fff;
        font-size: 16px;
        font-weight: 400;
        line-height: 16px;
      }
      & .title {
        text-align: left;
        color: #fff;
        font-size: 32px;
        font-weight: 600;
        line-height: normal;
        margin: 0;
        padding: 0;
      }
      & .text {
        color: #fff;
        font-size: 16px;
        font-weight: 400;
        line-height: 20px;
      }
    }
  }

  &__bottom {
    display: flex;
    justify-content: space-between;
    align-items: flex-end;
    gap: 4px;
    position: relative;
    z-index: 1;

    @media screen and (min-width: 768px) {
      align-items: center;
    }

    &-left {
      display: none;

      @media screen and (min-width: 768px) {
        display: block;
      }

      & .subtitle {
        color: #fff;
        font-size: 20px;
        font-weight: 400;
        line-height: 20px;
      }
      & .title {
        text-align: left;
        margin: 0;
        color: #fff;
        font-size: 64px;
        font-weight: 600;
        line-height: normal;
        margin-bottom: 4px;
      }
      & .text {
        color: #fff;
        font-size: 22px;
        font-weight: 400;
        line-height: 20px;
      }
      & .info {
        display: flex;
        align-items: center;
        gap: 4px;
        color: #fff;
        font-size: 14px;
        font-weight: 500;
        line-height: normal;
        margin-top: 4px;

        & .help {
          position: relative;
          display: flex;
          align-items: center;
          gap: 8px;

          &:hover .tooltip-text {
            visibility: visible;
            opacity: 1 !important;
          }

          & .tooltip-text {
            position: absolute;
            z-index: 1;
            bottom: 175%;
            left: 59%;
            background-color: #e8f0ff;
            color: #000;
            text-align: center;
            font-size: 13px;
            font-weight: 400;
            line-height: 18px;
            visibility: hidden;
            width: 220px;
            border-radius: 8px;
            padding: 8px 12px;
            margin-left: -60px;
            opacity: 0;
            transition: opacity 0.3s;
            text-transform: capitalize;

            & h3 {
              color: #000;
              font-size: 18px;
              font-weight: 600;
              line-height: 20px;
              margin-bottom: 16px;
            }

            & p {
              display: flex;
              gap: 8px;
              color: #000;
              font-size: 16px;
              font-weight: 400;
              line-height: 20px;
              text-transform: capitalize;

              &:not(:last-child) {
                margin-bottom: 12px;
              }
            }

            &::after {
              content: "";
              position: absolute;
              top: 100%;
              left: 49%;
              margin-left: -5px;
              border-width: 8px;
              border-style: solid;
              border-color: #e8f0ff transparent transparent transparent;
              content: " ";
            }
          }
        }
      }

      &-mobile {
        display: block;

        @media screen and (min-width: 768px) {
          display: none;
        }
      }
    }

    &-right {
      & .price {
        color: #fff;
        text-align: right;
        font-size: 40px;
        font-weight: 700;
        line-height: 30px;

        @media screen and (min-width: 768px) {
          font-size: 64px;
          line-height: 58px;
        }

        &--old {
          display: none;

          @media screen and (min-width: 768px) {
            display: inline;
            color: #fff;
            text-align: right;
            font-size: 32px;
            font-weight: 500;
            line-height: normal;
            text-decoration-line: line-through;
            opacity: 0.6;
            margin-right: 4px;
          }
        }

        &-uptitle {
          color: #fff;
          text-align: right;
          font-size: 12px;
          font-weight: 500;
          line-height: 12px;
          margin-bottom: 6px;

          @media screen and (min-width: 768px) {
            font-size: 14px;
            line-height: 58px;
            margin-bottom: -20px;
          }
        }
        &-subtext {
          color: #fff;
          text-align: right;
          font-size: 12px;
          font-weight: 500;
          line-height: 12px;

          @media screen and (min-width: 768px) {
            text-align: right;
            font-size: 14px;
            margin-top: 4px;
          }
        }
        &-text {
          color: #fff;
          text-align: right;
          font-size: 12px;
          font-weight: 500;
          line-height: 12px;
          text-transform: uppercase;

          @media screen and (min-width: 768px) {
            font-size: 16px;
            font-weight: 400;
            line-height: 100%;
            margin-top: 6px;
          }
        }
      }
    }
  }
}
