.footer {
  padding: 80px 0;
  background-color: #00004c;

  @media screen and (max-width: 1150px) {
    padding: 40px 10px;
  }

  &__top {
    display: flex;
    align-items: center;
    justify-content: space-between;

    @media screen and (max-width: 1150px) {
      flex-direction: column;
      gap: 24px;
    }

    &-content {
      display: flex;
      align-items: center;
      justify-content: space-between;
      padding: 12px 56px;
      border-radius: 1000px;
      background-color: #2b67f6;
      max-width: 830px;
      width: 100%;

      @media screen and (max-width: 1150px) {
        display: grid;
        grid-template-columns: 1fr 1fr;
        column-gap: 70px;
        row-gap: 8px;
        border-radius: 8px;
        padding: 12px 16px;
        width: auto;
      }

      & a {
        color: #fff;
        font-size: 16px;
        font-weight: 400;
        line-height: 24px;

        &:hover {
          color: #000;
        }
      }
    }
  }

  &__bottom {
    display: flex;
    justify-content: space-between;
    padding-top: 40px;
    position: relative;

    @media screen and (max-width: 1150px) {
      flex-direction: column;
      align-items: center;
      padding-top: 24px;
      gap: 24px;
    }

    &::after {
      position: absolute;
      left: -125px;
      bottom: 125px;
      width: 434px;
      height: 194px;
      background-image: url("../img/footer-vector.png");
      content: "";

      @media screen and (max-width: 1150px) {
        display: none;
      }
    }

    &-left {
      display: flex;
      flex-direction: column;
      justify-content: space-between;
      position: relative;
      z-index: 1;

      &-social {
        display: flex;
        flex-direction: column;
        align-items: center;
        justify-content: center;
        gap: 20px;

        @media screen and (max-width: 1150px) {
          flex-direction: row;
          gap: 24px;
        }

        & a {
          display: flex;
          padding: 10px;
          border-radius: 100px;
          background-color: #2b67f6;
          color: #ffffff;
          border: 1px solid #2b67f6;
          width: 40px;
          height: 40px;

          &:hover {
            background-color: transparent;
            color: #2b67f6;
          }
        }
      }

      &-back {
        padding: 22px;
        background-color: transparent;
        border-radius: 186.364px;
        border: 1px solid #2b67f6;
        color: #2b67f6;
        cursor: pointer;

        &:hover {
          background-color: #2b67f6;
          color: #fff;
        }

        @media screen and (max-width: 1150px) {
          display: none;
        }
      }
    }

    &-right {
      display: grid;
      grid-template-columns: repeat(3, 1fr);
      row-gap: 32px;
      max-width: 830px;
      width: 100%;
      position: relative;
      z-index: 1;

      &-item {
        &:nth-child(4) {
          grid-column: 1 / 3;
        }

        @media screen and (max-width: 1150px) {
          display: none;
        }

        &-title {
          color: #fff;
          font-size: 14px;
          font-weight: 600;
          line-height: 150%;
          text-transform: uppercase;
          padding-bottom: 12px;
        }

        &-content {
          display: flex;
          flex-direction: column;
          gap: 4px;
          padding-top: 12px;
          border-top: 1px solid rgba($color: #ffffff, $alpha: 0.2);
          width: fit-content;

          & a {
            color: #fff;
            font-size: 14px;
            font-weight: 300;
            line-height: 24px;

            &:hover {
              color: #2b67f6;
            }
          }

          &-wrapper {
            display: flex;
            border-top: 1px solid rgba($color: #ffffff, $alpha: 0.2);
            gap: 130px;
            width: fit-content;
          }
        }
      }

      &-policy {
        display: flex;
        align-items: center;
        justify-content: space-between;
        width: 100%;
        padding: 14px 56px;
        border-radius: 1000px;
        background-color: #1a1a5e;
        grid-column: 1 / 4;

        @media screen and (max-width: 1150px) {
          border-radius: 8px;
          padding: 16px;
          flex-direction: column;
          align-items: flex-start;
          gap: 16px;
        }

        & a {
          color: #fff;
          font-size: 12px;
          font-weight: 400;
          line-height: 12px;
          opacity: 0.7;

          @media screen and (max-width: 1150px) {
            font-size: 14px;
          }

          &:hover {
            color: #2b67f6;
          }
        }
      }
    }

    &-reserved {
      display: none;
      color: #fff;
      font-size: 12px;
      font-weight: 300;
      line-height: 16px;
      opacity: 0.5;

      @media screen and (max-width: 1150px) {
        display: block;
      }
    }

    &-mobile-menu {
      display: none;

      @media screen and (max-width: 1150px) {
        display: flex;
        flex-direction: column;
        width: 100%;
      }

      &-item {
        &-header {
          display: flex;
          justify-content: space-between;
          position: relative;
          margin-bottom: 16px;
          padding: 16px 0;
          border-bottom: 1px solid rgba(255, 255, 255, 0.2);
          cursor: pointer;

          &:hover {
            & .footer__bottom-mobile-menu-item-header-title {
              color: #2b67f6;
            }
          }

          & svg.open {
            transform: rotate(180deg);
          }

          &-title {
            color: #fff;
            font-size: 14px;
            font-weight: 600;
            line-height: 150%;
            text-transform: uppercase;

            &.open {
              color: #2b67f6;
            }
          }
        }

        &:first-child {
          padding-top: 0;
        }

        &-sub {
          display: none;

          &.open {
            display: Flex;
            flex-direction: column;
            gap: 10px;
            margin-bottom: 20px;
          }

          a {
            color: #fff;
            font-size: 14px;
            font-weight: 400;
            line-height: 16px;
          }

          &-content {
            display: flex;
            flex-direction: column;
            gap: 10px;
          }
        }
      }
    }
  }
}
