.product-description {
  &__top {
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;
    gap: 32px;
    padding-bottom: 32px;

    &-slider {
      position: relative;
      overflow: hidden;

      .product-description__top-slide {
        display: flex;
        justify-content: center;
        width: auto;

        button {
          color: rgba(107, 111, 123, 0.97);
          font-size: 14px;
          font-weight: 500;
          line-height: normal;
          text-transform: uppercase;
          border-radius: 63px;
          background: #fff;
          padding: 12px 16px;
        }

        .active {
          color: #fff;
          background-color: #2b67f6;
        }

        :hover {
          color: #fff;
          background-color: #2b67f6;
        }
      }
    }

    & h2 {
      color: #333;
      font-size: 24px;
      font-weight: 600;
      line-height: 30px;

      @media screen and (min-width: 800px) {
        font-size: 32px;
        line-height: normal;
      }
    }

    &-contents {
      display: none;

      @media screen and (min-width: 800px) {
        display: flex;
        gap: 8px;
      }

      & button {
        color: rgba(107, 111, 123, 0.97);
        font-size: 14px;
        font-weight: 500;
        line-height: normal;
        text-transform: uppercase;
        border-radius: 63px;
        background: #fff;
        padding: 12px 16px;

        &.active {
          color: #fff;
          background-color: #2b67f6;
        }

        &:hover {
          color: #fff;
          background-color: #2b67f6;
        }
      }

      &-mobile {
        display: block;
        width: 100%;

        @media screen and (min-width: 800px) {
          display: none;
        }
      }
    }
  }

  &__slider {
    position: relative;
    overflow: hidden;

    .swiper-pagination-bullet {
      width: 10px;
      height: 10px;
      background-color: #fff;
      opacity: 1;
    }

    .swiper-pagination-bullet-active {
      background-color: #000;
      border: 2px solid #fff;
    }

    .product-description__slide {
      display: flex;
      justify-content: center;
    }
  }

  &__bottom {
    display: flex;
    flex-direction: column;
    gap: 32px;
    align-items: center;
    justify-content: center;
    border-radius: 16px;
    background: #fff;
    padding: 16px;
    margin-bottom: 20px;

    @media screen and (min-width: 800px) {
      margin-bottom: 24px;
      padding: 40px;
    }

    &-header {
      display: none;

      @media screen and (min-width: 800px) {
        display: grid;
        grid-template-columns: 1fr 1fr;
        gap: 32px;
      }

      & img {
        max-width: 540px;
        width: 100%;
      }

      &-mobile {
        display: block;
        width: 100%;

        @media screen and (min-width: 800px) {
          display: none;
        }
      }
    }

    &-list {
      width: 100%;

      & li {
        display: grid;
        padding: 16px 0;
        border-top: 1px solid #ebebeb;

        @media screen and (min-width: 800px) {
          grid-template-columns: 1fr 1fr;
          padding: 24px 0;
        }

        &:first-child {
          padding-top: 0;
          border-top: none;
        }

        &:last-child {
          padding-bottom: 0;
        }

        & h3 {
          color: #000;
          font-size: 16px;
          font-weight: 600;
          line-height: 20px;

          @media screen and (min-width: 800px) {
            font-size: 18px;
            line-height: normal;
          }

          & span {
            color: #000;
            font-size: 16px;
            font-weight: 400;
            line-height: 20px;

            @media screen and (min-width: 800px) {
              font-size: 18px;
              line-height: normal;
            }
          }
        }

        & div {
          display: flex;
          align-items: center;
          color: #656565;
          font-size: 14px;
          font-weight: 400;
          line-height: 20px;
          gap: 4px;

          @media screen and (min-width: 800px) {
            font-size: 16px;
            line-height: 24px;
          }
        }
      }

      &-item-mobile {
        @media screen and (max-width: 800px) {
          grid-template-columns: 1fr !important;

          & h3 {
            order: 2;
          }
        }
      }
    }

    & .bottom-image {
      display: none;

      @media screen and (min-width: 800px) {
        display: block;
      }
    }
  }

  &__texts {
    display: flex;
    flex-direction: column;
    gap: 24px;
    padding-bottom: 70px;

    &-item {
      display: flex;
      flex-direction: column;
      gap: 8px;

      & h3 {
        color: #656565;
        font-size: 12px;
        font-weight: 500;
        line-height: normal;

        @media screen and (min-width: 800px) {
          font-size: 14px;
        }
      }

      & p {
        color: #656565;
        font-size: 10px;
        font-weight: 400;
        line-height: 14px;

        @media screen and (min-width: 800px) {
          font-size: 12px;
          line-height: 16px;
        }
      }
    }
  }
}
