.orden-menu {
  position: fixed;
  top: 0;
  left: 0;
  z-index: 100;
  width: 100%;
  height: 100%;
  background-color: #f2f2f2;
  transform: translateX(100%);
  transition: transform 0.3s;
  padding: 47px 10px;
  overflow-y: auto;

  &__radio {
    display: flex;
    flex-direction: column;
    align-items: flex-start;
    gap: 16px;

    &-title {
      color: #000;
      text-align: center;
      font-family: Archivo;
      font-size: 16px;
      font-weight: 600;
      line-height: normal;
    }
  }

  &-title {
    color: #333;
    text-align: center;
    font-family: Archivo;
    font-size: 24px;
    font-weight: 600;
    line-height: 40px;
    margin-bottom: 32px;
  }

  &__inner {
    display: flex;
    flex-direction: column;
    gap: 24px;
    padding: 24px 32px;
    border-radius: 24px;
    background: #fff;
  }

  &.is-open {
    transform: translateX(0);
  }

  &__btn-close {
    width: 48px;
    height: 32px;
    display: flex;
    align-items: center;
    justify-content: center;
    border: none;
    border-radius: 100px;
    background-color: #fff;
    padding: 12px;
    margin-bottom: 23px;
    cursor: pointer;

    & svg {
      color: #000000;
    }

    &:hover {
      background-color: #2b67f6;

      & svg {
        color: #ffffff;
      }
    }
  }
}
