.categorie-guaranteed {
  display: none;
  background-color: #fff;
  padding: 0 32px 32px;
  border-radius: 0px 0px 16px 16px;

  @media screen and (max-width: 1100px) {
    padding: 0 16px 16px;
  }

  &.open {
    display: block;
  }

  &-top {
    display: flex;
    align-items: center;
    justify-content: space-between;
    padding-bottom: 16px;
    border-bottom: 1px solid #ebebeb;

    & h2 {
      color: #333;
      font-size: 18px;
      font-weight: 600;
      line-height: normal;

      @media screen and (max-width: 1100px) {
        font-size: 16px;
      }
    }

    &-plan {
      color: #2b67f6;
      font-size: 16px;
      font-weight: 500;
      line-height: normal;
      display: flex;
      align-items: center;
      gap: 8px;

      & p {
        @media screen and (max-width: 1100px) {
          display: none;
        }
      }
    }
  }

  &-bottom {
    &-item {
      display: flex;
      align-items: center;
      justify-content: space-between;
      padding: 16px 0;
      border-bottom: 1px solid #ebebeb;

      @media screen and (max-width: 1100px) {
        flex-direction: column;
        align-items: flex-start;
      }

      &-img {
        @media screen and (max-width: 1100px) {
          display: flex;
          width: 100%;
          justify-content: space-between;
          gap: 8px;
        }

        &-wrapper {
          display: flex;
          gap: 8px;
        }

        &-content {
          color: #fff;
          font-size: 14px;
          font-weight: 500;
          line-height: 14px;
          text-transform: capitalize;
          padding: 4px;
          border-radius: 4px;
          background-color: #ff8a4c;

          &:last-child {
            display: none;
          }

          @media screen and (max-width: 1100px) {
            font-size: 12px;

            &:last-child {
              display: block;
            }
          }
        }

        &-price {
          display: none;

          @media screen and (max-width: 1100px) {
            display: flex;
            align-items: center;
            gap: 8px;
            color: #333;
            font-size: 20px;
            font-weight: 700;
            line-height: 24px;
          }
        }
      }

      &-type {
        color: #000;
        font-size: 14px;
        font-weight: 400;
        line-height: 18px;
        max-width: 130px;

        @media screen and (max-width: 1100px) {
          max-width: 100%;
        }
      }

      &-list {
        @media screen and (max-width: 1100px) {
          order: 1;
        }

        & span {
          color: #000;
          font-size: 14px;
          font-weight: 500;
          line-height: 18px;

          @media screen and (max-width: 1100px) {
            font-weight: 600;
          }
        }

        & ul {
          @media screen and (max-width: 1100px) {
            display: flex;
            flex-wrap: wrap;
            order: 1;
          }

          li {
            position: relative;
            color: #656565;
            font-size: 14px;
            font-weight: 400;
            line-height: 18px;
            margin-left: 22px;

            @media screen and (max-width: 1100px) {
              &:first-child {
                margin: 0;

                &::after {
                  display: none;
                }
              }
            }

            &::after {
              position: absolute;
              top: 6px;
              left: -10px;
              width: 5px;
              height: 5px;
              background-color: #656565;
              border-radius: 50%;
              content: "";
            }
          }
        }
      }

      &-oferta {
        color: #2b67f6;
        font-size: 14px;
        font-weight: 400;
        line-height: 18px;
        max-width: 110px;

        @media screen and (max-width: 1100px) {
          max-width: 100%;
        }
      }

      &-price {
        display: block;
        color: #333;
        font-size: 20px;
        font-weight: 700;
        line-height: 24px;

        @media screen and (max-width: 1100px) {
          display: none;
        }
      }

      &-button {
        display: block;
        color: #2b67f6;
        font-size: 14px;
        font-weight: 400;
        line-height: 20px;
        background-color: transparent;
        padding: 10px 32px;
        border-radius: 27px;
        border: 1px solid #2b67f6;

        @media screen and (max-width: 1100px) {
          display: none;
        }

        &:hover {
          background-color: #2b67f6;
          color: #fff;
        }
      }
    }
  }
}
