.cruise {
  display: flex;
  flex-direction: column;
  gap: 24px;
  padding: 24px 32px;
  border-radius: 24px;
  background: #fff;

  &-wrapper {
    display: none;

    @media screen and (min-width: 768px) {
      display: block;
      margin-top: -85px;
      z-index: 12;
      width: 100%;
    }
  }

  &__top {
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    align-items: center;

    @media screen and (min-width: 850px) {
      flex-direction: row;
    }

    &-content {
      display: flex;
      flex-direction: column;
      gap: 18px;
      align-items: center;

      @media screen and (min-width: 850px) {
        flex-direction: row;
      }

      &-title {
        color: #333;
        font-family: Archivo;
        font-size: 24px;
        font-style: normal;
        font-weight: 600;
        line-height: normal;

        @media screen and (min-width: 850px) {
          font-size: 24px;
        }
      }

      &-label input[type="radio"] {
        display: none;
      }

      &-label {
        color: #000;
        text-align: center;
        font-family: Archivo;
        font-size: 16px;
        font-style: normal;
        font-weight: 400;
        line-height: normal;
        display: inline-block;
        position: relative;
        padding-left: 30px;
        margin-right: 16px;
        cursor: pointer;

        &:hover .radio-checkmark {
          background-color: #ccc;
        }

        & input[type="radio"]:checked + .radio-checkmark {
          border: 4px solid #2b67f6;
        }
      }
    }

    &-links {
      display: flex;
      gap: 24px;
    }

    &-link {
      display: flex;
      gap: 4px;
      align-items: center;
      justify-content: center;
      color: #2b67f6;
      font-family: Archivo;
      font-size: 12px;
      font-style: normal;
      font-weight: 400;
      line-height: 20px;
      text-transform: uppercase;
      text-align: center;

      &.tooltip {
        position: relative;
        display: inline-block;

        &:hover .tooltip-text {
          visibility: visible;
          opacity: 1 !important;
        }

        & .tooltip-text {
          position: absolute;
          z-index: 1;
          top: 125%;
          left: 13%;
          background-color: #e8f0ff;
          color: #000;
          text-align: center;
          font-size: 14px;
          font-weight: 400;
          line-height: 18px;
          visibility: hidden;
          width: 250px;
          border-radius: 8px;
          padding: 16px;
          margin-left: -60px;
          opacity: 0;
          transition: opacity 0.3s;
          text-transform: capitalize;

          & h3 {
            color: #000;
            font-size: 18px;
            font-weight: 600;
            line-height: 20px;
            margin-bottom: 16px;
          }

          & p {
            display: flex;
            gap: 8px;
            color: #000;
            font-size: 16px;
            font-weight: 400;
            line-height: 20px;
            text-transform: capitalize;

            &:not(:last-child) {
              margin-bottom: 12px;
            }
          }

          &::after {
            content: "";
            position: absolute;
            bottom: 100%;
            left: 49%;
            margin-left: -5px;
            border-width: 8px;
            border-style: solid;
            border-color: transparent transparent #e8f0ff transparent;
            content: " ";
          }
        }
      }
    }
  }

  &__bottom {
    display: grid;
    grid-template-columns: 1fr;
    row-gap: 16px;
    column-gap: 32px;

    @media screen and (min-width: 850px) {
      grid-template-columns: repeat(3, 1fr);
    }

    &-item {
      display: flex;
      gap: 8px;
      align-items: center;
      height: 48px;
      border-radius: 8px;
      background: #f8f8f8;
      padding: 8px 12px;
      width: 100%;
    }

    &-select {
      position: relative;
      -webkit-appearance: none;
      -moz-appearance: none;
      appearance: none;
      background-image: url("../img/chevron-down.svg");
      background-position: right center;
      background-repeat: no-repeat;
      height: 48px;
      border-radius: 8px;
      padding: 8px 12px;
      width: 100%;

      &::-ms-expand {
        display: none;
      }
    }

    &-button {
      color: #fff;
      font-family: Archivo;
      font-size: 20px;
      font-style: normal;
      font-weight: 400;
      line-height: 20px;
      border-radius: 27px;
      background: #2b67f6;
      padding: 12px 40px;
      text-align: center;

      &:hover {
        background-color: #1753e2;
      }
    }
  }
}

.radio-group {
  display: flex;
  justify-content: center;
  gap: 16px;
}

.radio-checkmark {
  position: absolute;
  top: 0;
  left: 0;
  height: 16px;
  width: 16px;
  border: 1px solid #ebebeb;
  background: #f8f8f8;
  border-radius: 50%;
}
