.modal-promo {
  position: relative;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  gap: 24px;
  background-color: #ffffff;
  border-radius: 24px;
  padding: 40px;
  max-width: 550px;

  &-wrapper {
    display: flex;
    justify-content: center;
    align-items: center;
  }

  &-backdrop {
    display: none;
    position: fixed;
    width: 100%;
    height: 100%;
    z-index: 9999;
    top: 0;
    left: 0;
    justify-content: center;
    align-items: center;
    background-color: rgba(0, 0, 0, 0.3);

    &.open {
      display: flex;
    }
  }

  &__close {
    position: absolute;
    top: 24px;
    right: 24px;
    width: 32px;
    height: 32px;
    background-color: #fff;
    cursor: pointer;
  }

  & h2 {
    color: #333;
    text-align: center;
    font-size: 32px;
    font-weight: 600;
    line-height: normal;
  }

  &__content {
    display: flex;
    align-items: center;
    gap: 16px;

    & input {
      width: 350px;
      height: 48px;
      padding: 8px 12px;
      border-radius: 8px;
      background-color: #f8f8f8;

      &::placeholder {
        color: #000;
        font-size: 16px;
        font-weight: 400;
        line-height: normal;
      }
    }

    & button {
      height: 48px;
      padding: 12px 40px;
      border-radius: 27px;
      background-color: #2b67f6;

      &:hover {
        background-color: #1753e2;
      }
    }
  }
}
