.travel-insurance {
  width: 100%;
  display: flex;
  flex-direction: column;
  align-items: flex-end;
  display: none;

  @media screen and (max-width: 880px) {
    align-items: normal;
  }

  &.open {
    display: flex;
  }

  &__top {
    padding: 24px;
    border-radius: 16px 16px 0px 0px;
    background-color: #fff;
    width: 100%;

    @media screen and (max-width: 880px) {
      padding: 16px;
    }

    & h2 {
      display: flex;
      align-items: center;
      gap: 12px;
      color: #333;
      font-size: 22px;
      font-weight: 600;
      line-height: normal;

      @media screen and (max-width: 880px) {
        font-size: 18px;
      }
    }
  }

  &__bottom {
    padding: 32px;
    border-radius: 0px 0px 16px 16px;
    background-color: #fff;
    margin-bottom: 24px;
    border-top: 1px solid #ebebeb;

    @media screen and (max-width: 880px) {
      padding: 16px;
    }

    & img {
      margin-bottom: 16px;
    }

    & p {
      color: #333;
      font-size: 16px;
      font-weight: 400;
      line-height: 22px;
      margin-bottom: 24px;

      @media screen and (max-width: 880px) {
        font-size: 14px;
      }
    }

    &-list {
      display: flex;
      justify-content: space-between;
      gap: 16px;
      margin-bottom: 24px;

      @media screen and (max-width: 880px) {
        flex-direction: column;
        gap: 12px;
      }

      &-item {
        display: flex;
        padding: 24px;
        border-radius: 16px;
        background-color: #f9f9f9;
        flex-direction: column;
        align-items: flex-start;

        @media screen and (max-width: 880px) {
          padding: 16px;
        }

        & h3 {
          color: #2b67f6;
          font-size: 16px;
          font-weight: 600;
          line-height: normal;
          padding: 6px 12px;
          border-radius: 100px;
          background-color: #e8f0ff;
          margin-bottom: 24px;

          @media screen and (max-width: 880px) {
            font-size: 14px;
          }
        }

        & label {
          color: #000;
          font-size: 16px;
          font-weight: 400;
          line-height: 20px;
          display: block;
          position: relative;
          padding-left: 30px;
          margin-bottom: 12px;
          cursor: pointer;
          font-size: 16px;

          & .checkmark {
            position: absolute;
            top: 0;
            left: 0;
            height: 20px;
            width: 20px;
            background-color: #eee;
            border-radius: 3px;

            &:after {
              content: "";
              position: absolute;
              display: none;
            }
          }

          & input {
            position: absolute;
            opacity: 0;
            cursor: pointer;
          }

          & input:checked ~ .checkmark {
            background-color: rgba(43, 103, 246, 0.4);
          }

          & input:checked ~ .checkmark:after {
            display: block;
          }

          & .checkmark:after {
            left: 7px;
            top: 3px;
            width: 6px;
            height: 12px;
            border: solid white;
            border-width: 0 2px 2px 0;
            transform: rotate(45deg);
          }

          & span {
            font-weight: 700;
          }
        }
      }
    }

    & a {
      display: flex;
      align-items: center;
      gap: 4px;
      color: #2b67f6;
      font-size: 14px;
      font-weight: 500;
      line-height: normal;

      @media screen and (max-width: 880px) {
        justify-content: center;
      }
    }
  }

  &__next {
    color: #fff;
    font-size: 20px;
    font-weight: 400;
    line-height: 20px;
    padding: 16px 56px;
    border-radius: 27px;
    background-color: #2b67f6;
    border: 1px solid #2b67f6;
    margin-bottom: 30px;

    &:hover {
      background-color: transparent;
      color: #2b67f6;
    }
  }
}
