.shipping-line {
  display: flex;
  flex-direction: column;
  align-items: center;
  gap: 40px;
  padding: 100px 0 72px;
  overflow: hidden;

  @media screen and (max-width: 1150px) {
    gap: 24px;
    padding: 40px 0;
  }

  &-title {
    color: #333;
    font-size: 32px;
    font-weight: 600;
    line-height: normal;

    @media screen and (max-width: 1150px) {
      font-size: 24px;
      text-align: center;
    }
  }

  &-controls {
    display: flex;
    align-items: center;
    gap: 8px;
    border-radius: 100px;
    background-color: #fff;
    padding: 12px 24px;

    @media screen and (max-width: 1150px) {
      display: none;
    }

    &-button {
      border-radius: 100px;
      border: 1px solid #ebebeb;
      width: 48px;
      height: 32px;
      display: flex;
      align-items: center;
      justify-content: center;

      &.prev:hover {
        background: #2b67f6;
        color: #fff;
      }
      &.next:hover {
        background: #2b67f6;
        color: #fff;
      }
    }
  }

  &-slider {
    max-width: 1190px;

    @media screen and (max-width: 1150px) {
      padding: 0 16px;
    }
  }

  &-slide {
    position: relative;
    max-width: 584px;
    width: 100% !important;

    @media screen and (max-width: 768px) {
      max-width: 340px;
    }

    &__img {
      width: 100%;
      height: 316px;

      @media screen and (max-width: 768px) {
        display: none;
      }

      &-mobile {
        display: none;
        @media screen and (max-width: 768px) {
          display: block;
        }
      }
    }

    &__title {
      position: absolute;
      left: 32px;
      top: 32px;
      color: #fff;
      font-size: 32px;
      font-weight: 600;
      line-height: 38px;

      @media screen and (max-width: 1150px) {
        line-height: normal;
        left: 24px;
        top: 24px;
      }
    }

    &__button {
      position: absolute;
      left: 32px;
      bottom: 40px;
      color: #fff;
      font-size: 18px;
      font-weight: 400;
      line-height: 20px;
      border-radius: 27px;
      border: 1px solid #fff;
      padding: 12px 40px;

      @media screen and (max-width: 1150px) {
        width: 80%;
        left: 24px;
      }
    }
  }

  &__pagination {
    display: none;

    @media screen and (max-width: 1150px) {
      display: flex;
      align-items: center;
      justify-content: center;
      gap: 8px;
    }

    &-item {
      width: 12px;
      height: 12px;
      padding: 0;
      background-color: #dedede;
      border-radius: 50%;
      border: 2px solid #dedede;

      &--active {
        background-color: #00004c;
      }
    }
  }
}
