.reserv-main {
  position: relative;
  background-image: url("../img/reserv-main-bg.png");
  background-repeat: no-repeat;
  background-size: cover;

  &::before {
    position: absolute;
    bottom: -1px;
    width: 100%;
    height: 220px;
    background: linear-gradient(
      180deg,
      rgba(239, 239, 239, 0) 0%,
      #f2f2f2 100%
    );
    content: "";
  }

  &__top {
    display: flex;
    justify-content: space-between;
    align-items: flex-end;

    & a {
      color: #fff;
      font-size: 16px;
      font-weight: 400;
      line-height: 20px;
      background-color: transparent;
      padding: 12px 32px;
      border-radius: 27px;
      border: 1px solid #fff;
    }
  }

  &__bottom {
    display: flex;
    align-items: flex-end;
    justify-content: space-between;
    padding-bottom: 420px;
    margin-top: 132px;

    &-title {
      & h2 {
        color: #fff;
        font-size: 64px;
        font-weight: 600;
        line-height: normal;
      }

      & p {
        color: #fff;
        font-size: 22px;
        font-weight: 400;
        line-height: 20px;
      }
    }

    & button {
      color: #fff;
      font-size: 16px;
      font-weight: 400;
      line-height: 20px;
      padding: 12px 32px;
      border-radius: 27px;
      background-color: #2b67f6;
    }
  }
}
