.recommendations {
    padding: 70px 0 140px;
    width: 100%;

    @media (max-width: 768px) {
        padding: 40px 0;
    }

    .inner {
        width: 100%;
        max-width: 1191px;
        padding: 0;
        margin: 0 auto;
        position: relative;
    
        @media (max-width: 1024px) {
            padding: 0 16px;
        }

        @media (max-width: 568px) {
            padding: 0;
        }
    }

    &__items {
        display: flex;
        gap: 17px;
        position: relative;
        width: 100%;
        max-width: 1223px;

        @media (max-width: 568px) {
            overflow: visible !important;
            max-width: 260px;
            margin: 0 !important;
        }
    }

    &__item {
        width: 100%;
        max-width: 285px;
        border-radius: 24px;
        height: 100%;
        min-height: 435px;
        max-height: 435px;
        position: relative;
        cursor: pointer;
        transition: all 0.2s;

        @media (max-width: 768px) {
            max-width: 100%;
        }

        &:hover {
            .btn {
                display: flex;
                width: 249px;
                padding: 12px 40px;
                justify-content: center;
                align-items: center;
                gap: 10px;
                border-radius: 27px;
                border: 1px solid #FFF;
                color: #FFF;
                font-family: 'Archivo', sans-serif;
                font-size: 16px;
                font-style: normal;
                font-weight: 400;
                line-height: 20px; /* 125% */
                margin: 11px auto 0;
                height: 44px;

                @media (max-width: 1200px) {
                    width: 100%;
                }

                @media (max-width: 1024px) {
                    height: 40px;
                }
            }

            .recommendations__item {
                &-wrapper {
                    background-color: rgba(43, 103, 246, 0.70);
                }

                &-bottom {
                    margin-top: 55px;

                    @media (max-width: 1100px) {
                        margin-top: 32px;
                    }

                    @media (max-width: 1024px) {
                        height: 37px;
                    }

                    @media (max-width: 868px) {
                        height: 20px;
                    }

                    @media (max-width: 380px) {
                        height: 37px;
                    }

                    @media (max-width: 350px) {
                        height: 23px;
                    }
                }

                &-top {
                    ul {
                        display: none;
                    }

                    .place {
                        display: block;
                        color: #FFF;
                        font-size: 14px;
                        font-style: normal;
                        font-weight: 400;
                        line-height: 18px; /* 128.571% */
                        position: relative;
                        padding-left: 24px;
                        padding-top: 16px;
                        width: 100%;
                        max-width: 225px;

                        @media (max-width: 350px) {
                            font-size: 11px;
                        }

                        &::before {
                            position: absolute;
                            content: '';
                            width: 17px;
                            height: 17px;
                            top: 18px;
                            left: 0;
                            background: url('../img/map1.png') no-repeat;
                        }
                    }
                }
            }

            .recommendations__promo {
                display: none;
            }
        }

        &-wrapper {
            position: relative;
            border-radius: 26.09px;
            background-color: rgba(0, 0, 0, 0.40);
            height: 100%;
            min-height: 435px;
            padding: 18px 15px 18px 17px;
            display: flex;
            flex-direction: column;
            justify-content: space-between;
            align-items: flex-start;
            transition: all 0.2s;

            @media (max-width: 768px) {
                padding: 16px 16.5px;
            }
        }

        .object-fit {
            position: absolute;
            object-fit: cover;
            top: 0;
            left: 0;
            width: 100%;
            height: 100%;
            object-position: center top;
            border-radius: 24px;
        }

        &-top {
            span {
                color: #FFF;
                font-size: 14px;
                font-style: normal;
                font-weight: 400;
                line-height: 21.742px; /* 155.3% */

                @media (max-width: 768px) {
                    font-size: 12px;
                }
            }

            h2 {
                color: #FFF;
                font-size: 24px;
                font-style: normal;
                font-weight: 600;
                line-height: normal;
                border-bottom: 1px solid rgba(255, 255, 255, 0.2);
                padding-bottom: 16px;

                @media (max-width: 768px) {
                    font-size: 18px;
                    margin-top: 8px;
                    padding-bottom: 12px;
                }
            }

            ul {
                padding-top: 16px;

                @media (max-width: 768px) {
                    padding-bottom: 12px;
                }

                li {
                    color: #FFF;
                    position: relative;
                    padding-left: 27px;
                    font-size: 16px;
                    font-style: normal;
                    font-weight: 400;
                    line-height: 21.742px; /* 135.887% */
                    margin-bottom: 4.35px;

                    @media (max-width: 768px) {
                        font-size: 14px;
                    }
                    
                    &::before {
                        position: absolute;
                        content: '';
                        width: 20px;
                        height: 20px;
                        top: 0;
                        left: 0;
                        background-size: 100%;
                        background-position: center;
                    }

                    &:first-of-type {
                        &::before {
                            background: url('../img/ship-white.png') no-repeat;
                        }
                    }

                    &:nth-child(2) {
                        &::before {
                            background: url('../img/clock-white.png') no-repeat center;
                        }
                    }

                    &:last-of-type {
                        margin-bottom: 0;

                        &::before {
                            background: url('../img/calendar-white.png') no-repeat;
                        }
                    }
                }
            }

            .place {
                display: none;
            }
        }

        &-bottom {
            display: flex;
            flex-direction: column;
            align-items: flex-end;
            width: 100%;
            margin-top: 85px;

            span {
                color: #FFF;
                text-align: right;
                font-size: 10px;
                font-style: normal;
                font-weight: 500;
                line-height: 18px;
            }

            p {
                color: #FFF;
                font-size: 40px;
                font-style: normal;
                font-weight: 700;
                line-height: 40.223px;

                @media (max-width: 768px) {
                    font-size: 38px;
                    line-height: 37.2px;
                }
            }
        }

        .btn {
            display: none;
        }
    }

    &__promo {
        display: flex;
        padding: 4.348px 6.523px;
        justify-content: flex-end;
        align-items: center;
        gap: 6.523px;
        border-radius: 86.968px;
        background: #FFF;
        width: 100%;
        max-width: 238px;
        align-self: flex-end;

        @media (max-width: 450px) {
            justify-content: center;
        }

        span {
            display: none;
        }

        &-text {
            display: flex;
            padding: 2.174px 8.369px 2.174px 3.348px;
            justify-content: center;
            align-items: center;
            gap: 8.369px;
            border-radius: 41.01px;
            border: 1.087px solid rgba(255, 138, 76, 0.12);
            background: var(--orange-50, #FFF8F1);

            img {
                width: 17.39px;
                height: 17.39px;
            }

            p {
                color: #FF8A4C;
                font-size: 10px;
                font-style: normal;
                font-weight: 600;
                line-height: 16.739px; /* 167.386% */
                text-transform: uppercase;
                white-space: nowrap;

                @media (max-width: 1100px) {
                    font-size: 8px;
                    line-height: 16px;
                }
            }
        }

        &-images {
            display: flex;
            gap: 6.52px;

            img {
                width: 17.39px;
                height: 17.39px;
            }
        }
    }

    .slider-controls {
        display: flex;
        cursor: pointer;
        padding: 12px 24px;
        align-items: center;
        gap: 8px;
        border-radius: 100px;
        background: #FFF;
        left: 50%;
        transform: translateX(-50%);
        position: absolute;
        bottom: -96px;
        width: 100%;
        max-width: 152px;

        @media (max-width: 768px) {
            display: none;
        }

        .black {
            display: block;
        }

        .white {
            display: none;
        }

        .prev-slide, .next-slide {
            border-radius: 100px;
            border: 1px solid #EBEBEB;
            width: 48px;
            height: 32px;
            display: flex;
            align-items: center;
            justify-content: center;

            a {
                width: 25px;
                height: 25px;
            }

            &:hover {
                background: #2B67F6;

                .black {
                    display: none;
                }
    
                .white {
                    display: block;
                } 
            }
        }

        .next-slide {
            right: 0;
        }

        .prev-slide {
            left: 0;
        }
    }

    .tabs {
        display: flex;
        gap: 8px;        
        margin-top: 40px;
        overflow-x: auto;
        scrollbar-color: transparent transparent;

        @media (max-width: 1024px) {
            padding-left: 12px;
            margin-top: 24px;
        }

        .tab {
            display: flex;
            padding: 8px 12px;
            align-items: center;
            gap: 10px;
            border-radius: 63px;
            background: #FFF;
            height: 29px;
            color: rgba(107, 111, 123, 0.97);
            font-size: 12px;
            font-style: normal;
            font-weight: 500;
            line-height: normal;
            text-transform: uppercase;
            white-space: nowrap;
            cursor: pointer;

            &:hover {
                background: #2B67F6;
                color: #fff;
            }

            &.active {
                background: #2B67F6;
                color: #fff;
            }
        }
    }

    .tab-pane {
        display: none;
    }
    
    .tab-pane.active {
        display: block;
        width: 100%;
        max-width: 1191px;
    }

    .tab-content {
        display: flex;
        justify-content: flex-end;
        overflow: hidden;
        margin-top: 40px;
        height: 435px;

        @media (max-width: 768px) {
            height: auto;
        }
    }
}

.swiper-button-disabled {
    opacity: 0.2;
    pointer-events: none;
}