.product-reservs {
  background-color: #fff;
  padding: 8px;
  width: 100%;
  z-index: 10;
  display: none;
  position: fixed;
  bottom: 0;
  box-shadow: 0px -10px 15px rgba(0, 0, 0, 0.1);

  @media screen and (min-width: 800px) {
    display: none;
  }

  &-wrapper {
    display: flex;
    justify-content: space-between;
    align-items: center;
  }

  &__select {
    display: flex;
    align-items: center;
    gap: 8px;

    & select {
      color: #000;
      font-size: 16px;
      font-weight: 500;
      line-height: 20px;
      text-transform: capitalize;
      appearance: none;
    }
  }

  &__socials {
    display: flex;
    align-items: center;
    gap: 12px;

    & p {
      color: #000;
      font-size: 16px;
      font-weight: 500;
      line-height: 20px;
      text-transform: capitalize;
    }
  }

  &__button {
    color: #fff;
    font-size: 18px;
    font-weight: 400;
    line-height: 20px;
    border-radius: 27px;
    background: #2b67f6;
    padding: 12px 40px;

    &:hover {
      background-color: #1753e2;
    }
  }
}
