.modal-badge {
  position: relative;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  gap: 16px;
  background-color: #ffffff;
  border-radius: 24px;
  padding: 40px;
  max-width: 800px;
  margin: 0 auto;

  &-backdrop {
    display: none;
    position: fixed;
    width: 100%;
    height: 100%;
    z-index: 9999;
    top: 0;
    left: 0;
    justify-content: center;
    align-items: center;
    background-color: rgba(0, 0, 0, 0.3);

    &.open {
      display: flex;
    }
  }

  &__close {
    position: absolute;
    top: 24px;
    right: 24px;
    width: 32px;
    height: 32px;
    background-color: #fff !important;
    cursor: pointer;
  }

  &__title {
    color: #333;
    text-align: center;
    font-size: 28px;
    font-weight: 600;
    line-height: normal;
    max-width: 510px;
  }

  &__container {
    display: flex;
    gap: 24px;
    border-radius: 10px;
    background: #fff8f1;
    padding: 16px;

    &-list {
      display: flex;
      flex-direction: column;
      gap: 16px;

      &-item {
        display: flex;
        align-items: center;
        gap: 6px;

        & img {
          width: 20px !important;
        }

        & p {
          color: #ff8a4c;
          font-size: 14px;
          font-weight: 500;
          line-height: 15.398px;
          text-transform: uppercase;
        }
      }
    }
  }
}
