.filters-menu {
  position: fixed;
  top: 0;
  left: 0;
  z-index: 100;
  width: 100%;
  height: 100%;
  background-color: #f2f2f2;
  transform: translateX(100%);
  transition: transform 0.3s;
  padding: 47px 10px;

  &-title {
    color: #333;
    text-align: center;
    font-family: Archivo;
    font-size: 24px;
    font-weight: 600;
    line-height: 40px;
    margin-bottom: 32px;
  }

  &__inner {
    border-radius: 24px;
    background: #fff;

    & > div > div .cruise__bottom-item {
      font-size: 14px;
    }

    & > div > div .cruise__bottom-button {
      font-size: 18px;
      margin-top: 40px;
    }

    & > div > div .cruise__top-link {
      color: #4d515d;
      font-size: 14px;
    }
  }

  &.is-open {
    transform: translateX(0);
  }

  &__btn-close {
    width: 48px;
    height: 32px;
    display: flex;
    align-items: center;
    justify-content: center;
    border: none;
    border-radius: 100px;
    background-color: #fff;
    padding: 12px;
    margin-bottom: 23px;
    cursor: pointer;

    & svg {
      color: #000000;
    }

    &:hover {
      background-color: #2b67f6;

      & svg {
        color: #ffffff;
      }
    }
  }
}
