.product-price {
  display: none;

  @media screen and (min-width: 768px) {
    display: block;
    padding-bottom: 32px;
  }

  &-wrapper {
    position: relative;
    margin-top: -100px;
  }

  &__top {
    display: flex;
    align-items: center;
    justify-content: space-between;
    padding: 24px 32px;
    border-radius: 16px 16px 0 0;
    background-color: #fff;

    &-select {
      display: flex;
      align-items: center;
      gap: 12px;
      max-width: 296px;
      width: 100%;
      background-image: url("../img/chevron-down.svg");
      background-position: right center;
      background-repeat: no-repeat;

      & p {
        color: #333;
        font-size: 22px;
        font-weight: 600;
        line-height: normal;
        appearance: none;
        width: 100%;
      }
    }

    &-buttons {
      display: flex;
      flex-direction: row;
      align-items: center;
      gap: 24px;

      &-heart {
        fill: transparent;

        &.open {
          fill: #2b67f6;
        }
      }

      & .tooltip {
        position: relative;
        display: inline-block;

        &:hover .tooltip-text {
          visibility: visible;
          opacity: 1 !important;
        }

        & .tooltip-text {
          position: absolute;
          z-index: 1;
          bottom: 140%;
          left: -52px;
          background-color: #e8f0ff;
          color: #000;
          text-align: center;
          font-size: 14px;
          font-weight: 400;
          line-height: 18px;
          visibility: hidden;
          width: 250px;
          border-radius: 8px;
          padding: 16px;
          margin-left: -60px;
          opacity: 0;
          transition: opacity 0.3s;
          text-transform: capitalize;

          & h3 {
            color: #000;
            font-size: 18px;
            font-weight: 600;
            line-height: 20px;
            margin-bottom: 16px;
          }

          & p {
            display: flex;
            gap: 8px;
            color: #000;
            font-size: 16px;
            font-weight: 400;
            line-height: 20px;
            text-transform: capitalize;

            &:not(:last-child) {
              margin-bottom: 12px;
            }
          }

          &::after {
            content: "";
            position: absolute;
            top: 100%;
            left: 49%;
            margin-left: -5px;
            border-width: 8px;
            border-style: solid;
            border-color: #e8f0ff transparent transparent transparent;
            content: " ";
          }
        }
      }
    }

    &-button {
      color: #fff;
      font-size: 18px;
      font-weight: 400;
      line-height: 20px;
      border-radius: 27px;
      background: #2b67f6;
      padding: 12px 56px;

      &:hover {
        background-color: #1753e2;
      }
    }
  }

  &__bottom {
    display: flex;
    flex-direction: column;
    padding: 32px;
    border-radius: 0 0 16px 16px;
    background-color: #fff;
    border-top: 1px solid #ebebeb;

    &-item-top {
      display: flex;
      justify-content: space-between;
      align-items: center;
      padding-bottom: 16px;
      gap: 20px;

      & h2 {
        color: #000;
        font-size: 18px;
        font-weight: 500;
        line-height: 20px;
      }

      &-list {
        display: flex;
        align-items: center;
        gap: 34px;

        @media screen and (min-width: 1100px) {
          gap: 96px;
        }

        & li {
          display: flex;
          align-items: center;
          gap: 6px;
          color: #000;
          font-size: 14px;
          font-weight: 500;
          line-height: 20px;
          text-transform: uppercase;
        }
      }
    }

    &-item {
      display: flex;
      justify-content: space-between;
      align-items: center;
      padding: 32px 0;
      border-top: 1px solid #ebebeb;
      gap: 20px;

      & h2 {
        color: #000;
        font-size: 16px;
        font-weight: 400;
        line-height: 20px;
        text-transform: capitalize;
      }

      &-list {
        display: flex;
        align-items: center;
        gap: 40px;

        @media screen and (min-width: 1100px) {
          gap: 100px;
        }

        & li {
          color: #2b67f6;
          font-size: 24px;
          font-weight: 500;
          line-height: 20px;
          text-transform: uppercase;
          position: relative;

          &:hover .tooltip-text {
            visibility: visible;
            opacity: 1 !important;
          }

          & .tooltip-text {
            position: absolute;
            z-index: 3;
            bottom: 150%;
            left: 42%;
            background-color: #e8f0ff;
            color: #000;
            text-align: center;
            font-size: 14px;
            font-weight: 400;
            line-height: 18px;
            visibility: hidden;
            width: 120px;
            border-radius: 8px;
            padding: 8px 12px;
            margin-left: -60px;
            opacity: 0;
            transition: opacity 0.3s;
            text-transform: capitalize;

            &::after {
              content: "";
              position: absolute;
              top: 100%;
              left: 50%;
              margin-left: -5px;
              border-width: 8px;
              border-style: solid;
              border-color: #e8f0ff transparent transparent transparent;
            }

            &.orange {
              display: flex;
              gap: 8px;
              left: -16px;
              align-items: center;
              color: #ff8a4c;
              font-size: 14px;
              font-weight: 700;
              line-height: 18px;
              width: 212px;

              & span {
                color: #2b67f6;
                font-size: 14px;
                font-weight: 500;
                line-height: 18px;
                text-decoration: line-through;
              }
            }
          }

          &.absent {
            text-decoration: line-through;
            color: rgba(43, 103, 246, 0.5);
          }
        }
      }

      & p {
        color: #000;
        font-size: 14px;
        font-weight: 400;
        line-height: normal;
      }
    }

    &-item:last-child {
      display: flex;
      justify-content: space-between;
      align-items: center;
      padding: 16px 0 0;
      border-top: 1px solid #ebebeb;
    }
  }

  &-mobile {
    display: block;
    padding-bottom: 16px;

    @media screen and (min-width: 768px) {
      display: none;
    }

    &-wrapper {
      position: relative;
      z-index: 1;
      margin-top: -100px;
    }

    &__top {
      padding: 16px;
      border-radius: 16px 16px 0px 0px;
      background: #fff;

      &-select {
        display: flex;
        align-items: center;
        gap: 8px;
        width: 100%;
        background-image: url("../img/chevron-down.svg");
        background-position: right center;
        background-repeat: no-repeat;

        & p {
          color: #333;
          font-size: 18px;
          font-weight: 600;
          line-height: normal;
          appearance: none;
          width: 100%;
        }
      }
    }

    &__bottom {
      display: flex;
      flex-direction: column;
      gap: 8px;
      padding: 16px;
      border-radius: 0px 0px 16px 16px;
      background: #fff;
      border-top: 1px solid #ebebeb;

      & h2 {
        color: #000;
        font-size: 16px;
        font-weight: 500;
        line-height: 20px;
      }

      &-list {
        & li {
          display: flex;
          justify-content: space-between;
          align-items: center;
          padding: 16px;
          border-top: 1px solid #ebebeb;

          &.active {
            background-image: url("../img/chevron-right.svg");
            background-position: right center;
            background-repeat: no-repeat;
          }

          &:first-child {
            border-top: none;
          }

          & div {
            display: flex;
            align-items: center;
            gap: 8px;
          }

          & p {
            color: #2b67f6;
            text-align: right;
            font-size: 18px;
            font-weight: 500;
            line-height: 20px;
            text-transform: uppercase;

            &.absent {
              font-size: 18px;
              text-decoration: line-through;
              color: rgba(43, 103, 246, 0.5);
            }
          }
        }
      }

      &-controls {
        display: flex;
        justify-content: center;
        align-items: center;
        gap: 8px;
        margin-bottom: 2px;

        & button {
          color: #4d515d;
          font-size: 12px;
          font-weight: 500;
          line-height: normal;
          text-transform: uppercase;
          border-radius: 63px;
          padding: 8px 12px;
          border: 1px solid #ebebeb;
          background-color: transparent;
          width: 100%;

          &:hover {
            background-color: #2b67f6;
            color: #fff;
          }

          &.active {
            background-color: #2b67f6;
            color: #fff;
          }
        }
      }

      &-text {
        color: #000;
        font-size: 12px;
        font-weight: 400;
        line-height: 16px;
      }
    }
  }
}
