.search {
  background-color: #00004c;
  padding: 100px 0;

  &-wrapper {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
  }

  &__img {
    margin-bottom: 40px;

    & svg {
      width: 150px;
      height: 150px;

      @media screen and (max-width: 1150px) {
        width: 100px;
        height: 100px;
      }
    }
  }

  &__title {
    color: #fff;
    font-size: 40px;
    font-weight: 400;
    line-height: 56px;
    margin-bottom: 32px;

    @media screen and (max-width: 1150px) {
      font-size: 24px;
      line-height: 32px;
      text-align: center;
      max-width: 274px;
    }
  }

  &__button {
    display: flex;
    justify-content: space-between;
    align-items: center;
    height: 50px;
    padding: 8px 12px 8px 24px;
    border-radius: 189px;
    background: #fff;
    max-width: 327px;
    width: 100%;
    text-align: left;
    color: #8e8e8e;
    font-family: Archivo;
    font-size: 18px;
    font-weight: 400;

    & img {
      width: 24px;
      height: 24px;
    }
  }
}
