html {
	scroll-behavior: smooth;
}

body {
	background-color: #f2f2f2;
    font-family: 'Archivo', sans-serif;
}

.title {
	color: #333;
	text-align: center;
	font-size: 40px;
	font-style: normal;
	font-weight: 600;
	line-height: normal;
	margin: 0 auto;
	width: 100%;
	max-width: 642px;

	@media (max-width: 768px) {
		padding: 0 12.5px;
		font-size: 24px;
		line-height: 30px
	}
}
