.reserv-datos-passengers {
  padding-bottom: 32px;

  &__top {
    padding: 24px;
    border-radius: 16px 16px 0px 0px;
    background-color: #fff;

    & h2 {
      color: #333;
      font-size: 22px;
      font-weight: 600;
      line-height: normal;
      display: flex;
      align-items: center;
      gap: 12px;
    }
  }

  &__bottom {
    border-top: 1px solid #ebebeb;
    padding: 32px;
    border-radius: 0px 0px 16px 16px;
    background-color: #fff;

    &-content {
      display: grid;
      grid-template-columns: 1fr 1fr;
      column-gap: 32px;
      row-gap: 32px;
      padding-bottom: 24px;

      &-block {
        display: flex;
        flex-direction: column;
        gap: 24px;
        padding: 24px;
        border-radius: 16px;
        background-color: #f9f9f9;

        &-item {
          display: grid;
          grid-template-columns: 1.4fr 1fr;

          & h3 {
            color: #2b67f6;
            font-size: 24px;
            font-weight: 500;
            line-height: 32px;
          }

          & span {
            color: #656565;
            font-size: 14px;
            font-weight: 500;
            line-height: 20px;
            text-transform: uppercase;
            margin-bottom: 4px;
          }

          &-text {
            color: #000;
            font-size: 20px;
            font-weight: 400;
            line-height: 32px;
          }

          &-age {
            color: #656565;
            font-size: 16px;
            font-weight: 500;
            line-height: 20px;
          }

          &-price {
            color: #333;
            font-size: 24px;
            font-weight: 600;
            line-height: 32px;
            text-align: end;
          }
        }
      }
    }

    &-result {
      display: flex;
      justify-content: space-between;
      padding-top: 24px;
      border-top: 1px solid #ebebeb;

      &-total {
        color: #333;
        font-size: 20px;
        font-weight: 400;
        line-height: normal;
      }

      &-right {
        text-align: end;

        &-price {
          color: #333;
          font-size: 28px;
          font-weight: 600;
          line-height: normal;
          margin-bottom: 12px;
        }

        & p {
          color: #656565;
          font-size: 14px;
          font-weight: 400;
          line-height: 18px;
          max-width: 470px;

          & span {
            font-weight: 700;
          }
        }
      }
    }
  }
}
