.product-route {
  padding-bottom: 16px;

  @media screen and (min-width: 768px) {
    padding-bottom: 32px;
  }

  &__slider {
    position: relative;
    overflow: hidden;

    .swiper-pagination-bullet {
      width: 10px;
      height: 10px;
      background-color: #fff;
      opacity: 1;
    }

    .swiper-pagination-bullet-active {
      background-color: #000;
      border: 2px solid #fff;
    }

    .product-description__slide {
      display: flex;
      justify-content: center;
    }
  }

  &__top {
    display: flex;
    flex-direction: column;
    align-items: left;
    padding: 16px;
    border-radius: 16px 16px 0 0;
    background-color: #fff;

    @media screen and (min-width: 1100px) {
      padding: 24px 32px;
      flex-direction: row;
      align-items: center;
      justify-content: space-between;
    }

    &-title {
      display: flex;
      align-items: center;
      gap: 8px;

      @media screen and (min-width: 1100px) {
        gap: 12px;
      }

      & h2 {
        color: #333;
        font-size: 22px;
        font-weight: 600;
        line-height: normal;
      }
    }

    &-text {
      display: flex;
      flex-direction: column;
      padding-left: 40px;

      @media screen and (min-width: 1100px) {
        align-items: flex-end;
        padding: 0;
      }

      & p {
        color: #656565;
        font-size: 14px;
        font-weight: 400;
        line-height: 18px;
      }

      & a {
        color: #2b67f6;
        font-size: 14px;
        font-weight: 400;
        line-height: 18px;
      }
    }
  }

  &__bottom {
    display: flex;
    flex-direction: column-reverse;
    align-items: center;
    gap: 40px;
    padding: 16px;
    border-radius: 0 0 16px 16px;
    background-color: #fff;
    border-top: 1px solid #ebebeb;

    @media screen and (min-width: 1100px) {
      flex-direction: row;
      padding: 32px;
    }

    &-left {
      display: flex;
      flex-direction: column;
      gap: 24px;
      max-width: 430px;
      width: 100%;
    }

    &-right {
      display: flex;
      flex-direction: column;
      align-items: center;
      padding: 12px;
      border-radius: 16px;
      background-color: #f9f9f9;
      width: 100%;
      overflow-y: hidden;
      height: 100%;
      max-height: 555px;

      @media screen and (min-width: 1100px) {
        overflow-y: auto;
        padding: 24px 16px;
      }

      &-list {
        position: relative;
        display: flex;
        flex-direction: column;
        gap: 20px;
        margin-left: 20px;

        @media screen and (min-width: 1100px) {
          gap: 24px;
          margin-left: 0;
        }

        & li {
          position: relative;
          display: flex;
          flex-direction: column;
          gap: 6px;

          &.orange {
            &::after {
              position: absolute;
              left: -20px;
              top: 30px;
              height: calc(100% + 24px);
              border-left: 2px dotted #ff8a4c;
              content: "";
            }
          }

          &.last {
            &::after {
              position: absolute;
              left: -20px;
              top: 30px;
              height: calc(100% + 24px);
              border-left: none;
              content: "";
            }
          }

          &::after {
            position: absolute;
            left: -20px;
            top: 30px;
            height: calc(100% + 24px);
            border-left: 2px dotted #2b67f6;
            content: "";
          }

          & p {
            color: #656565;
            font-size: 14px;
            font-weight: 400;
            line-height: normal;
          }

          & h3 {
            position: relative;
            color: #000;
            font-size: 14px;
            font-weight: 600;
            line-height: normal;

            @media screen and (min-width: 1100px) {
              font-size: 18px;
            }

            &.orange {
              &::before {
                position: absolute;
                width: 18px;
                height: 18px;
                background: #ff8a4c;
                border-radius: 50%;
                left: -28px;
                border: 5px solid #feecdc;
                z-index: 3;
                content: "";
              }
            }

            &::before {
              position: absolute;
              width: 18px;
              height: 18px;
              background: #2b67f6;
              border-radius: 50%;
              left: -28px;
              border: 5px solid #e1effe;
              z-index: 2;
              content: "";
            }
          }

          & span {
            color: #656565;
            font-size: 14px;
            font-weight: 400;
            line-height: 12px;

            @media screen and (min-width: 1100px) {
              font-size: 16px;
              line-height: normal;
            }
          }
        }
      }
    }

    &-button-mobile {
      color: #2b67f6;
      padding: 10px 40px;
      font-size: 16px;
      font-weight: 400;
      line-height: 20px;
      background-color: transparent;
      border: 1px solid #2b67f6;
      color: #2b67f6;
      border-radius: 27px;
      width: 100%;

      @media screen and (min-width: 1100px) {
        display: none;
      }

      &:hover {
        background-color: #2b67f6;
        color: #fff;
      }
    }
  }
}
