.highlights {
  background-color: #00004c;
  padding: 80px 0;

  @media screen and (max-width: 1150px) {
    padding: 40px 16px;
    overflow: hidden;
  }

  & .inner {
    width: 100%;
    max-width: 1191px;
    padding: 0;
    margin: 0 auto;
    position: relative;
  }

  &__title {
    color: #fff;
    text-align: center;
    font-size: 32px;
    font-weight: 600;
    line-height: normal;
    margin-bottom: 20px;

    @media screen and (max-width: 1150px) {
      font-size: 24px;
    }
  }

  &__item {
    display: flex;
    align-items: center;

    @media screen and (max-width: 1150px) {
      flex-direction: column;
    }

    &:not(:last-child) {
      margin-bottom: 38px;
    }

    &-img {
      @media screen and (max-width: 1150px) {
        max-width: 340px;
        height: 230px;
        margin-left: -95px;
      }
    }

    &.reverse {
      flex-direction: row-reverse;

      @media screen and (max-width: 1150px) {
        flex-direction: column;
      }

      & .highlights__item-content {
        margin-right: -40px;
        max-width: 491px;

        @media screen and (max-width: 1150px) {
          margin-right: 0;
          max-width: auto;
        }

        &-button {
          margin-top: 20px;

          @media screen and (max-width: 1150px) {
            margin-top: 0;
          }
        }
      }

      & .highlights__item-img {
        @media screen and (max-width: 1150px) {
          margin-left: 0;
          margin-right: -95px;
        }
      }
    }

    &-content {
      position: relative;
      z-index: 1;
      padding: 40px;
      border-radius: 16px;
      background-color: #fff;
      margin-left: -40px;

      @media screen and (max-width: 1150px) {
        padding: 16px;
        margin-left: 0;
        margin-top: -70px;
      }

      &-title {
        color: #333;
        font-size: 28px;
        font-weight: 600;
        line-height: normal;
        margin-bottom: 8px;

        @media screen and (max-width: 1150px) {
          font-size: 22px;
          margin-bottom: 4px;
        }
      }

      &-subtitle {
        color: var(--Black-color, #333);
        font-size: 18px;
        font-weight: 400;
        line-height: 24px;
        margin-bottom: 16px;

        @media screen and (max-width: 1150px) {
          font-size: 16px;
          line-height: 20px;
          margin-bottom: 14px;
        }
      }

      &-list {
        display: flex;
        flex-direction: column;
        gap: 4px;
        margin-bottom: 16px;
        padding-left: 24px;

        & p {
          position: relative;
          color: #656565;
          font-size: 16px;
          font-style: normal;
          font-weight: 400;
          line-height: 22px;

          @media screen and (max-width: 1150px) {
            font-size: 14px;
            line-height: 20px;
          }

          &::after {
            position: absolute;
            top: 8px;
            left: -16px;
            width: 5px;
            height: 5px;
            border-radius: 50%;
            background-color: #656565;
            content: "";
          }
        }
      }

      &-text {
        color: #656565;
        font-size: 16px;
        font-weight: 400;
        line-height: 20px;
        margin-bottom: 28px;

        @media screen and (max-width: 1150px) {
          font-size: 14px;
          margin-bottom: 16px;
        }
      }

      &-button {
        color: #fff;
        font-size: 18px;
        font-weight: 400;
        line-height: 20px;
        padding: 14px 48px;
        border-radius: 27px;
        background-color: #2b67f6;
        border: 1px solid #2b67f6;

        @media screen and (max-width: 1150px) {
          font-size: 16px;
          width: 100%;
        }

        &:hover {
          background-color: transparent;
          color: #2b67f6;
        }
      }
    }
  }
}
