.carts {
  padding: 72px 0 70px;
  border-bottom: 1px solid rgba(0, 0, 0, 0.1);

  @media (max-width: 768px) {
    padding: 31px 0 40px;
  }

  .inner {
    width: 100%;
    max-width: 1226px;
    padding: 0 16px;
    margin: 0 auto;

    @media (max-width: 768px) {
      padding: 0;
    }
  }

  &__items {
    display: flex;
    flex-wrap: wrap;
    justify-content: center;
    gap: 40px 18px;
    margin-top: 40px;

    @media (max-width: 768px) {
      flex-wrap: nowrap;
      overflow-x: auto;
      justify-content: flex-start;
      gap: 18px;
      scrollbar-color: transparent transparent;
      margin-top: 24px;
    }

    &:hover {
      a {
        text-decoration: none;
      }
    }
  }

  &__item {
    width: 100%;
    max-width: 285px;
    border-radius: 24px;
    height: 100%;
    min-height: 360px;
    position: relative;
    cursor: pointer;
    transition: all 0.2s ease-in;

    &:hover {
      .btn {
        display: flex;
        width: 249px;
        padding: 12px 40px;
        justify-content: center;
        align-items: center;
        gap: 10px;
        border-radius: 27px;
        border: 1px solid #fff;
        color: #fff;
        font-family: "Archivo", sans-serif;
        font-size: 16px;
        font-style: normal;
        font-weight: 400;
        line-height: 20px; /* 125% */
        margin: 0 auto;
        position: absolute;
        bottom: 17px;
      }

      .carts__item {
        &-wrapper {
          background-color: rgba(43, 103, 246, 0.7);
        }

        &-bottom {
          margin-bottom: 60px;
        }
      }
    }

    &-wrapper {
      position: relative;
      border-radius: 24px;
      background-color: rgba(0, 0, 0, 0.32);
      height: 100%;
      min-height: 360px;
      padding: 24px 24px 24px 17px;
      display: flex;
      flex-direction: column;
      justify-content: space-between;
      align-items: flex-start;
      transition: all 0.2s;

      @media (max-width: 768px) {
        width: 285px;
      }
    }

    img {
      position: absolute;
      object-fit: cover;
      top: 0;
      left: 0;
      width: 100%;
      height: 100%;
      object-position: center top;
      border-radius: 24px;
    }

    &-top {
      span {
        color: #fff;
        font-family: "Archivo", sans-serif;
        font-size: 16px;
        font-style: normal;
        font-weight: 400;
        line-height: normal;
      }

      h2 {
        color: #fff;
        font-family: "Archivo", sans-serif;
        font-size: 32px;
        font-style: normal;
        font-weight: 600;
        line-height: normal;
        margin-top: 6px;
      }
    }

    &-bottom {
      display: flex;
      flex-direction: column;
      align-items: flex-end;
      width: 100%;

      span {
        color: #fff;
        text-align: right;
        font-family: "Archivo", sans-serif;
        font-size: 10px;
        font-style: normal;
        font-weight: 500;
        line-height: 18px;

        &:last-of-type {
          line-height: 10px;
        }
      }

      p {
        color: #fff;
        font-family: "Archivo", sans-serif;
        font-size: 40px;
        font-style: normal;
        font-weight: 700;
        line-height: 38px;
        margin-bottom: 4px;
        text-align: right;
      }
    }

    .btn {
      display: none;
    }
  }

  &__button {
    display: flex;
    padding: 12px 24px;
    align-items: flex-start;
    gap: 8px;
    border-radius: 100px;
    background-color: #fff;
    margin: 40px auto 0;
    width: fit-content;
    transition: all 0.2s;

    @media (max-width: 768px) {
      display: none;
    }

    a {
      display: flex;
      height: 32px;
      padding: 12px 16px;
      justify-content: center;
      align-items: center;
      gap: 4px;
      border-radius: 100px;
      border: 1px solid #ebebeb;
      color: #4d515d;
      font-size: 14px;
      font-style: normal;
      font-weight: 400;
      line-height: 20px; /* 142.857% */
      position: relative;
      transition: all 0.2s;

      &:hover {
        background-color: #2b67f6;
        color: #fff;

        &::before {
          background: url("../img/plus-white.svg") no-repeat;
        }
      }

      &::before {
        content: "";
        width: 20px;
        height: 20px;
        background: url("../img/plus.svg") no-repeat;
      }
    }
  }
}
