.modal-cities {
  position: relative;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  gap: 24px;
  background-color: #ffffff;
  border-radius: 24px;
  padding: 40px;
  max-width: 880px;
  margin: 0 auto;

  &-backdrop {
    display: none;
    position: fixed;
    width: 100%;
    height: 100%;
    z-index: 9999;
    top: 0;
    left: 0;
    justify-content: center;
    align-items: center;
    background-color: rgba(0, 0, 0, 0.3);

    &.open {
      display: flex;
    }
  }

  &__close {
    position: absolute;
    top: 24px;
    right: 24px;
    width: 32px;
    height: 32px;
    background-color: #fff !important;
    cursor: pointer;
  }

  &__title {
    color: #333;
    text-align: center;
    font-size: 28px;
    font-weight: 600;
    line-height: normal;
  }

  &__bottom {
    display: flex;
    gap: 24px;
    width: 100%;

    &-left {
      max-width: 297px;
      width: 100%;
    }

    &-right {
      padding: 24px 16px;
      width: 100%;
      display: flex;
      justify-content: center;
      max-height: 393px;
      overflow-y: auto;
      background-color: #f9f9f9;

      &-list {
        position: relative;
        display: flex;
        flex-direction: column;
        gap: 20px;
        margin-left: 20px;

        @media screen and (min-width: 1100px) {
          gap: 24px;
          margin-left: 0;
        }

        & li {
          position: relative;
          display: flex;
          flex-direction: column;
          gap: 6px;

          &.orange {
            &::after {
              position: absolute;
              left: -20px;
              top: 30px;
              height: calc(100% + 24px);
              border-left: 2px dotted #ff8a4c;
              content: "";
            }
          }

          &.last {
            &::after {
              position: absolute;
              left: -20px;
              top: 30px;
              height: calc(100% + 24px);
              border-left: none;
              content: "";
            }
          }

          &::after {
            position: absolute;
            left: -20px;
            top: 30px;
            height: calc(100% + 24px);
            border-left: 2px dotted #2b67f6;
            content: "";
          }

          & p {
            color: #656565;
            font-size: 14px;
            font-weight: 400;
            line-height: normal;
          }

          & h3 {
            position: relative;
            color: #000;
            font-size: 14px;
            font-weight: 600;
            line-height: normal;

            @media screen and (min-width: 1100px) {
              font-size: 16px;
            }

            &.orange {
              &::before {
                position: absolute;
                width: 18px;
                height: 18px;
                background: #ff8a4c;
                border-radius: 50%;
                left: -28px;
                border: 5px solid #feecdc;
                z-index: 3;
                content: "";
              }
            }

            &::before {
              position: absolute;
              width: 18px;
              height: 18px;
              background: #2b67f6;
              border-radius: 50%;
              left: -28px;
              border: 5px solid #e1effe;
              z-index: 2;
              content: "";
            }
          }

          & span {
            color: #656565;
            font-size: 14px;
            font-weight: 400;
            line-height: 12px;

            @media screen and (min-width: 1100px) {
              font-size: 16px;
              line-height: normal;
            }
          }
        }
      }
    }
  }
}
