.mobile-menu {
  position: fixed;
  top: 0;
  left: 0;
  z-index: 2;
  width: 82%;
  height: 100%;
  background-color: #ffffff;
  transform: translateX(-100%);
  transition: transform 0.3s;
  overflow-y: auto;
  overflow-x: hidden;

  &__inner {
    display: flex;
    flex-direction: column;
    align-items: center;
    gap: 40px;
    padding: 16px;
    position: relative;

    &-list {
      display: flex;
      flex-direction: column;
      gap: 32px;
      width: 100%;

      & li {
        background-image: url("../img/chevron-right.svg");
        background-position: right center;
        background-repeat: no-repeat;
      }

      & li a {
        color: #2b67f6;
        font-family: Archivo;
        font-size: 18px;
        font-weight: 400;
        line-height: 24px;
        letter-spacing: 0.48px;
      }
    }

    &-icon {
      display: flex;
      flex-direction: column;
      gap: 24px;
      width: 100%;
      padding: 24px 16px;
      border-radius: 16px;
      background: #f8f8f8;

      & li a {
        display: flex;
        gap: 8px;
        color: #656565;
        font-family: Archivo;
        font-size: 13px;
        font-weight: 400;
        line-height: 12px;
        letter-spacing: 0.417px;
        text-transform: uppercase;
      }
    }

    &-selects {
      display: flex;
      gap: 32px;
    }

    li {
      .submenu {
        position: fixed;
        width: 100%;
        border-radius: 0px 0px 24px 24px;
        border-top: 4px solid #2b67f6;
        background: #fff;
        left: 0;
        top: 0;
        transform: translateX(-100%);
        transition: 0.2s linear;
        padding-bottom: 20px;

        &.is-open {
          transform: translateX(0);
        }

        &__col {
          padding-left: 40px;
          padding-right: 40px;
          border-right: 1px solid #00000023;

          & ul li {
            background-image: none;
          }

          &-title {
            color: #2b67f6;
            font-size: 16px;
            font-style: normal;
            font-weight: 700;
            line-height: 24px;
            letter-spacing: 0.417px;
            text-transform: uppercase;
            margin-bottom: 8px;
          }

          ul {
            flex-direction: column;
            gap: 0;
            align-items: flex-start;

            li {
              padding: 0;

              a {
                padding: 0;
                color: #000;
                font-size: 16px;
                font-style: normal;
                font-weight: 400;
                line-height: 32px;
                letter-spacing: 0.417px;
                text-align: left;

                &:hover {
                  text-decoration: underline;
                }
              }
            }
          }
        }
      }
    }
  }

  &.is-open {
    transform: translateX(0);
  }

  &__btn-close {
    width: 48px;
    height: 48px;
    display: flex;
    align-items: center;
    justify-content: center;
    border: none;
    border-radius: 100px;
    background-color: #fff;
    padding: 12px;
    cursor: pointer;
  }
}
