.checkout {
  position: relative;
  display: flex;
  justify-content: space-between;
  align-items: flex-start;
  padding-top: 30px;
  gap: 24px;

  @media screen and (max-width: 880px) {
    display: block;
    padding-top: 16px;
  }

  &__select {
    width: 100%;
    display: none;

    &.open {
      display: block;
    }

    &-top {
      display: flex;
      align-items: center;
      gap: 12px;
      padding: 24px;
      border-radius: 16px 16px 0px 0px;
      background-color: #fff;

      @media screen and (max-width: 880px) {
        gap: 8px;
        padding: 16px;
      }

      & h2 {
        color: #333;
        font-size: 22px;
        font-weight: 600;
        line-height: normal;

        @media screen and (max-width: 880px) {
          font-size: 18px;
        }
      }
    }

    &-bottom {
      display: flex;
      gap: 24px;
      align-items: center;
      justify-content: center;
      padding: 32px;
      border-radius: 0px 0px 16px 16px;
      background-color: #fff;
      border-top: 1px solid #ebebeb;

      @media screen and (max-width: 880px) {
        padding: 16px;
        flex-direction: column;
        gap: 12px;
      }

      &.open {
        border-radius: 0px;
      }

      &-item {
        position: relative;
        display: flex;
        flex-direction: column;
        justify-content: space-between;
        padding: 24px;
        border-radius: 12px;
        border: 1px solid #ebebeb;
        min-height: 180px;
        width: 100%;

        @media screen and (max-width: 880px) {
          padding: 16px;
          min-height: 100%;
          justify-content: normal;
          gap: 16px;
        }

        &::before {
          position: absolute;
          top: 12px;
          right: 12px;
          width: 16px;
          height: 16px;
          border-radius: 50%;
          border: 1px solid #d1d5db;
          background-color: #fff;
          content: "";
        }

        &.guaranteed.open {
          background-color: #2b67f6;

          &::before {
            position: absolute;
            top: 12px;
            right: 12px;
            width: 16px;
            height: 16px;
            border-radius: 50%;
            border: 4px solid #d1d5db;
            background-color: transparent;
            content: "";
          }

          & .checkout__select-bottom-item-title {
            display: flex;
            flex-direction: column;
            gap: 8px;

            & h3 {
              color: #fff;
              font-size: 18px;
              font-weight: 500;
              line-height: 20px;
            }

            & p {
              color: #fff;
              font-size: 14px;
              font-weight: 400;
              line-height: 18px;
            }
          }

          & .checkout__select-bottom-item-price {
            &-uptitle {
              color: #fff;
              font-size: 10px;
              font-weight: 400;
              line-height: 10px;
              text-transform: uppercase;
            }

            &-main {
              color: #fff;
              font-size: 24px;
              font-weight: 700;
              line-height: 24px;
            }
          }
        }

        &.camarote.open {
          background-color: #2b67f6;

          &::before {
            position: absolute;
            top: 12px;
            right: 12px;
            width: 16px;
            height: 16px;
            border-radius: 50%;
            border: 4px solid #d1d5db;
            background-color: transparent;
            content: "";
          }

          & .checkout__select-bottom-item-title {
            display: flex;
            flex-direction: column;
            gap: 8px;

            & h3 {
              color: #fff;
              font-size: 18px;
              font-weight: 500;
              line-height: 20px;
            }

            & p {
              color: #fff;
              font-size: 14px;
              font-weight: 400;
              line-height: 18px;
            }
          }

          & .checkout__select-bottom-item-price {
            &-uptitle {
              color: #fff;
              font-size: 10px;
              font-weight: 400;
              line-height: 10px;
              text-transform: uppercase;
            }

            &-main {
              color: #fff;
              font-size: 24px;
              font-weight: 700;
              line-height: 24px;
            }
          }
        }

        &-title {
          display: flex;
          flex-direction: column;
          gap: 8px;

          & h3 {
            color: #333;
            font-size: 18px;
            font-weight: 500;
            line-height: 20px;

            @media screen and (max-width: 880px) {
              font-size: 16px;
            }
          }

          & p {
            color: #656565;
            font-size: 14px;
            font-weight: 400;
            line-height: 18px;

            @media screen and (max-width: 880px) {
              font-size: 12px;
            }
          }
        }

        &-price {
          &-uptitle {
            color: #333;
            font-size: 10px;
            font-weight: 400;
            line-height: 10px;
            text-transform: uppercase;
          }

          &-main {
            color: #333;
            font-size: 24px;
            font-weight: 700;
            line-height: 24px;
          }
        }
      }
    }

    &-back {
      color: #1a56db;
      font-size: 14px;
      font-weight: 500;
      line-height: 150%;
      display: flex;
      align-items: center;
      gap: 8px;
      margin-top: 16px;

      &.hide {
        display: none;
      }
    }
  }

  &__flight {
    width: 100%;
    display: none;

    &.open {
      display: flex;
      flex-direction: column;
      align-items: flex-end;

      @media screen and (max-width: 880px) {
        align-items: normal;
      }
    }

    &-top {
      padding: 24px;
      border-radius: 16px 16px 0px 0px;
      background-color: #fff;
      width: 100%;

      @media screen and (max-width: 880px) {
        padding: 16px;
      }

      & h2 {
        display: flex;
        align-items: center;
        gap: 12px;
        color: #333;
        font-size: 22px;
        font-weight: 600;
        line-height: normal;

        @media screen and (max-width: 880px) {
          gap: 8px;
          font-size: 18px;
        }
      }
    }

    &-bottom {
      padding: 32px;
      border-radius: 0px 0px 16px 16px;
      background-color: #fff;
      margin-bottom: 24px;
      border-top: 1px solid #ebebeb;
      width: 100%;

      @media screen and (max-width: 880px) {
        padding: 16px;
      }

      &-item {
        display: flex;
        justify-content: space-between;
        align-items: center;
        border-top: 1px solid #ebebeb;
        padding: 16px 0;

        & h3 {
          color: #000;
          font-size: 16px;
          font-weight: 500;
          line-height: 20px;
        }

        &:first-child {
          border: none;
          padding-top: 0;
        }

        &:last-child {
          padding-bottom: 0;
        }

        &-content {
          display: flex;
          align-items: center;
          gap: 52px;

          @media screen and (max-width: 880px) {
            gap: 0;
          }

          & p {
            color: #333;
            font-size: 20px;
            font-weight: 700;
            line-height: 24px;
          }

          & button {
            position: relative;
            padding: 10px 32px;
            border-radius: 27px;
            border: 1px solid #2b67f6;
            background-color: transparent;

            @media screen and (max-width: 880px) {
              border: none;
              padding: 0;
            }

            & p {
              color: #2b67f6;
              font-size: 14px;
              font-weight: 400;
              line-height: 20px;

              @media screen and (max-width: 880px) {
                display: none;
              }
            }

            & svg {
              display: none;

              @media screen and (max-width: 880px) {
                display: block;
              }
            }

            @media screen and (min-width: 880px) {
              &:hover {
                background-color: #2b67f6;

                & p {
                  color: #fff;
                }
              }

              &.open {
                background-color: #2b67f6;

                &:hover {
                  background-color: #2b67f6;

                  & p {
                    color: #2b67f6;
                  }
                }

                &::after {
                  content: "";
                  position: absolute;
                  top: 11px;
                  left: 50%;
                  transform: translateX(-50%);
                  width: 6px;
                  height: 11px;
                  border: solid #fff;
                  border-width: 0 2px 2px 0;
                  transform: rotate(45deg);
                }
              }
            }
          }
        }
      }

      &-sub {
        display: none;
        grid-template-columns: repeat(3, 1fr);
        column-gap: 16px;
        padding-bottom: 16px;

        &.open {
          display: grid;
        }

        @media screen and (max-width: 880px) {
          padding: 16px;

          &.open {
            display: flex;
            flex-direction: column;
            gap: 12px;
          }
        }

        &-item {
          display: flex;
          flex-direction: column;
          align-items: flex-start;
          gap: 24px;
          padding: 24px;
          border-radius: 16px;
          background-color: #f9f9f9;

          @media screen and (max-width: 880px) {
            padding: 16px;
            gap: 16px;
          }

          & h3 {
            color: #2b67f6;
            font-size: 16px;
            font-weight: 600;
            line-height: normal;
            padding: 6px 12px;
            border-radius: 100px;
            background-color: #e8f0ff;

            @media screen and (max-width: 880px) {
              font-size: 14px;
            }
          }

          & label {
            color: #000;
            font-size: 16px;
            font-weight: 400;
            line-height: 20px;
            display: block;
            position: relative;
            padding-left: 30px;
            margin-bottom: 12px;
            cursor: pointer;
            font-size: 16px;

            & .checkmark {
              position: absolute;
              top: 0;
              left: 0;
              height: 20px;
              width: 20px;
              background-color: #eee;
              border-radius: 3px;

              &:after {
                content: "";
                position: absolute;
                display: none;
              }
            }

            & input {
              position: absolute;
              opacity: 0;
              cursor: pointer;
            }

            & input:checked ~ .checkmark {
              background-color: rgba(43, 103, 246, 0.4);
            }

            & input:checked ~ .checkmark:after {
              display: block;
            }

            & .checkmark:after {
              left: 7px;
              top: 3px;
              width: 6px;
              height: 12px;
              border: solid white;
              border-width: 0 2px 2px 0;
              transform: rotate(45deg);
            }

            & span {
              font-weight: 700;
            }
          }
        }
      }
    }

    &-next {
      display: none;
      color: #fff;
      font-size: 20px;
      font-weight: 400;
      line-height: 20px;
      padding: 16px 56px;
      border-radius: 27px;
      background-color: #2b67f6;
      border: 1px solid #2b67f6;
      margin-bottom: 30px;

      @media screen and (max-width: 880px) {
        font-size: 16px;
      }

      &:hover {
        background-color: transparent;
        color: #2b67f6;
      }

      &.open {
        display: block;
      }
    }
  }

  &__reservar {
    width: 100%;
    display: none;
    flex-direction: column;
    align-items: flex-end;

    @media screen and (max-width: 880px) {
      align-items: normal;
    }

    &.open {
      display: flex;
    }

    &-top {
      padding: 24px;
      border-radius: 16px 16px 0px 0px;
      background-color: #fff;
      width: 100%;

      @media screen and (max-width: 880px) {
        padding: 16px;
      }

      & h2 {
        display: flex;
        align-items: center;
        gap: 12px;
        color: #333;
        font-size: 22px;
        font-weight: 600;
        line-height: normal;

        @media screen and (max-width: 880px) {
          font-size: 18px;
          gap: 8px;
        }
      }
    }

    &-bottom {
      padding: 32px;
      border-radius: 0px 0px 16px 16px;
      background-color: #fff;
      border: 1px solid #ebebeb;
      width: 100%;

      @media screen and (max-width: 880px) {
        padding: 16px;
      }

      &-header {
        display: flex;
        justify-content: space-between;
        align-items: flex-start;
        border-radius: 8px;
        padding: 24px 16px;
        background-color: #e8f0ff;
        margin-bottom: 24px;

        @media screen and (max-width: 880px) {
          padding: 16px;
        }

        & .price {
          color: #333;
          font-size: 24px;
          font-weight: 600;
          line-height: 20px;

          @media screen and (max-width: 880px) {
            font-size: 20px;
          }
        }

        &-title {
          padding-left: 24px;

          & h3 {
            position: relative;
            color: #2b67f6;
            font-size: 18px;
            font-weight: 600;
            line-height: normal;

            @media screen and (max-width: 880px) {
              font-size: 16px;
            }

            &::after {
              position: absolute;
              top: 1px;
              left: -24px;
              width: 16px;
              height: 16px;
              border-radius: 100px;
              border: 4px solid #2b67f6;
              opacity: 0.5;
              background-color: #fff;
              content: "";
            }
          }

          & p {
            color: #000;
            font-size: 16px;
            font-weight: 500;
            line-height: 22px;

            @media screen and (max-width: 880px) {
              font-size: 14px;
            }
          }
        }
      }
      &-subtitle {
        color: #333;
        font-size: 18px;
        font-weight: 600;
        line-height: normal;
        margin-bottom: 16px;

        @media screen and (max-width: 880px) {
          font-size: 14px;
        }
      }

      &-item {
        padding: 16px;
        border-radius: 8px;
        background-color: #f9f9f9;
        margin-bottom: 16px;

        @media screen and (max-width: 880px) {
          margin-bottom: 12px;
        }

        &:last-child {
          margin-bottom: 0;
        }

        & label {
          position: relative;
          color: #000;
          font-size: 16px;
          font-weight: 500;
          line-height: 20px;
          padding-left: 24px;

          @media screen and (max-width: 880px) {
            font-size: 14px;
          }

          & input[type="radio"] {
            display: none;
          }

          &:hover .radio-checkmark {
            background-color: #ccc;
          }

          & input[type="radio"]:checked + .radio-checkmark {
            border: 4px solid #2b67f6;
          }
        }

        &-text {
          color: #656565;
          font-size: 16px;
          font-weight: 400;
          line-height: 22px;
          padding-left: 42px;

          @media screen and (max-width: 880px) {
            font-size: 14px;
          }

          & p {
            position: relative;

            &::after {
              position: absolute;
              top: 8px;
              left: -15px;
              width: 5px;
              height: 5px;
              border-radius: 50%;
              background-color: #656565;
              content: "";
            }
          }
        }
      }
    }

    &-next {
      color: #fff;
      font-size: 20px;
      font-weight: 400;
      line-height: 20px;
      padding: 16px 56px;
      border-radius: 27px;
      background-color: #2b67f6;
      border: 1px solid #2b67f6;
      margin-top: 46px;
      margin-bottom: 30px;

      @media screen and (max-width: 880px) {
        font-size: 16px;
        margin-top: 16px;
      }

      &:hover {
        background-color: transparent;
        color: #2b67f6;
      }
    }
  }

  &__data {
    display: none;
    flex-direction: column;
    align-items: flex-end;
    gap: 24px;
    width: 100%;

    @media screen and (max-width: 880px) {
      align-items: normal;
      gap: 16px;
    }

    &.open {
      display: flex;
    }

    &-client {
      width: 100%;

      &-top {
        width: 100%;
        padding: 24px;
        border-radius: 16px 16px 0px 0px;
        background-color: #fff;

        @media screen and (max-width: 880px) {
          padding: 16px;
        }

        & h2 {
          display: flex;
          align-items: center;
          gap: 12px;
          color: #333;
          font-size: 22px;
          font-weight: 600;
          line-height: normal;

          @media screen and (max-width: 880px) {
            font-size: 18px;
            gap: 8px;
          }
        }
      }

      &-bottom {
        display: grid;
        grid-template-columns: 1fr 1fr;
        column-gap: 32px;
        row-gap: 24px;
        width: 100%;
        padding: 32px;
        border-radius: 0px 0px 16px 16px;
        background-color: #fff;
        border-top: 1px solid #ebebeb;

        @media screen and (max-width: 880px) {
          padding: 16px;
          grid-template-columns: 1fr;
          row-gap: 16px;
        }

        &-item {
          display: flex;
          flex-direction: column;
          gap: 10px;

          @media screen and (max-width: 880px) {
            gap: 4px;
          }

          & h3 {
            color: #656565;
            font-size: 14px;
            font-weight: 500;
            line-height: 20px;
            text-transform: uppercase;

            @media screen and (max-width: 880px) {
              font-size: 12px;
            }
          }

          & input {
            height: 48px;
            border-radius: 8px;
            background-color: #f8f8f8;
            padding: 8px 12px;
            width: 100%;

            &::placeholder {
              color: #8d8d8d;
              font-size: 16px;
              font-weight: 400;
              line-height: normal;

              @media screen and (max-width: 880px) {
                font-size: 14px;
              }
            }
          }

          &-tel {
            display: flex;
            align-items: center;
            gap: 16px;

            @media screen and (max-width: 880px) {
              gap: 8px;
            }

            &-select {
              display: flex;
              align-items: center;
              justify-content: center;
              height: 48px;
              border-radius: 8px;
              background-color: #f8f8f8;
              padding: 8px 12px;
              max-width: 113px;
              width: 100%;
            }

            & select {
              color: #333;
              font-size: 16px;
              font-weight: 400;
              line-height: normal;
              width: 100%;
              height: 48px;
              border-radius: 8px;
              background-color: #f8f8f8;
              -webkit-appearance: none;
              -moz-appearance: none;
              appearance: none;
              background-image: url("../img/chevron-down.svg");
              background-position: right center;
              background-repeat: no-repeat;

              @media screen and (max-width: 880px) {
                font-size: 14px;
              }
            }
          }
        }
      }
    }

    &-passengers {
      width: 100%;

      &-top {
        padding: 24px;
        border-radius: 16px 16px 0px 0px;
        background-color: #fff;
        width: 100%;

        @media screen and (max-width: 880px) {
          padding: 16px;
        }

        & h2 {
          display: flex;
          align-items: center;
          gap: 12px;
          color: #333;
          font-size: 22px;
          font-weight: 600;
          line-height: normal;

          @media screen and (max-width: 880px) {
            gap: 8px;
            font-size: 18px;
          }
        }
      }

      &-control {
        display: none;
        justify-content: center;
        align-items: center;
        gap: 12px;
        background-color: #fff;

        @media screen and (max-width: 880px) {
          display: flex;
        }

        &-button {
          color: #2b67f6;
          font-size: 12px;
          font-weight: 500;
          line-height: normal;
          text-transform: uppercase;
          padding: 8px 12px;
          border-radius: 63px;
          background-color: transparent;
          border: 1px solid #2b67f6;

          &.open {
            background-color: #2b67f6;
            color: #fff;
          }
        }
      }

      &-bottom {
        display: flex;
        flex-direction: column;
        gap: 40px;
        padding: 32px;
        border-radius: 0px 0px 16px 16px;
        background-color: #fff;
        border-top: 1px solid #ebebeb;

        @media screen and (max-width: 880px) {
          padding: 16px;
        }

        &-form {
          display: none;
          flex-direction: column;
          align-items: flex-start;
          gap: 24px;

          @media screen and (min-width: 880px) {
            display: flex !important;
          }

          &.open {
            display: flex;
          }

          &-title {
            color: #2b67f6;
            font-size: 16px;
            font-weight: 600;
            line-height: normal;
            padding: 6px 12px;
            border-radius: 100px;
            background-color: #e8f0ff;

            @media screen and (max-width: 880px) {
              display: none;
            }
          }

          &-list {
            display: grid;
            grid-template-columns: 1fr 1fr;
            column-gap: 32px;
            row-gap: 24px;
            width: 100%;

            @media screen and (max-width: 880px) {
              grid-template-columns: 1fr;
              row-gap: 16px;
            }

            &-item {
              display: flex;
              flex-direction: column;
              align-items: flex-start;
              gap: 10px;

              @media screen and (max-width: 880px) {
                gap: 4px;
              }

              & h3 {
                color: #656565;
                font-size: 14px;
                font-weight: 500;
                line-height: 20px;
                text-transform: uppercase;

                @media screen and (max-width: 880px) {
                  font-size: 12px;
                }
              }

              & input {
                height: 48px;
                padding: 8px 12px;
                border-radius: 8px;
                background-color: #f8f8f8;
                width: 100%;

                &::placeholder {
                  color: #8d8d8d;
                  font-size: 16px;
                  font-weight: 400;
                  line-height: normal;

                  @media screen and (max-width: 880px) {
                    font-size: 14px;
                  }
                }
              }

              &-select {
                display: flex;
                align-items: center;
                justify-content: center;
                height: 48px;
                border-radius: 8px;
                background-color: #f8f8f8;
                padding: 8px 12px;
                width: 100%;

                & select {
                  color: #333;
                  font-size: 16px;
                  font-weight: 400;
                  line-height: normal;
                  width: 100%;
                  height: 48px;
                  border-radius: 8px;
                  background-color: #f8f8f8;
                  -webkit-appearance: none;
                  -moz-appearance: none;
                  appearance: none;
                  background-image: url("../img/chevron-down.svg");
                  background-position: right center;
                  background-repeat: no-repeat;
                }
              }

              &-date {
                display: flex;
                align-items: center;
                justify-content: center;
                height: 48px;
                border-radius: 8px;
                background-color: #f8f8f8;
                padding: 8px 12px;
                width: 100%;

                & select {
                  color: #333;
                  font-size: 16px;
                  font-weight: 400;
                  line-height: normal;
                  width: 100%;
