.offers {
    padding: 70px 0;
    border-bottom: 1px solid rgba(0, 0, 0, 0.1);

    @media (max-width: 768px) {
        padding: 40px 0;
    }

    .inner {
        width: 100%;
        max-width: 1224px;
        padding: 0 16px;
        margin: 0 auto;
    
        @media (max-width: 1100px) {
            padding: 0 13px;
        }
    }

    &__calendar {
        position: relative;
    }

    &__items {
        margin-top: 40px;
        display: flex;
        flex-direction: column;
        gap: 16px;

        @media (max-width: 768px) {
            margin-top: 24px;
            gap: 8px;
        }
    }

    &__item {
        display: flex;
        gap: 16px;
        height: 100%;
        max-height: 141px;

        @media (max-width: 1100px) {
            max-height: 100%;
        }

        @media (max-width: 768px) {
            gap: 8px;
        }

        &-image {
            position: relative;

            @media (max-width: 1100px) {
                width: 100%;
                max-width: 30%;  
            }

            @media (max-width: 568px) {
                max-width: 104px;
                display: flex;
                flex-direction: column;
                justify-content: space-between;
            }

            .image-static {
                height: 100%;
                min-height: 141px;
                object-fit: cover;
                border-radius: 16px;

                @media (max-width: 1100px) {
                    width: 100%;  
                }

                @media (max-width: 768px) {
                    height: auto; 
                    min-height: auto;
                }

                @media (max-width: 568px) {
                    min-height: 91px;
                }

                @media (max-width: 400px) {
                    min-height: 81px;
                }
            }

            p {
                background: #fff;
                padding: 5px 7px;
                position: absolute;
                top: 6px;
                left: 6px;
                border-radius: 16px;

                @media (max-width: 768px) {
                    position: relative;
                    top: auto; 
                    left: auto;
                    text-align: center;
                    margin-top: 8px;
                    min-height: 29px;
                    border-radius: 10px;
                }
            }

            .image-hover {
                display: none;

                @media (max-width: 568px) {
                    min-height: 91px;
                }

                @media (max-width: 400px) {
                    min-height: 81px;
                }
            }

            &:hover {
                .image-hover {
                    display: block;
                }

                .image-static, .image-before {
                    display: none;
                }

                @media (max-width: 768px) {
                    .image-before {
                        display: block;
                        margin: 0 auto;
                    }
                }
            }
        }

        &-text {
            border-radius: 16px;
            background: #FFF;
            display: flex;
            padding: 24px;
            align-items: flex-start;
            justify-content: space-between;
            width: 100%;
            max-width: 963px;
            transition: all 0.2s;

            @media (max-width: 1100px) {
                max-width: 80%;
                padding: 15px;
                align-items: center;
            }

            @media (max-width: 768px) {
                max-width: 100%;
                flex-direction: column;
                align-items: flex-start;
            }

            @media (max-width: 400px) {
                max-width: 238px;
                padding: 8px;
                align-items: flex-start;
            }
        }

        &-left {
            h3 {
                color: #333;
                font-size: 24px;
                font-style: normal;
                font-weight: 600;
                line-height: normal;

                @media (max-width: 568px) {
                    font-size: 14px;
                }
            }

            .hide-places {
                display: none;
            }

            h4 {
                display: none;

                @media (max-width: 768px) {
                    display: block;
                    color: var(--Black-color, #333);
                    font-size: 12px;
                    font-style: normal;
                    font-weight: 400;
                    line-height: 16px; /* 133.333% */
                    margin-top: 2px;

                    strong {
                        font-weight: 600;
                    }
                }
            }

            p {
                color: #2B67F6;
                font-size: 16px;
                font-style: normal;
                font-weight: 400;
                line-height: 20px; /* 125% */
                margin-top: 8px;
                position: relative;
                cursor: pointer;

                span {
                    display: none;

                    @media (max-width: 768px) {
                        display: inline-block;
                        font-weight: 600;
                    }
                }

                @media (max-width: 568px) {
                    font-size: 12px;
                    margin-top: 0;
                }

                &:hover .hide-places {
                    position: absolute;
                    bottom: 22px;
                    left: -77px;
                    display: flex;
                    padding: 8px 12px;
                    align-items: center;
                    border-radius: 8px;
                    background: #E3EBFF;
                    color: var(--black, #000);
                    font-size: 13px;
                    font-style: normal;
                    font-weight: 400;
                    line-height: 18px; /* 138.462% */
                    width: 302px;

                    @media (max-width: 768px) {
                        bottom: 21px;
                        left: 0;
                        padding: 5px;
                        width: 224px;
                        font-size: 11px;
                        line-height: 14px;
                        text-align: center;
                    }

                    &::before {
                        position: absolute;
                        content: '';
                        bottom: -8px;
                        left: 50%;
                        transform: translateX(-50%);
                        width: 34px;
                        height: 8px;
                        background: url('../img/vector.png') no-repeat;
                    }
                }
            }

            &-bottom {
                display: flex;
                gap: 24px;
                margin-top: 18px;

                @media (max-width: 1100px) {
                    gap: 10px;
                    flex-direction: column;
                }

                @media (max-width: 768px) {
                    display: none;
                }

                .calendar {
                    display: none;
                    position: absolute;
                    right: -88%;
                    border-radius: 8px;
                    background: #E3EBFF;
                    box-shadow: 0px 2px 4px -2px rgba(0, 0, 0, 0.05), 0px 4px 6px -1px rgba(0, 0, 0, 0.10);
                    width: 286px;
                    z-index: 2;
                    max-height: 134px;
                    overflow-y: auto;
            
                    &_data {
                        padding: 0;
                        color: var(--Black-color, #333) !important;
                        font-size: 16px;
                        font-style: normal;
                        font-weight: 400;
                        line-height: 20px; /* 125% */
                    }
            
                    &_price {
                        color: var(--Black-color, #333) !important;
                        font-size: 18px;
                        font-style: normal;
                        font-weight: 700;
                        line-height: 20px; /* 111.111% */
                    }
            
                    &_item {
                        cursor: pointer;
                        padding: 12px 16px;
                        display: flex;
                        align-items: flex-end;
                        justify-content: space-between;
                        gap: 12px; 
                        border-bottom: 1px solid #D8E3FF;
                        height: 44px;
            
                        &:hover {
                            background: #d6dff7;
                        }
            
                        &:last-of-type {
                            border-bottom: 0;

                            &:hover {
                                border-radius: 0 0 8px 8px;
                            }
                        }

                        &:first-of-type {
                            &:hover {
                                border-radius: 8px 8px 0 0;
                            }
                        }
                    }
                }

                p {
                    color: var(--Black-color, #333);
                    font-size: 18px;
                    font-style: normal;
                    font-weight: 400;
                    line-height: 20px; /* 111.111% */
                    position: relative;
                    padding-left: 28px;

                    @media (max-width: 1100px) {
                        font-size: 15px; 
                    }

                    &::before {
                        position: absolute;
                        content: '';
                        width: 22px;
                        height: 22px;
                        background-size: 100%;
                        background-repeat: no-repeat;
                        top: 0;
                        left: 0;
                    }
                }

                .ship {
                    &::before {
                        background: url('../img/ship.png') no-repeat;
                    }
                }

                .data {
                    padding-right: 24px;
                    cursor: pointer;
                    width: fit-content;

                    &::before {
                        background: url('../img/calendar.png') no-repeat;
                    }

                    &::after {
                        position: absolute;
                        content: '';
                        width: 20px;
                        height: 20px;
                        background-size: 100%;
                        background-repeat: no-repeat;
                        top: -2px;
                        right: 0;
                        background: url('../img/down.png') no-repeat;
                    }

                    &.visible + .calendar {
                        display: flex !important;
                        flex-direction: column;
                    }
                }

                .data.visible::after {
                    background: url('../img/up.png') no-repeat;
                }

                .time {
                    &::before {
                        background: url('../img/clock.png') no-repeat;
                    }
                }
            }
        }

        &-right {
            display: flex;
            flex-direction: column;
            align-items: flex-end;

            @media (max-width: 768px) {
                flex-direction: row;
                align-items: flex-end;
                justify-content: space-between;
                width: 100%;
            }

            &-top {
                display: flex;
                gap: 9px;
                align-items: center;

                @media (max-width: 768px) {
                    align-items: flex-start;
                    flex-direction: column-reverse;
                }
            }

            &-images {
                display: flex;
                gap: 12px;

                @media (max-width: 768px) {
                    flex-direction: row-reverse;
                    gap: 8px;
                }

                .image-static {
                    display: block;
                }

                .image-hover {
                    display: none;
                }
            }

            &-text {
                display: flex;
                padding: 3.08px 7.699px 3.08px 3.08px;
                justify-content: center;
                align-items: center;
                gap: 7.699px;
                position: relative;
                border-radius: 37.724px;
                background: var(--orange-50, #FFF8F1);
                position: relative;

                @media (max-width: 768px) {
                    background: transparent;
                    padding: 0;
                    border-radius: 0;
                    gap: 0;
                }

                span {
                    display: none;
                }

                &:hover span {
                    position: absolute;
                    bottom: 28px;
                    right: 5%;
                    display: flex;
                    padding: 8px 12px;
                    align-items: center;
                    border-radius: 8px;
                    background: #E3EBFF;
                    color: var(--black, #000);
                    font-size: 13px;
                    font-style: normal;
                    font-weight: 400;
                    line-height: 18px; /* 138.462% */
                    width: 302px;

                    @media (max-width: 768px) {
                        right: auto;
                        padding: 5px;
                        width: 224px;
                        font-size: 11px;
                        line-height: 14px;
                        text-align: center;
                    }

                    &::before {
                        position: absolute;
                        content: '';
                        bottom: -8px;
                        left: 50%;
                        transform: translateX(-50%);
                        width: 34px;
                        height: 8px;
                        background: url('../img/vector.png') no-repeat;
                    }
                }

                img {
                    display: block;

                    @media (max-width: 768px) {
                        display: none;
                    }
                }

                .promo {
                    color: var(--orange-400, #FF8A4C);
                    font-size: 11px;
                    font-style: normal;
                    font-weight: 600;
                    line-height: 15.398px; /* 139.978% */
                    text-transform: uppercase;
                    cursor: pointer;

                    @media (max-width: 768px) {
                        font-size: 9px;
                        line-height: 15.398px;
                    }
                }
            }

            &-bottom {
                display: flex;
                gap: 32px;
                align-items: flex-end;
                margin-top: 20px;

                @media (max-width: 1100px) {
                    gap: 10px;
                    margin-top: 0;
                }

                div {
                    display: flex;
                    flex-direction: column;
                    position: relative;

                    span {
                        color: #333;
                        text-align: right;
                        font-size: 10px;
                        font-style: normal;
                        font-weight: 500;
                        line-height: 18px;

                        @media (max-width: 568px) {
                            font-size: 8px;
                            padding-right: 24px;
                        }
                    }

                    .price {
                        color: #333;
                        font-size: 40px;
                        font-style: normal;
                        font-weight: 700;
                        line-height: 37px;
                        position: relative;

                        &.visible + .calendar {
                            display: flex !important;
                        }
                        

                        @media (max-width: 768px) {
                            padding-right: 24px;
                            cursor: pointer;
                            width: fit-content;

                            &::after {
                                position: absolute;
                                content: '';
                                width: 17px;
                                height: 16px;
                                background-size: 100%;
                                background-repeat: no-repeat;
                                top: 9px;
                                right: 0;
                                background: url('../img/chevron-down.png') no-repeat;
                            }
    
                            &.visible::after {
                                background: url('../img/chevron-up.png') no-repeat;
                            }
                        }

                        @media (max-width: 568px) {
                            font-size: 24px;
                            line-height: 22px;

                            &::after {
                                top: 2px;
                            }
                        }
                    }

                    .calendar {
                        display: none;
                        position: absolute;
                        right: 0;
                        top: 50px;
                        border-radius: 8px;
                        background: #E3EBFF;
                        box-shadow: 0px 2px 4px -2px rgba(0, 0, 0, 0.05), 0px 4px 6px -1px rgba(0, 0, 0, 0.10);
                        width: 286px;
                        z-index: 2;
                        max-height: 134px;
                        overflow-y: auto;
                
                        &_data {
                            padding: 0;
                            color: var(--Black-color, #333) !important;
                            font-size: 16px;
                            font-style: normal;
                            font-weight: 400;
                            line-height: 20px; /* 125% */
                        }
                
                        &_price {
                            color: var(--Black-color, #333) !important;
                            font-size: 18px;
                            font-style: normal;
                            font-weight: 700;
                            line-height: 20px; /* 111.111% */
                        }
                
                        &_item {
                            cursor: pointer;
                            padding: 12px 16px;
                            display: flex;
                            align-items: center;
                            justify-content: space-between;
                            gap: 12px; 
                            border-bottom: 1px solid #D8E3FF;
                            flex-direction: row;
                            height: 44px;
                
                            &:hover {
                                background: #adb9d9;
                            }
                
                            &:last-of-type {
                                border-bottom: 0;

                                &:hover {
                                    border-radius: 0 0 8px 8px;
                                }
                            }

                            &:first-of-type {
                                &:hover {
                                    border-radius: 8px 8px 0 0;
                                }
                            }
                        }
                    }
                }

                a {
                    display: flex;
                    padding: 12px 40px;
                    justify-content: center;
                    align-items: center;
                    gap: 10px;
                    border-radius: 27px;
                    background: #2B67F6;
                    color: #FFF;
                    font-size: 18px;
                    font-style: normal;
                    font-weight: 400;
                    line-height: 20px; /* 111.111% */
                    height: 44px;
                    width: 100%;
                    min-width: 145px;
                    border: 1px solid #2B67F6;

                    @media (max-width: 1100px) {
                        padding: 12px;  
                    }

                    @media (max-width: 768px) {
                        display: none; 
                    }
                }
            }
        }
    }

    .hovered {
        background: #2B67F6;

        .offers {
            &__item {
                &-left {
                    h3, span, p, h4 {
                        color: #fff;
                    }

                    &-bottom {
                        p {
                            color: #fff;
                        }

                        .ship {
                            &::before {
                                background: url('../img/ship-white.png') no-repeat;
                            }
                        }

                        .time {
                            &::before {
                                background: url('../img/clock-white.png') no-repeat;
                            }
                        }

                        .data {
                            &::before {
                                background: url('../img/calendar-white.png') no-repeat;
                            }
        
                            &::after {
                                background: url('../img/down-white.png') no-repeat;
                            }
                        }
        
                        .data.visible::after {
                            background: url('../img/up-white.png') no-repeat;
                        }
                    }
                }

                &-right {
                    &-images {
                        .image-static {
                            display: none;
                        }
        
                        .image-hover {
                            display: block;
                        }
                    }

                    &-bottom {
                        span, .price {
                            color: #fff;
                        }

                        a {
                            border: 1px solid #FFF;
                        }

                        .price {
        
                            &::after {
                                background: url('../img/chevron-down.png') no-repeat;
                            }
                        }
        
                        .price.visible::after {
                            background: url('../img/chevron-up.png') no-repeat;
                        }
                    }
                }
            }
        }
    }

    .pagination {
        margin: 40px auto 0;
        padding: 12px 24px;
        border-radius: 100px;
        background: #FFF;
        display: flex;
        align-items: flex-start;
        gap: 8px;
        width: 100%;
        max-width: 352px;

        @media (max-width: 768px) {
            margin: 24px auto 0;
            padding: 6.667px 13.333px;
            border-radius: 83.333px;
            gap: 6.667px;
            max-width: fit-content;
            justify-content: center;
        }

        li {
            display: flex;
            height: 32px;
            padding: 12px;
            flex-direction: column;
            justify-content: center;
            align-items: center;
            cursor: pointer;
            gap: 10px;
            border-radius: 100px;
            border: 1px solid #EBEBEB;
            color: #4D515D;
            font-size: 14px;
            font-style: normal;
            font-weight: 400;
            line-height: 20px;
            background-color: #fff;
            transition: all 0.2s;

            &:hover {
                background-color: #007bff;
                color: #fff;

                a {
                    color: #fff;
                }
            }

            @media (max-width: 768px) {
                width: 27px;
                height: 27px;
            }

            a {
                color: #4D515D;
            }

            &:first-of-type, &:last-of-type {
                width: 100%;
                max-width: 48px;

                @media (max-width: 768px) {
                    max-width: 20px;
                }

                .black {
                    display: block;
                }

                .white {
                    display: none;
                }

                &:hover {
                    .black {
                        display: none;
                    }
    
                    .white {
                        display: block;
                    } 
                }

                a {
                    width: 24px;
                    height: 24px;
                }
            }

            &.active {
                background-color: #007bff;
                
                a {
                    color: #fff;
                }
            }

            &.disabled {
                color: #ccc;
            }
        }
    }

    .calendar::-webkit-scrollbar {
        width: 2.3px;
    }

    .calendar::-webkit-scrollbar-thumb {
        background: #2B67F6;
    }

    .calendar::-webkit-scrollbar-track {
        background: #f1f1f1;
    }

    .calendar::-webkit-scrollbar-button {
        display: none;
    }
}