.categorie-camarote {
  background-color: #fff;
  padding: 0 32px 32px;
  display: none;
  margin-bottom: 30px;

  &.open {
    display: block;
  }

  &__top {
    display: flex;
    flex-direction: column;
    gap: 24px;

    &-title {
      display: flex;
      flex-direction: column;
      gap: 8px;

      & h2 {
        color: #333;
        font-size: 18px;
        font-weight: 600;
        line-height: normal;
      }

      & p {
        color: #333;
        font-size: 14px;
        font-weight: 400;
        line-height: 18px;
      }
    }

    &-ship {
      position: relative;
      display: flex;
      justify-content: space-between;
      height: 340px;
      margin-bottom: 32px;

      @media screen and (min-width: 880px) {
        background-image: url("../img/ship.svg");
        background-repeat: no-repeat;
        background-position: center;
        background-size: 80%;
      }

      @media screen and (max-width: 880px) {
        overflow: auto;

        &::after {
          position: absolute;
          top: 80px;
          left: 50%;
          transform: translateX(-50%);
          background-image: url(../img/ship.svg);
          background-repeat: no-repeat;
          width: 685px;
          height: 142px;
          content: "";
        }
      }

      @media screen and (max-width: 530px) {
        overflow: auto;

        &::after {
          transform: translateX(-34%);
        }
      }

      &-item {
        display: flex;
        flex-direction: column;
        border: 1px solid #ddd;
        width: 100%;
        align-items: center;
        justify-content: space-between;
        padding: 30px 0;
        min-width: 123px;

        @media screen and (max-width: 880px) {
          padding: 20px 12px;
        }

        &:hover {
          background-color: #f2f7ff;
        }

        & h3 {
          color: #656565;
          font-size: 14px;
          font-weight: 500;
          line-height: 20px;
          text-transform: uppercase;

          @media screen and (max-width: 880px) {
            font-size: 12px;
          }
        }

        &-price {
          display: flex;
          flex-direction: column;
          align-items: center;
          gap: 13px;

          & span {
            color: #333;
            font-size: 14px;
            font-weight: 500;
            line-height: 37px;
            margin-bottom: 50px;

            @media screen and (max-width: 880px) {
              font-size: 10px;
            }
          }

          & p {
            color: #333;
            font-size: 20px;
            font-weight: 700;
            line-height: 37px;

            @media screen and (max-width: 880px) {
              font-size: 14px;
            }
          }
        }
      }
    }
  }

  &__bottom {
    display: none;

    &.open {
      display: block;
    }

    &-title-main {
      padding-bottom: 16px;
      border-bottom: 1px solid #ebebeb;

      & h2 {
        color: #333;
        font-size: 18px;
        font-weight: 600;
        line-height: normal;
      }
    }

    &-item {
      display: flex;
      justify-content: space-between;
      align-items: center;
      padding: 16px 0;
      border-bottom: 1px solid #ebebeb;

      & h2 {
        color: #000;
        font-size: 18px;
        font-weight: 400;
        line-height: 20px;
        text-transform: capitalize;
      }

      &-price {
        display: flex;
        align-items: center;
        gap: 40px;

        @media screen and (max-width: 880px) {
          & > div {
            background-image: url("../img/chevron-down.svg");
            background-repeat: no-repeat;
            background-position: right center;
            padding-right: 25px;

            &.open {
              background-image: url("../img/chevron-up.svg");
            }
          }

          & .uptitle {
            text-align: right;
            color: #000;
            font-size: 12px;
            font-weight: 400;
            line-height: 12px;
          }

          & .main {
            color: #333;
            font-size: 20px;
            font-weight: 700;
            line-height: 24px;
          }
        }

        & button {
          display: block;
          padding: 10px 32px;

          @media screen and (max-width: 880px) {
            display: none;
          }
        }
      }
    }
  }

  &__sub {
    display: none;
    padding: 16px;
    border-radius: 8px;
    background-color: #f9f9f9;

    &.open {
      display: block;
    }

    &-top {
      display: flex;
      align-items: center;
      justify-content: space-between;
      padding-bottom: 16px;

      & h2 {
        color: #333;
        font-size: 16px;
        font-weight: 600;
        line-height: normal;

        @media screen and (max-width: 880px) {
          font-size: 14px;
        }
      }

      &-plan {
        color: #2b67f6;
        font-size: 16px;
        font-weight: 500;
        line-height: normal;
        display: flex;
        align-items: center;
        gap: 8px;

        & p {
          @media screen and (max-width: 880px) {
            display: none;
          }
        }
      }
    }

    &-bottom {
      &-item {
        display: flex;
        align-items: center;
        justify-content: space-between;
        padding: 16px 0;
        border-top: 1px solid #ebebeb;

        @media screen and (max-width: 880px) {
          flex-direction: column;
          align-items: normal;
        }

        &-img {
          @media screen and (max-width: 880px) {
            display: flex;
            width: 100%;
            justify-content: space-between;
            gap: 8px;
            margin-bottom: 8px;
          }

          &-wrapper {
            display: flex;
            gap: 8px;
          }

          &-content {
            color: #fff;
            font-size: 14px;
            font-weight: 500;
            line-height: 14px;
            text-transform: capitalize;
            padding: 4px;
            border-radius: 4px;
            background-color: #ff8a4c;

            @media screen and (max-width: 880px) {
              font-size: 12px;
            }
          }

          &-price {
            display: none;

            @media screen and (max-width: 880px) {
              display: flex;
              align-items: center;
              gap: 8px;
              color: #333;
              font-size: 20px;
              font-weight: 700;
              line-height: 24px;
            }
          }
        }

        &-type {
          color: #000;
          font-size: 14px;
          font-weight: 400;
          line-height: 18px;
          max-width: 130px;

          @media screen and (max-width: 880px) {
            max-width: 100%;
          }
        }

        &-oferta {
          color: #2b67f6;
          font-size: 14px;
          font-weight: 400;
          line-height: 18px;
          max-width: 110px;

          @media screen and (max-width: 880px) {
            max-width: 100%;
          }
        }

        &-price {
          display: block;
          color: #333;
          font-size: 20px;
          font-weight: 700;
          line-height: 24px;

          @media screen and (max-width: 880px) {
            display: none;
          }
        }

        &-button {
          display: block;

          &:hover {
            color: #fff;
            &::after {
              display: none;
              content: "";
            }
          }
          @media screen and (max-width: 880px) {
            display: none;
          }
        }
      }
    }
  }

  & button {
    position: relative;
    color: #2b67f6;
    font-size: 14px;
    font-weight: 400;
    line-height: 20px;
    background-color: transparent;
    padding: 8px 24px;
    border-radius: 27px;
    border: 1px solid #2b67f6;

    @media screen and (max-width: 880px) {
      font-size: 12px;
      padding: 6px 16px;
    }

    &:hover {
      background-color: #2b67f6;

      &::after {
        content: "";
        position: absolute;
        top: 11px;
        left: 50%;
        transform: translateX(-50%);
        width: 6px;
        height: 11px;
        border: solid #fff;
        border-width: 0 2px 2px 0;
        transform: rotate(45deg);
      }
    }

    &.open {
      background-color: #2b67f6;

      &::after {
        content: "";
        position: absolute;
        top: 11px;
        left: 50%;
        transform: translateX(-50%);
        width: 6px;
        height: 11px;
        border: solid #fff;
        border-width: 0 2px 2px 0;
        transform: rotate(45deg);
      }
    }
  }
}
