.filtros-menu {
  position: fixed;
  top: 0;
  left: 0;
  z-index: 100;
  width: 100%;
  height: 100%;
  background-color: #f2f2f2;
  transform: translateX(100%);
  transition: transform 0.3s;
  padding: 47px 10px;
  overflow-y: auto;

  &-title {
    color: #333;
    text-align: center;
    font-family: Archivo;
    font-size: 24px;
    font-weight: 600;
    line-height: 40px;
    margin-bottom: 32px;
  }

  &__radio-group {
    display: flex;
    flex-direction: column;
    align-items: flex-start;
    gap: 16px;
  }

  &__inner {
    border-radius: 24px;
    background: #fff;

    & > .cruise {
      padding: 24px 32px 0 32px;

      & > .cruise__bottom > .cruise__bottom-item {
        font-size: 14px;
      }
    }
  }

  &.is-open {
    transform: translateX(0);
  }

  &__btn-close {
    width: 48px;
    height: 32px;
    display: flex;
    align-items: center;
    justify-content: center;
    border: none;
    border-radius: 100px;
    background-color: #fff;
    padding: 12px;
    margin-bottom: 23px;
    cursor: pointer;

    & svg {
      color: #000000;
    }

    &:hover {
      background-color: #2b67f6;

      & svg {
        color: #ffffff;
      }
    }
  }

  &__full {
    display: flex;
    padding: 0 32px 24px 24px;
    border-radius: 24px;
    background: #fff;
    flex-direction: column;
    align-items: center;
    justify-content: center;

    &-line {
      width: 100%;
      padding-top: 32px;
      margin-top: 32px;
      border-top: 1px solid rgba(0, 0, 0, 0.1);
    }

    &-search-wrapper {
      position: relative;
      height: 48px;
      border-radius: 8px;
      background: #f8f8f8;
      padding: 8px 12px;
      width: 100%;
      margin-bottom: 16px;
    }

    &-search {
      position: relative;
      border-radius: 8px;
      background: #f8f8f8;
      padding: 8px 20px 8px 12px;
      width: 100%;
      background-image: url("../img/search.svg");
      background-position: right center;
      background-repeat: no-repeat;

      &::placeholder {
        color: #898989;
        font-family: Archivo;
        font-size: 14px;
        font-weight: 400;
      }
    }

    &-top {
      display: grid;
      row-gap: 16px;
      padding-bottom: 16px;
      width: 100%;

      &-item {
        display: flex;
        gap: 8px;
        align-items: center;
        font-size: 14px;
        height: 48px;
        border-radius: 8px;
        background: #f8f8f8;
        padding: 8px 12px;
        width: 100%;
      }

      &-select {
        height: 48px;
        border-radius: 8px;
        background: #f8f8f8;
        padding: 8px 12px;
        width: 100%;
        -webkit-appearance: none;
        -moz-appearance: none;
        appearance: none;
        background-image: url("../img/chevron-down.svg");
        background-position: right center;
        background-repeat: no-repeat;
        padding-right: 12px;

        &::-ms-expand {
          display: none;
        }
      }

      &-range-title {
        color: #000;
        font-family: Archivo;
        font-size: 16px;
        font-weight: 400;
        margin-bottom: 56px;
      }

      &-range-cost {
        display: flex;
        justify-content: space-between;
        padding-top: 14px;
      }

      &-range-cost p {
        color: #000;
        font-family: Archivo;
        font-size: 16px;
        font-weight: 400;
      }
    }

    &-bottom {
      display: flex;
      flex-direction: column;
      justify-content: center;
      align-items: center;
      width: 100%;

      &-wrapper {
        width: 100%;

        &-select {
          display: flex;
          justify-content: space-between;
          width: 100%;
          margin-bottom: 32px;
        }

        &-checkbox {
          display: flex;
          flex-direction: column;
          gap: 16px;
        }
      }

      &-select-item {
        display: flex;
        gap: 4px;

        & span {
          border-radius: 100px;
          display: inline-flex;
          background: #2b67f6;
          color: #fff;
          font-family: Archivo;
          font-size: 14px;
          width: 22px;
          height: 22px;
          justify-content: center;
          align-items: center;
        }
      }

      &-select {
        color: #2b67f6;
      }

      &-label {
        margin-left: 12px;
        color: #000;
        font-family: Archivo;
        font-size: 14px;
        font-weight: 400;

        & span {
          color: #2b67f6;
          font-family: Archivo;
          font-size: 16px;
          font-weight: 600;
          margin-right: 4px;
        }
      }

      &-buttons {
        display: flex;
        flex-direction: column;
        justify-content: center;
        align-items: center;
        gap: 16px;
      }

      &-button {
        color: #fff;
        font-family: Archivo;
        font-size: 18px;
        font-style: normal;
        font-weight: 400;
        line-height: 20px;
        border-radius: 27px;
        background: #2b67f6;
        padding: 12px 40px;
        text-align: center;
        margin-top: 33px;

        &:hover {
          background-color: #1753e2;
        }
      }

      &-link {
        display: flex;
        gap: 4px;
        align-items: center;
        color: #4d515d;
        font-family: Archivo;
        font-size: 14px;
        font-style: normal;
        font-weight: 400;
        line-height: 20px;
      }
    }
  }
}
