.info-passengers {
  padding-bottom: 16px;
  margin-top: -16px;
  display: none;

  @media screen and (min-width: 880px) {
    &.open {
      display: block;
    }
  }

  &.mobile {
    &.open {
      display: block;
    }

    & .info-passengers__close {
      padding: 16px 0;

      & h2 {
        font-size: 16px;
      }

      & button {
        width: 25px;
        height: 25px;
      }
    }

    & .info-passengers__top-title {
      gap: 8px;

      & h2 {
        font-size: 18px;
      }
    }

    & .info-passengers__bottom {
      grid-area: 24px;

      &-option {
        display: grid;
        grid-template-columns: 1fr;
        justify-items: center;
        row-gap: 16px;

        &-item {
          width: 100%;
          justify-content: center;
          gap: 4px;

          &:first-child {
            gap: 16px;
          }

          &:last-child {
            gap: 10px;
          }

          & .counter {
            justify-content: center;
            gap: 24px;

            & button {
              padding: 5px;
            }

            & span {
              font-size: 20px;
            }
          }

          & p {
            font-size: 12px;
          }

          & .input {
            & input {
              font-size: 14px;
            }

            & select {
              font-size: 14px;
            }
          }
        }
      }

      &-buttons {
        flex-direction: column;
        gap: 24px;

        &-check {
          flex-direction: column;
          align-items: flex-start;
          width: 100%;

          &-item {
            width: 100%;
            justify-content: space-between;

            & p {
              font-size: 12px;
            }
          }
        }

        &-continue {
          width: 100%;
        }
      }
    }

    @media screen and (min-width: 880px) {
      display: none;
    }
  }

  &__close {
    display: flex;
    justify-content: center;
    align-items: center;
    gap: 12px;
    padding-bottom: 16px;

    & h2 {
      color: #656565;
      font-size: 20px;
      font-weight: 500;
      line-height: normal;
    }
  }

  &__top {
    display: flex;
    align-items: center;
    justify-content: space-between;
    padding: 24px 32px;
    border-radius: 16px 16px 0px 0px;
    background: #fff;

    &.active {
      border-radius: 16px;

      & button {
        display: block;
      }
    }

    &-title {
      display: flex;
      align-items: center;
      gap: 12px;

      & h2 {
        color: #333;
        font-size: 22px;
        font-weight: 600;
        line-height: normal;
      }
    }

    & button {
      display: none;
    }
  }

  &__bottom {
    display: flex;
    flex-direction: column;
    gap: 32px;
    padding: 32px;
    border-radius: 0px 0px 16px 16px;
    background: #fff;
    border-top: 1px solid #ebebeb;

    &.active {
      display: none;
    }

    &-option {
      display: grid;
      grid-template-columns: repeat(4, 1fr);
      column-gap: 32px;

      &-item {
        display: flex;
        flex-direction: column;
        gap: 16px;

        & p {
          color: #656565;
          font-size: 14px;
          font-weight: 500;
          line-height: 20px;
          text-transform: uppercase;
        }

        & .counter {
          display: flex;
          align-items: center;
          gap: 16px;

          & button {
            padding: 8px;
            border-radius: 50%;
            border: 1px solid #2b67f6;
          }

          & span {
            color: #2b67f6;
            font-size: 22px;
            font-weight: 600;
            line-height: normal;
          }
        }

        & .input {
          display: flex;
          align-items: center;
          max-height: 48px;
          padding: 8px 12px;
          border-radius: 8px;
          background-color: #f8f8f8;

          & input {
            width: 100%;
            padding: 8px 12px;
            appearance: none;
            background-image: url("../img/chevron-down.svg");
            background-position: right center;
            background-repeat: no-repeat;
          }

          & select {
            width: 100%;
            padding: 8px 12px;
            appearance: none;
            background-image: url("../img/chevron-down.svg");
            background-position: right center;
            background-repeat: no-repeat;
          }
        }
      }
    }

    &-buttons {
      display: flex;
      align-items: center;
      justify-content: flex-end;
      gap: 150px;

      &-check {
        display: flex;
        align-items: center;
        gap: 24px;

        @media screen and (max-width: 1050px) {
          flex-direction: column;
          align-items: flex-end;
        }

        &-item {
          display: flex;
          align-items: center;
          gap: 16px;

          & p {
            color: #656565;
            font-size: 14px;
            font-weight: 500;
            line-height: 20px;
            text-transform: uppercase;
          }

          label {
            position: relative;
            width: 58px;
            height: 29px;
            cursor: pointer;
          }

          label input {
            position: relative;
            z-index: 1;
            appearance: none;
          }

          label span {
            position: absolute;
            top: 0;
            left: 0;
            width: 100%;
            height: 100%;
            background: #c2c2c2;
            border-radius: 40px;
            transition: 0.5s;
          }

          label input:checked ~ span {
            background: #2b67f6;
          }

          label span i {
            position: absolute;
            top: 3px;
            left: 4px;
            width: 23px;
            height: 23px;
            background: #fff;
            border-radius: 50%;
            transition: 0.5s;
          }

          label input:checked ~ span i {
            left: 32px;

            &::after {
              content: "";
              display: block;
              position: absolute;
              top: 5px;
              left: 9px;
              width: 6px;
              height: 11px;
              border: solid #2b67f6;
              border-width: 0 2px 2px 0;
              transform: rotate(45deg);
            }
          }
        }
        &-promo {
          color: #2b67f6;
          font-size: 14px;
          font-weight: 400;
          line-height: normal;
          text-decoration-line: underline;
        }
      }

      &-continue {
        color: #fff;
        font-size: 18px;
        font-weight: 400;
        line-height: 20px;
        padding: 12px 56px;
        border-radius: 27px;
        background-color: #2b67f6;

        &:hover {
          background-color: #1753e2;
        }
      }
    }
  }

  &.checkout-page {
    display: none;

    &.open {
      display: flex;
      flex-direction: column;
      align-items: flex-end;
      margin-top: 0;
    }

    &:last-child {
      display: flex;
      align-items: flex-end;
    }

    & .info-passengers__top {
      width: 100%;
    }

    & .info-passengers__bottom {
      margin-bottom: 32px;

      &-option {
        display: grid;
        grid-template-columns: repeat(4, 1fr);
        -moz-column-gap: 32px;
        column-gap: 32px;
        row-gap: 24px;

        &-item:last-child {
          grid-column: span 4;
          margin-left: 228px;
        }
      }

      &-buttons-check {
        display: flex;
        align-items: flex-start;
        gap: 24px;
        flex-direction: column;
        margin-right: 180px;

        &-wrapper {
          display: flex;
          gap: 32px;
        }
      }
    }
  }
}
