.experience {
  padding: 80px 0;
  border-top: 1px solid rgba(0, 0, 0, 0.1);

  @media screen and (max-width: 1150px) {
    display: none;
  }

  &__title {
    color: #333;
    text-align: center;
    font-size: 32px;
    font-weight: 600;
    line-height: normal;
    margin-bottom: 40px;
  }

  &__list {
    display: grid;
    grid-template-columns: 1fr 1fr;
    column-gap: 24px;
    row-gap: 24px;

    &-item {
      position: relative;

      &-title {
        position: absolute;
        z-index: 1;
        top: 32px;
        left: 32px;
        color: #fff;
        font-size: 32px;
        font-weight: 600;
        line-height: 38px;
        max-width: 306px;
      }

      &-button {
        position: absolute;
        left: 32px;
        bottom: 32px;
        color: #fff;
        font-size: 18px;
        font-weight: 400;
        line-height: 20px;
        padding: 12px 40px;
        border-radius: 27px;
        border: 1px solid #fff;
        background-color: transparent;

        &:hover {
          background-color: #2b67f6;
          border: 1px solid #2b67f6;
        }
      }
    }
  }
}
