.modal-plan {
  position: relative;
  display: flex;
  flex-direction: column;
  align-items: center;
  background-color: #ffffff;
  border-radius: 24px;
  padding: 40px;
  max-width: 600px;
  width: 100%;
  height: 600px;
  overflow: auto;

  &-wrapper {
    display: flex;
    justify-content: center;
    align-items: center;
  }

  &__close {
    position: absolute !important;
    top: 16px;
    right: 16px;
    width: 28px;
    height: 28px;
    background-color: #fff !important;
    padding: 0 !important;
    border-radius: 0 !important;
    border: none !important;
    cursor: pointer;

    &::after {
      display: none;
      content: "";
    }
  }

  &-backdrop {
    display: none;
    position: fixed;
    width: 100%;
    height: 100%;
    z-index: 9999;
    top: 0;
    left: 0;
    justify-content: center;
    align-items: center;
    background-color: rgba(0, 0, 0, 0.3);

    &.open {
      display: flex;
    }
  }
}
