.travel-experience {
  width: 100%;
  display: none;

  &.open {
    display: flex;
    flex-direction: column;
    align-items: flex-end;

    @media screen and (max-width: 880px) {
      align-items: normal;
    }
  }

  &__top {
    padding: 24px;
    border-radius: 16px 16px 0px 0px;
    background-color: #fff;
    width: 100%;

    & h2 {
      display: flex;
      align-items: center;
      gap: 12px;
      color: #333;
      font-size: 22px;
      font-weight: 600;
      line-height: normal;
    }
  }

  &__img {
    width: 100%;

    & img {
      width: 100%;
    }
  }

  &__bottom {
    padding: 0 24px 24px 24px;
    border-radius: 0px 0px 16px 16px;
    background-color: #fff;
    margin-bottom: 24px;

    &-wrapper {
      border-radius: 16px;
      border: 1px solid #ebebeb;
      background-color: #fff;
      position: relative;
      margin-top: -162px;

      @media screen and (max-width: 880px) {
        margin-top: -42px;
      }
    }

    &-header {
      display: grid;
      grid-template-columns: repeat(4, 1fr);

      @media screen and (max-width: 880px) {
        display: none;
      }

      &-item {
        display: flex;
        flex-direction: column;
        align-items: center;
        gap: 16px;
        padding: 24px;

        @media screen and (max-width: 880px) {
          gap: 12px;
          padding: 16px 24px;
        }

        & h2 {
          color: #252430;
          font-size: 18px;
          font-weight: 700;
          line-height: normal;
        }

        & button {
          position: relative;
          color: #2b67f6;
          font-size: 14px;
          font-weight: 400;
          line-height: 20px;
          padding: 8px 24px;
          border-radius: 27px;
          border: 1px solid #2b67f6;

          &:hover {
            background-color: #2b67f6;
            color: #fff;
          }

          &.open {
            background-color: #2b67f6;

            &:hover {
              background-color: #2b67f6;
              color: #2b67f6;
            }

            &::after {
              content: "";
              position: absolute;
              top: 11px;
              left: 50%;
              transform: translateX(-50%);
              width: 6px;
              height: 11px;
              border: solid #fff;
              border-width: 0 2px 2px 0;
              transform: rotate(45deg);
            }
          }
        }
      }
    }

    &-main {
      @media screen and (max-width: 880px) {
        display: none;
      }
      &.hide {
        display: none;
      }

      &-item {
        display: grid;
        grid-template-columns: repeat(4, 1fr);
        justify-items: center;
        align-items: center;
        padding: 16px 0;
        border-top: 1px solid #ebebeb;

        & p {
          padding-left: 12px;
          color: #252430;
          font-size: 14px;
          font-weight: 500;
          line-height: 18px;
          width: 100%;

          @media screen and (max-width: 880px) {
            font-size: 13px;
          }
        }

        & div {
          display: flex;
          flex-direction: column;
          align-items: center;
          justify-content: center;
          gap: 2px;

          & span {
            color: #ff8a4c;
            font-size: 14px;
            font-weight: 400;
            line-height: 20px;

            @media screen and (max-width: 880px) {
              font-size: 12px;
            }
          }
        }

        &.total {
          border-bottom: 1px solid #d1defe;
          background-color: #e8f0ff;

          & h3 {
            color: #252430;
            font-size: 14px;
            font-weight: 700;
            line-height: 20px;
            width: 100%;
            padding-left: 16px;

            @media screen and (max-width: 880px) {
              font-size: 12px;
            }
          }

          & p {
            color: #252430;
            text-align: center;
            font-size: 15px;
            font-weight: 400;
            line-height: 20px;

            @media screen and (max-width: 880px) {
              font-size: 14px;
            }

            & span {
              color: #252430;
              text-align: center;
              font-size: 15px;
              font-weight: 700;
              line-height: 20px;

              @media screen and (max-width: 880px) {
                font-size: 14px;
              }
            }
          }
        }
      }

      &-mobile {
        display: none;

        @media screen and (max-width: 880px) {
          display: block;
        }

        &-item {
          display: grid;
          grid-template-columns: 1fr 1fr;
          padding: 16px 0;
          border-top: 1px solid #ebebeb;

          &-left {
            padding: 8px 0 8px 16px;
            min-width: 160px;
            box-shadow: 5px 0px 10px rgba(0, 0, 0, 0.1);

            &-header {
              display: flex !important;
              flex-direction: column !important;
              align-items: center !important;
              justify-content: center !important;
              gap: 12px !important;
              height: 100px !important;
            }

            &-footer {
              background-color: #e8f0ff;
              border-bottom: 1px solid #d1defe !important;
            }

            & p {
              display: flex;
              align-items: center;
              color: #252430;
              font-size: 13px;
              font-weight: 500;
              line-height: 14px;
              height: 60px;
              padding: 16px 8px;
              border-top: 1px solid #ebebeb;

              &.hide {
                display: none;
              }
            }
          }

          &-right {
            display: grid;
            grid-template-columns: repeat(3, 1fr);
            overflow-x: auto;
            padding: 8px 0 8px 0;

            &-header {
              display: flex !important;
              flex-direction: column !important;
              align-items: center !important;
              justify-content: center !important;
              gap: 12px !important;
              height: 100px !important;

              & h2 {
                color: #252430;
                text-align: center;
                font-size: 18px;
                font-weight: 700;
                line-height: normal;
              }

              & button {
                position: relative;
                color: #fff;
                font-size: 14px;
                font-weight: 400;
                line-height: 20px;
                padding: 8px 24px;
                border-radius: 27px;
                border: 1px solid #2b67f6;
                background-color: #2b67f6;

                &.open {
                  color: #2b67f6;

                  &::after {
                    content: "";
                    position: absolute;
                    top: 11px;
                    left: 50%;
                    transform: translateX(-50%);
                    width: 6px;
                    height: 11px;
                    border: solid #fff;
                    border-width: 0 2px 2px 0;
                    transform: rotate(45deg);
                  }
                }
              }
            }

            &-footer {
              background-color: #e8f0ff;
              border-bottom: 1px solid #d1defe !important;
            }

            & div {
              display: flex;
              flex-direction: column;
              align-items: center;
              min-width: 175px;

              & div {
                height: 60px;
                padding: 16px 8px;
                border-top: 1px solid #ebebeb;

                &.hide {
                  display: none;
                }

                & span {
                  color: #ff8a4c;
                  font-size: 12px;
                  font-weight: 400;
                  line-height: 20px;
                }

                & p {
                  text-align: center;
                  color: #252430;
                  font-size: 14px;
                  font-weight: 400;
                  line-height: 20px;

                  & span {
                    text-align: center;
                    color: #252430;
                    font-size: 14px;
                    font-weight: 700;
                    line-height: 18px;
                  }
                }
              }
            }
          }
        }
      }
    }
  }

  &__check {
    display: none;

    &.open {
      display: flex;
      flex-direction: column;
      gap: 16px;
      padding: 16px;
    }

    & h2 {
      color: #333;
      font-size: 16px;
      font-weight: 400;
      line-height: 22px;

      @media screen and (max-width: 880px) {
        font-size: 14px;
      }
    }

    &-list {
      display: grid;
      grid-template-columns: repeat(3, 1fr);
      column-gap: 16px;

      @media screen and (max-width: 880px) {
        grid-template-columns: 1fr;
        row-gap: 12px;
      }

      &-item {
        display: flex;
        flex-direction: column;
        align-items: flex-start;
        gap: 24px;
        padding: 24px;
        border-radius: 16px;
        background-color: #f9f9f9;

        @media screen and (max-width: 880px) {
          padding: 16px;
        }

        & h3 {
          color: #2b67f6;
          font-size: 16px;
          font-weight: 600;
          line-height: normal;
          padding: 6px 12px;
          border-radius: 100px;
          background-color: #e8f0ff;
        }

        & label {
          color: #000;
          font-size: 16px;
          font-weight: 400;
          line-height: 20px;
          display: block;
          position: relative;
          padding-left: 30px;
          margin-bottom: 12px;
          cursor: pointer;
          font-size: 16px;

          & .checkmark {
            position: absolute;
            top: 0;
            left: 0;
            height: 20px;
            width: 20px;
            background-color: #eee;
            border-radius: 3px;

            &:after {
              content: "";
              position: absolute;
              display: none;
            }
          }

          & input {
            position: absolute;
            opacity: 0;
            cursor: pointer;
          }

          & input:checked ~ .checkmark {
            background-color: rgba(43, 103, 246, 0.4);
          }

          & input:checked ~ .checkmark:after {
            display: block;
          }

          & .checkmark:after {
            left: 7px;
            top: 3px;
            width: 6px;
            height: 12px;
            border: solid white;
            border-width: 0 2px 2px 0;
            transform: rotate(45deg);
          }

          & span {
            font-weight: 700;
          }
        }
      }
    }
    & a {
      display: flex;
      align-items: center;
      gap: 4px;
      color: #2b67f6;
      font-size: 14px;
      font-weight: 500;
      line-height: normal;
    }
  }

  &__next {
    display: none;
    color: #fff;
    font-size: 20px;
    font-weight: 400;
    line-height: 20px;
    padding: 16px 56px;
    border-radius: 27px;
    background-color: #2b67f6;
    border: 1px solid #2b67f6;
    margin-bottom: 30px;

    &:hover {
      background-color: transparent;
      color: #2b67f6;
    }

    &.open {
      display: block;
    }
  }
}
