.product-buttons {
  background-color: #fff;
  position: fixed;
  bottom: 0;
  left: 0;
  width: 100%;
  z-index: 10;

  @media screen and (min-width: 800px) {
    display: none;
  }

  &-wrapper {
    display: grid;
    grid-template-columns: 1fr 5fr;
    column-gap: 12px;
    padding: 8px;
  }

  &__share {
    border-radius: 100px;
    background: #e8f0ff;
    padding: 8px 14px;

    &.tooltip {
      position: relative;

      &:hover .tooltip-text {
        visibility: visible;
        opacity: 1;
      }

      & .tooltip-text {
        position: absolute;
        z-index: 1;
        bottom: 125%;
        left: 85%;
        background-color: #e8f0ff;
        color: #000;
        text-align: center;
        font-size: 14px;
        font-weight: 400;
        line-height: 18px;
        visibility: hidden;
        width: 250px;
        border-radius: 8px;
        padding: 16px;
        margin-left: -60px;
        opacity: 0;
        transition: opacity 0.3s;
        text-transform: capitalize;

        & h3 {
          color: #000;
          font-size: 18px;
          font-weight: 600;
          line-height: 20px;
          margin-bottom: 16px;
        }

        & a {
          display: flex;
          gap: 8px;
          color: #000;
          font-size: 16px;
          font-weight: 400;
          line-height: 20px;
          text-transform: capitalize;

          &:not(:last-child) {
            margin-bottom: 12px;
          }
        }

        &::after {
          content: "";
          position: absolute;
          top: 100%;
          left: 15%;
          margin-left: -5px;
          border-width: 8px;
          border-style: solid;
          border-color: #e8f0ff transparent transparent transparent;
          content: " ";
        }
      }
    }

    & img {
      width: 18px;
      height: 18px;
    }
  }

  &__reserv {
    color: #fff;
    font-size: 16px;
    font-weight: 400;
    line-height: 20px;
    border-radius: 27px;
    background-color: #2b67f6;
    padding: 12px 40px;
  }
}
